<template>
  <div class="subprocess">
    <lines
      :is-draw-line="isDrawLine"
      :parent-node="parentNode"
      :lines="lines"
      :nodes="nodes"
      :events="events"
      :checked="checked"
      :is-sub="true"
      :config="config"
    ></lines>
    <nodes
      :is-draw-line="isDrawLine"
      :parent-node="parentNode"
      :nodes="nodes"
      :checked="checked"
      :config="config"
      :is-sub="true"
      :data-id="parentNode.id"
    ></nodes>
    <events
      :is-draw-line="isDrawLine"
      :parent-node="parentNode"
      :nodes="nodes"
      :events="events"
      :checked="checked"
      :is-sub="true"
      :config="config"
    ></events>
  </div>
</template>
<script>
  import Lines from "./Lines";
  import Events from "./Events";
  export default {
    name: "Subprocess",
    components: {
      Lines,
      Events
    },
    props: {
      lines: {
        type: Array,
        default() {
          return [];
        },
      },
      nodes: {
        type: Array,
        default() {
          return [];
        },
      },
      events: {
        type: Array,
        default() {
          return [];
        },
      },
      isDrawLine: {
        type: Boolean,
        default() {
          return false;
        },
      },
      checked: {
        type: Object,
        default() {
          return {};
        },
      },
      config: {
        type: Object,
        default() {
          return {};
        },
      },
      parentNode: {
        type: Object,
        default() {
          return {};
        },
      },
    }
  }
</script>
<style lang="scss" scoped>
</style>