<template>
  <el-container>
    <el-header>
      <div style="text-align:right;height:auto;margin:0px -20px">
        <DsfButton @click="add">新增</DsfButton>
      </div>
    </el-header>
    <el-main style="padding:0px">
      <el-table :data="tableData" border>
        <el-table-column :prop="'code'" :label="'元数据代码'">
          <template v-slot:default="scope">
            <el-input v-model="scope.row.code"></el-input>
          </template>
        </el-table-column>
        <el-table-column v-if="!type" :prop="'type'" :label="'类型'">
          <template v-slot:default="scope">
            <el-select v-model="scope.row.type">
              <el-option :value="0" :label="'检索框'"></el-option>
              <el-option :value="1" :label="'附加条件'"></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column :prop="'operation'" :label="'操作符'">
          <template v-slot:default="scope">
            <el-select v-model="scope.row.operation">
              <el-option value="like" label="模糊匹配"></el-option>
              <el-option value="slike" label="开头匹配"></el-option>
              <el-option value="elike" label="结尾匹配"></el-option>
              <el-option value="eq" label="等于"></el-option>
              <el-option value="noteq" label="不等于"></el-option>
              <el-option value="lt" label="小于"></el-option>
              <el-option value="gt" label="大于"></el-option>
              <el-option value="lteq" label="小于等于"></el-option>
              <el-option value="gteq" label="大于等于"></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column :prop="'valueExpress'" :label="'值'">
          <template v-slot:default="scope">
            <el-input v-model="scope.row.valueExpress" :disabled="scope.row.type===0"></el-input>
          </template>
        </el-table-column>
        <el-table-column :label="'操作'">
          <template v-slot:default="scope">
            <el-link type="primary" @click="remove(scope.row)">删除</el-link>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesDataGridSearch",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
      type: 0
    };
  },
  created() {
    let value = this.currentData || []
    this.tableData = value;
    if (this.config.other) {
      this.type = (this.config.other.type || '0') * 1;
    }
  },
  mounted() {

  },
  methods: {
    add() {
      this.tableData.push({
        "code": "",
        "type": this.type,
        "operation": "like",
        "valueExpress": ""
      })
    },
    remove(row) {
      dsf.array.remove(this.tableData, row)
    },
    yes() {
      return this.tableData;
    }
  }
}
</script>