<template>
  <div>
    <SelectTree
      :tree-data="nodes"
      :select.sync="selectFlow"
      :show-search="true"
      :multi="true"
    ></SelectTree>
  </div>
</template>
<script>
import SelectTree from "./selectTree";
export default {
  name: "ApplyTo",
  components: {
    SelectTree,
  },
  props: {
    data: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      nodes: [],
      selectFlow: [],
    };
  },
  created() {
    const data = dsf.mix(true, this.data);
    this.nodes = [this.formatData(data[0])];
  },
  mounted() {},
  methods: {
    yes() {
      return this.selectFlow;
    },
    formatData(data) {
      delete data.disabled;
      if (data.children) {
        for (var i = 0; i < data.children.length; i++) {
          this.formatData(data.children[i]);
        }
      }
      return data;
    },
  },
};
</script>
