import layoutPreView from "_platform/dsf/output/layoutPreView";
import layoutMobilePreView from "_platform/dsf/output/layoutMobilePreView";
import test from "_platform/dsf/designer/page/dsfDesigner/test";
import seatDesign from "_platform/dsf/designer/seat/seatDesign";
import seatView from "_platform/dsf/designer/seat/seatView";
import exam from "_platform/dsf/designer/page/dsfDesigner/examPage";
//此处导出设计器使用的默认路由

let router = [
  //设计预览路由
  {
    path: "/pc/preview",
    name: "DsfLayoutPreView",
    meta: {
      title: "预览"
    },
    component: layoutPreView,
    props(router) {
      return {
        mobile: router.query['mobile'] == '1' ? true : false,
        tpl: router.query['tpl'] == '1' ? true : false
      }
    }
  }, {
    path: "/mobile/preview",
    name: "DsfMobileLayoutPreView",
    meta: {
      title: "预览"
    },
    component: layoutMobilePreView
  }, {
    path: "/test",
    name: "Test",
    component: test
  }, {
    path: "/seatDesign",
    name: "seatDesign",
    component: seatDesign
  }, {
    path: "/seatView",
    name: "seatView",
    component: seatView
  },
  {
    path: "/flow",
    name: "Flow",
    meta: {
      title: "流程设计器"
    },
    component: () => import( /* webpackChunkName: "Flow" */ "_platform/dsf/designer/flow/flow.vue")
  },
  {
    path: "/todoTemp",
    name: "TodoTemp",
    meta: {
      title: "待办事项"
    },
    component: () => import( /* webpackChunkName: "TodoTemp" */ "_platform/dsf/pc/bhc/flow/todoTemp.vue")
  },
  {
    path: "/exam",
    name: "Exam",
    component: exam,
    meta: {
      title: "在线考试"
    },
  },
];

export default router;