class EventBus {
  constructor() {
    this.events = new Map();
  }

  on(event, callback)  {
    this.events.get(event) === undefined ?
      this.events.set(event, callback) :
      console.log(event + " 注册器重名");
  }

  emit(event, params) {
    if (this.events.get(event)) this.events.get(event)(params)
  }

  off(event) {
    this.events.delete(event);
  }

  clear(){
    this.events.clear()
  }

  $getAllListener(){
    const _iterator = this.events.entries();
    const obj = {};
    for (let i of _iterator){
      console.log(i);
      obj[_iterator[i][0]] = _iterator[i][1]
    }

    return obj;
  }
}

const eventBus = new EventBus();
window.$seatEventBus = eventBus;
export default eventBus