<template>
  <div class="ds-meta-data-select-item">
    <dsf-tree-select
      :tree-data="treeData"
      :loading="loading"
      theme="theme2"
      node-key="ID"
      node-name="name"
      search-control
      default-expand-all
      multiple
      @choose-node="checkList = $event"
    />
  </div>
</template>

<script>
import DsfTreeSelect from './treeSelect';

export default {
  name: 'DesMetaDataSelectItem',
  components: {
    DsfTreeSelect
  },
  props: {
    url: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      loading: false,
      treeData: [],
      checkList: []
    }
  },
  created() {
    this.postData();
  },
  methods: {
    yes() {
      return this.checkList;
    },
    postData() {
      this.loading = true;
      this.$http.get(
        this.url, {
        namespace: ''
      }
      ).then(({ data }) => {
        if (data.success) {
          this.dataFormat(data.data);
          this.treeData = data.data;
        } else {
          dsf.layer.message('请求异常！', false);
        }
      }).finally(() => {
        this.loading = false;
      });
    },
    dataFormat(nodes) {
      for (let i = 0; i < nodes.length; i++) {
        const node = nodes[i];
        node.label = node.name;
        if (node.type_value !== '3' && node.type_value !== '8') {
          node.nocheck = true;
        }
        if ((node.type_value !== '3' && node.type_value !== '8') && !node.children) {
          node.iconSkin = '_special';
        }
        if (node.children) {
          this.dataFormat(node.children)
        }
      }
    }
  }
}
</script>