import Raphael from "raphael";

// 泳道的两种类型垂直和水平
const LaneDirection = {
  "vertical": "vertical",
  "horizontal": "horizontal"
}
// 连线的8种箭头位置
const _Direction = {
  Left: "left",
  Right: "right",
  Top: "top",
  Bottom: "bottom",
  LeftTop: "leftTop",
  LeftBottom: "leftBottom",
  RightTop: "rightTop",
  RightBottom: "rightBottom"
}
// 鼠标拖动的线分垂直和水平类型
const _LineType = {
  "vertical": "vertical",
  "horizontal": "horizontal"
}
// 线的方向类型
const LineDirection = {
  Left: "left",
  Right: "right",
  Top: "top",
  Bottom: "bottom"
}
// 用于绘制连接的点
const nodeSize = {
  width: 40,
  height: 44
};
// 节点是任务的
const nodeTaskSize = {
  width: 200,
  height: 124
}
// 节点是子流程
const childNodeSize = {
  width: 500,
  height: 304
}
//线与线之间过渡的圆弧半径
const transitionRadius = 7;

const tools = {
  "uuid": function (len, radix) {
    const CHARS = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
    let chars = CHARS,
      uuid = [],
      i;
    len = len || 32;
    radix = radix || chars.length;

    if (len) {
      // Compact form 
      for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
    } else {
      // rfc4122, version 4 form 
      let r;

      // rfc4122 requires these characters 
      uuid[8] = uuid[13] = uuid[18] = uuid[23] = '';
      uuid[14] = '4';

      // Fill in random data.  At i==19 set the high bits of clock sequence as 
      // per rfc4122, sec. 4.1.5 
      for (i = 0; i < 36; i++) {
        if (!uuid[i]) {
          r = 0 | Math.random() * 16;
          uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
        }
      }
    }

    return uuid.join('');
  },
  //添加纵向泳道文本旋转
  "laneTextRotate": function (text) {
    text.css("transform", "rotate(-90deg) translateX(-" + text.outerWidth() + "px)");
  },
  //获取节点中心点
  "getNodeCenter": function (node) {
    // const size = (node.type === '1' || node.type === '5') ? nodeTaskSize : nodeSize;
    let x = node.point.x;
    let y = node.point.y;
    x += node.width / 2;
    y += node.height / 2;
    return {
      x: x,
      y: y
    };
  },
  //获取节点对角线的角度
  "getNodeArriswiseAngle": function (node) {
    const p1 = {
      x: node.point.x,
      y: node.point.y
    };
    const p2 = {
      x: node.point.x + node.width,
      y: node.point.y + node.height
    }
    const deg = this.getAngle(p1, p2);
    return deg;
  },
  "getNodeDirection": function (node, targetPoint) {
    let direction = "";
    const arriswiseAngle = this.getNodeArriswiseAngle(node);
    const p1 = this.getNodeCenter(node);
    const p2 = targetPoint;
    const deg = this.getAngle({
      x: p1.x,
      y: p1.y
    }, {
      x: p2.x,
      y: p2.y
    });
    const a = arriswiseAngle;
    if (deg == 0 || deg >= (360 - a) || deg <= a) {
      direction = LineDirection.Right;
    } else if (deg == 90 || (deg > a && deg <= (180 - a))) {
      direction = LineDirection.Bottom;
    } else if (deg == 270 || (deg > (180 + a) && deg < (360 - a))) {
      direction = LineDirection.Top
    } else {
      direction = LineDirection.Left;
    }
    return direction;
  },
  //获取画线方向
  "getDirection": function (startPoint, endPoint) {
    let direction = "";
    const deg = this.getAngle({
      x: parseFloat(startPoint.x),
      y: parseFloat(startPoint.y)
    }, {
      x: parseFloat(endPoint.x),
      y: parseFloat(endPoint.y)
    });
    if (deg == 0 || deg == 360) {
      direction = _Direction.Right;
    } else if (deg > 0 && deg < 90) {
      direction = _Direction.RightBottom;
    } else if (deg == 90) {
      direction = _Direction.Bottom;
    } else if (deg > 90 && deg < 180) {
      direction = _Direction.LeftBottom;
    } else if (deg == 180) {
      direction = _Direction.Left;
    } else if (deg > 180 && deg < 270) {
      direction = _Direction.LeftTop;
    } else if (deg == 270) {
      direction = _Direction.Top;
    } else if (deg > 270 && deg < 360) {
      direction = _Direction.RightTop;
    }
    return direction;
  },
  //获取弧度
  "getRadina": function (p1, p2, is360) {
    let radians = Math.atan((p2.y - p1.y) / (p2.x - p1.x));
    if (is360 != false) {
      //修正第三象限和第二象限的弧度
      if ((p2.y - p1.y > 0 && p2.x - p1.x < 0) || p2.y - p1.y < 0 && p2.x - p1.x < 0) {
        radians += Math.PI;
      }
      //修正第一象限弧度
      else if (p2.y - p1.y < 0 && p2.x - p1.x > 0) {
        radians += Math.PI * 2;
      }
      //x轴负方向 180度
      if (p2.x < p1.x && p2.y == p1.y) {
        //console.log("x轴负方向");
        radians = Math.PI;
      }
      //y轴负方向 270度
      else if (p2.x == p1.x && p2.y < p1.y) {
        radians = 1.5 * Math.PI;
      }
    }
    return radians;
  },
  //获取数学坐标角度
  "getAngle": function (p1, p2) {
    const radina = this.getRadina(p1, p2) //用反三角函数求弧度
    const angle = 180 / (Math.PI / radina); //将弧度转换成角度
    return angle;
  },
  //角度转弧度
  "angleToRadina": function (angle) {
    return angle * Math.PI / 180;
  },
  //获得线长度
  "getLineLength": function (p1, p2) {
    const x = p2.x - p1.x;
    const y = p2.y - p1.y;
    const length = Math.sqrt(Math.pow(x, 2) + Math.pow(y, 2));
    return length;
  },
  //获取节点元素上下左右的连接点位置
  "getNodeJoinPoint": function (node) {
    // const size = (node.type === '1' || node.type === '5') ? nodeTaskSize : nodeSize;
    const ncp = this.getNodeCenter(node);
    const point = {
      "top": {
        x: ncp.x,
        y: ncp.y - node.height / 2
      },
      "bottom": {
        x: ncp.x,
        y: ncp.y + node.height / 2
      },
      "left": {
        x: ncp.x - node.width / 2,
        y: ncp.y
      },
      "right": {
        x: ncp.x + node.width / 2,
        y: ncp.y
      }
    }
    return point;
  },
  //将线路径点转为线段
  "convertSegmentByPoint": function (points) {
    let segmentList = [];
    for (let i = 0; i < points.length; i++) {
      const point1 = points[i];
      const point2 = points[i + 1];
      if (point1 && point2) {
        segmentList.push({
          sp: point1,
          ep: point2
        });
      }
    }
    return segmentList;
  },
  //获取线段是否为水平线
  "getLineType": function (segmen) {
    //水平线
    if (segmen.sp.y == segmen.ep.y) {
      return _LineType.horizontal;
    }
    if (segmen.sp.x == segmen.ep.x) {
      return _LineType.vertical;
    }
  },
  //获取线段焦点
  "segmentsIntr": function (a, b, c, d) {
    //线段ab的法线N1
    const nx1 = (b.y - a.y),
      ny1 = (a.x - b.x);

    //线段cd的法线N2
    const nx2 = (d.y - c.y),
      ny2 = (c.x - d.x);

    //两条法线做叉乘, 如果结果为0, 说明线段ab和线段cd平行或共线,不相交
    const denominator = nx1 * ny2 - ny1 * nx2;
    if (denominator == 0) {
      return false;
    }
    //在法线N2上的投影
    const distC_N2 = nx2 * c.x + ny2 * c.y;
    const distA_N2 = nx2 * a.x + ny2 * a.y - distC_N2;
    const distB_N2 = nx2 * b.x + ny2 * b.y - distC_N2;

    // 点a投影和点b投影在点c投影同侧 (对点在线段上的情况,本例当作不相交处理);
    if (distA_N2 * distB_N2 >= 0) {
      return false;
    }

    //
    //判断点c点d 和线段ab的关系, 原理同上
    //
    //在法线N1上的投影
    const distA_N1 = nx1 * a.x + ny1 * a.y;
    const distC_N1 = nx1 * c.x + ny1 * c.y - distA_N1;
    const distD_N1 = nx1 * d.x + ny1 * d.y - distA_N1;
    if (distC_N1 * distD_N1 >= 0) {
      return false;
    }

    //计算交点坐标
    const fraction = distA_N2 / denominator;
    const dx = fraction * ny1,
      dy = -fraction * nx1;
    return {
      x: a.x + dx,
      y: a.y + dy
    };
  },
  "drawArrow": function (segmen) {
    const size = 8;
    //analysis line
    //var line = paper.getById(line_id);
    //get last points
    //var length = line.attrs.path.length;
    // var x1 = segmen.sp.x; //单线段起点
    // var y1 = segmen.sp.y; //单线段起点
    const x2 = segmen.ep.x; //单线段终点
    const y2 = segmen.ep.y; //单线段终点
    //get last angle
    const angle = tools.getAngle(segmen.sp, segmen.ep); // Raphael.angle(x1, y1, x2, y2);//API方法获取角度，原直线倾斜度
    const angle_one = Raphael.rad(angle - 25); //API方法转变为弧度
    const angle_two = Raphael.rad(angle + 25); //API方法转变为弧度
    //arrow points求出箭头的双向点
    const angle_x1 = x2 - Math.cos(angle_one) * size;
    const angle_y1 = y2 - Math.sin(angle_one) * size;
    const angle_x2 = x2 - Math.cos(angle_two) * size;
    const angle_y2 = y2 - Math.sin(angle_two) * size;
    //return
    const result = ["M", x2, y2, "L", angle_x1, angle_y1, "L", angle_x2, angle_y2, "Z"];
    return result;
  }

}

export {
  LaneDirection,
  _Direction,
  _LineType,
  LineDirection,
  nodeSize,
  nodeTaskSize,
  childNodeSize,
  transitionRadius,
  tools,
};