<template>
  <el-tabs v-model="tabIndex" class="ds-enum-editor" type="border-card">
    <el-tab-pane name="1" label="数据源">
      <enum-editor-item v-model="value1" type="db" :tree-loading="loading[1]" :tree-data="treeData1" :default-checked-key="defaultCheckedKey1" :table-post="tablePost1" empty-text="请选择数据源" />
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import EnumEditorItem from './item/enumEditorItem';

export default {
  name: 'DesCascaderItemsEditor',
  components: {
    EnumEditorItem
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      tabIndex: '0',
      loaded: [false],
      loading: [false],
      value1: {
        type: 'db',
        code: null,
        attach: []
      },
      treeData1: [],
      defaultCheckedKey1: '',
      tablePost1: (rmId, callback) => {
        this.$http.get(
          'meta/tree/dbSource', {
          rmId
        }
        ).done((data) => {
          if (data.success) {
            callback && callback(data.data);
          } else {
            dsf.layer.message('请求异常！', false);
          }
        })
      }
    }
  },
  watch: {
    'tabIndex'(to) {
      console.log(to)
      if (!this.loaded[to - 1] && this['postData' + to]) {
        this['postData' + to]();
      }
      this.$set(this.loaded, parseInt(to) - 1, true);
    }
  },
  created() {
    this.currentData && this.init(this.currentData);
  },
  methods: {
    init({ type, attach, code }) {
      this.value1 = { type, attach: [], code };
      this.defaultCheckedKey1 = code;
      this.tabIndex = '1';
    },
    yes() {
      if (this.tabIndex === '1') {
        let v = this['value' + this.tabIndex];
        delete v.attach;
        return v;
      }
    },
    postData1() {
      this.$set(this.loading, 1, true);
      this.$http.get('/rm/list/ds', {
        namespace: ''
      }).done((data) => {
        if (data.success) {
          this.treeData1 = data.data.map(v => this.dataFormat1(v));
        } else {
          dsf.layer.message('请求异常！', false);
        }
      }).always(() => {
        this.$set(this.loading, 1, false);
      });
    },
    dataFormat1({ name, ID, type_value, children }) {
      const obj = {
        label: name,
        ID,
        nocheck: type_value !== "3" && type_value !== "8",
        iconSkin: ''
      };
      if (children) {
        obj.children = children.map(v => this.dataFormat1(v));
      } else if (obj.nocheck) {
        obj.iconSkin = '_special';
      }
      return obj;
    }
  }
}
</script>