<template>
  <el-table v-loading="loading" :data="tableData" :border="true" style="width: 100%;">
    <el-table-column label width="35">
      <template slot-scope="scope">
        <el-radio v-model="radioId" :label="scope.row._id" />
      </template>
    </el-table-column>
    <el-table-column label="规则名称">
      <template slot-scope="{row}">
        {{ row['dsfa_number_rule.name'] }}
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesNumeration",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
      loading: false,
      param: {
        pageNum: 1,
        pageSize: 1000,
        namespace: "dsfa.number.rule",
        pageName: "list",
      },
      radioId: this.currentData
    };
  },
  created() {
    this.initData();
  },
  methods: {
    initData() {
      this.loading = true;
      this.$http
        .get("meta/list/data", this.param)
        .done((res) => {
          if (res.success) {
            this.tableData = res.data;
          } else {
            dsf.layer.message(res.message || "服务器异常", false);
          }
        })
        .error((err) => {
          dsf.layer.message(err.message || "服务器异常", false);
        }).always(() => {
            this.loading = false;
        })
    },
    yes() {
      if(!this.radioId){
          dsf.layer.message("请选择一个编号规则", false);
      }
      return this.radioId;
    },
  },
};
</script>
