import { tools, _LineType, transitionRadius } from './tools';
const Segmen = function (fromLine, paper, index) {
  let element = null,
    arrow = null;
  let inter = [];
  let line = fromLine;
  //var isHigh = false;
  this.sp = null;
  this.ep = null;
  this.type = null;
  this.isLast = false;
  this.index = index;
  this.direction = null;
  this.hasTransition = false;
  this.getLine = function () {
    return line;
  }
  this.render = function () {
    this.sp.x = parseFloat(this.sp.x);
    this.sp.y = parseFloat(this.sp.y);
    this.ep.x = parseFloat(this.ep.x);
    this.ep.y = parseFloat(this.ep.y);
    let arr = [];
    const sp = {
      "type": "point",
      "data": {
        x: this.sp.x,
        y: this.sp.y
      }
    };
    const ep = {
      "type": "point",
      "data": {
        x: this.ep.x,
        y: this.ep.y
      }
    };
    let path = [];
    this.remove();
    let arrowSegmen = {
      sp: {
        x: sp.data.x,
        y: sp.data.y
      },
      ep: {
        x: ep.data.x,
        y: ep.data.y
      }
    };
    if (this.sp.y == this.ep.y) {
      this.type = _LineType.horizontal;
      if (this.isLast) {
        arrowSegmen.ep.x = arrowSegmen.ep.x - (sp.data.x < ep.data.x ? 2 : -2);
      }
    } else {
      this.type = _LineType.vertical;
      if (this.isLast) {
        arrowSegmen.ep.y = arrowSegmen.ep.y - (sp.data.y < ep.data.y ? 2 : -2);
      }
    }

    //如果上一根线有过渡圆弧，则当前线需要减去圆弧半径
    const pre = line.getPreLine(this);
    if (pre && pre.type != this.type) {
      if (pre.hasTransition) {
        if (this.type == _LineType.horizontal) {
          sp.data.x = this.direction == "left" ? sp.data.x - transitionRadius : sp.data.x + transitionRadius;
        } else {
          sp.data.y = this.direction == "top" ? sp.data.y - transitionRadius : sp.data.y + transitionRadius;
        }
      }

    }

    //如果是水平线，获取交叉点
    if (this.type == _LineType.horizontal && inter.length > 0) {
      const radius = 5;
      let dir = "right";
      if (this.sp.x > this.ep.x) {
        dir = "left";
      }
      inter = inter.sort(function (v1, v2) {
        if (dir == "right") {
          return v1.x < v2.x ? -1 : 1;
        } else {
          return v1.x < v2.x ? 1 : -1;
        }
      });
      arr.push(sp);
      for (let i = 0; i < inter.length; i++) {
        let l = inter[i];
        const obj1 = {
          "type": "point",
          "data": {
            x: l.x - (dir == "right" ? radius : -radius),
            y: l.y
          }
        }
        const obj2 = {
          "type": "point",
          "data": {
            x: l.x + (dir == "right" ? radius : -radius),
            y: l.y
          }
        }
        arr.push(obj1);
        arr.push({
          "type": "arc",
          data: {
            "radius": radius,
            "overX": obj2.data.x,
            "overY": obj2.data.y,
            "sweep": dir == "right" ? 1 : 0
          }
        });
        arr.push(obj2);
      }

    } else {
      arr.push(sp);
    }

    //与下一根线的过渡圆弧
    const next = line.getNextLine(this);
    if (next && next.type != this.type) {
      let line_length = 0;
      line_length = tools.getLineLength(this.sp, this.ep);
      const n_line_length = tools.getLineLength(next.sp, next.ep);
      const alwaySzie = transitionRadius * 2;
      if (line_length <= alwaySzie || n_line_length <= alwaySzie) {
        arr.push(ep);
        this.hasTransition = false;
      } else {
        let obj1;
        line_length = tools.getLineLength(next.sp, next.ep);
        if (this.type == _LineType.vertical) {
          obj1 = {
            "type": "point",
            "data": {
              x: this.ep.x,
              y: this.ep.y - (this.direction == "top" ? -transitionRadius : transitionRadius)
            }
          }
          arr.push(obj1);
          let sweep = null; //圆弧的顺时针和逆时针 1为顺，0为逆
          if ((this.direction == "bottom" && next.direction == "left") || (this.direction == "top" && next.direction ==
            "right")) {
            sweep = 1;
          } else if ((this.direction == "top" && next.direction == "left") || (this.direction == "bottom" && next.direction ==
            "right")) {
            sweep = 0;
          }
          arr.push({
            "type": "arc",
            data: {
              "radius": transitionRadius,
              "overX": this.ep.x - (next.direction == "left" ? transitionRadius : -transitionRadius), //(next.direction == "left")?-transitionRadius:transitionRadius,
              "overY": this.ep.y,
              "sweep": sweep
            }
          });
        } else {
          obj1 = {
            "type": "point",
            "data": {
              x: this.ep.x - (this.direction == "right" ? transitionRadius : -transitionRadius),
              y: this.ep.y
            }
          }
          arr.push(obj1);
          let sweep = null; //圆弧的顺时针和逆时针 1为顺，0为逆
          if ((this.direction == "left" && next.direction == "top") || (this.direction == "right" && next.direction ==
            "bottom")) {
            sweep = 1;
          } else if ((this.direction == "left" && next.direction == "bottom") || (this.direction == "right" && next.direction ==
            "top")) {
            sweep = 0;
          }
          arr.push({
            "type": "arc",
            data: {
              "radius": transitionRadius,
              "overX": this.ep.x, //(next.direction == "left")?-transitionRadius:transitionRadius,
              "overY": this.ep.y - (next.direction == "top" ? transitionRadius : -transitionRadius),
              "sweep": sweep
            }
          });
        }
        this.hasTransition = true;
      }
    } else {
      arr.push(ep);
      this.hasTransition = false;
    }
    for (let n = 0; n < arr.length; n++) {
      let obj = arr[n];
      if (n == 0 && obj.type == "point") {
        path.push(["M", obj.data.x, obj.data.y]);
      } else if (obj.type == "point") {
        path.push(["L", obj.data.x, obj.data.y]);
      } else if (obj.type == "arc") {
        path.push(["A", obj.data.radius, obj.data.radius, 0, 0, obj.data.sweep, obj.data.overX, obj.data.overY]);
      }
    }
    try {
      element = paper.path(path).attr({
        "stroke-width": line.strokeWidth,
        "stroke": line.stroke
      });
      if (this.isLast) {
        arrow = paper.path(tools.drawArrow(arrowSegmen)).attr({
          "stroke-width": line.strokeWidth,
          "stroke": line.stroke,
          "fill": line.stroke
        });
      }
    } catch (ex) {
      console.log(ex);
    }
    if (element) {
      $(element[0]).data("object", this);
    }
    if (arrow) {
      $(arrow[0]).data("object", this);
    }
  }
  //设置交点
  this.setIntersection = function (point) {
    inter.push(point);
  }
  //清理已经有的交点
  this.clearIntersection = function () {
    inter = [];
  }
  this.getIntersection = function () {
    return inter;
  }
  this.remove = function () {
    if (element) {
      element.remove();
    }
    if (arrow) {
      arrow.remove();
    }
  }
  this.refresh = function () {
    if (element) {
      element.attr({
        "stroke-width": line.strokeWidth,
        "stroke": line.stroke
      });
    }
    if (arrow) {
      arrow.attr({
        "stroke-width": line.strokeWidth,
        "stroke": line.stroke,
        "fill": line.stroke
      });
    }
  }
  this.hide = function () {
    if (element) {
      element.hide();
    }
    if (arrow) {
      arrow.hide();
    }
  }
  this.show = function () {
    if (element) {
      element.show();
    }
    if (arrow) {
      arrow.show();
    }
  }
}

export default Segmen;