<template>
  <el-tabs v-model="tabIndex" class="ds-meta-data-select" type="border-card">
    <el-tab-pane name="0" label="普通组件">
      <meta-data-select-item
        v-if="loaded[0]"
        ref="select0"
        url="rm/list/control"
      />
    </el-tab-pane>
    <el-tab-pane name="1" label="业务组件">
      <meta-data-select-item
        v-if="loaded[1]"
        ref="select1"
        url="rm/list/bc"
      />
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import MetaDataSelectItem from "./item/metaDataSelectItem";

export default {
  name: 'DesCustomComponent',
  components: {
    MetaDataSelectItem
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      tabIndex: '0',
      loaded: [true, false]
    }
  },
  watch: {
    'tabIndex' (to) {
      this.$set(this.loaded, parseInt(to), true);
    }
  },
  methods: {
    yes () {
      return this.$refs['select' + this.tabIndex].yes();
    }
  }
}
</script>