/* eslint-disable no-unused-vars */
import { nodeSize, nodeTaskSize, childNodeSize } from "./tools";
import { getAttributes } from "./common";
import {
  NodeProperties,
  UserTaskNodeProperties,
  JudgementNodeProperties,
  ChildTaskProperties,
  StartNodeProperties,
  endNodeProperties,
  ScriptNodeProperties,
  JumpNodeProperties,
  WebTaskProperties,
  MessageTaskProperties
} from "./propertySource";

const Node = function (opt) {
  const size = opt.sort === "task" && opt.type !== '102' ? nodeTaskSize : nodeSize;
  this.padding = 5;
  this.nameWidth = 120;
  const options = Object.assign({
    name: "",
    width: size.width,
    height: size.height,
    x: 0,
    y: 0,
    img: "",
    sort: "task",
    type: "101"
  }, opt);
  this.id = options.id || null;
  this.name = options.name;
  this.point = {
    x: options.x,
    y: options.y
  };
  this.img = options.img;
  this.width = options.width;
  this.height = options.height;
  this.imgWidth = nodeSize.width;
  this.imgHeight = nodeSize.height;
  if (options.sort === "task") {
    this.open = Object.assign({
      show: options.type === "102" ? false : true,
      width: options.type === "102" ? childNodeSize.width : nodeTaskSize.width,
      height: options.type === "102" ? childNodeSize.height : nodeTaskSize.height
    }, options.open);
    this.taskMsg = options.taskMsg || '';
  }
  this.isRender = false;
  this.type = options.type;
  this.sort = options.sort; // 分类
  this.out = [];
  this.in = [];
  if (options.type === '6') {
    this.OutLine = {};
  }
  let properties;
  if (!options.attributes) {
    if(options.type === '101') {
      properties = UserTaskNodeProperties();
    } else if(options.type === '102') {
      properties = ChildTaskProperties();
    } else if(options.type === '103') {
      const config = {
        name: "脚本任务",
        Type: "脚本任务",
        language: "java"
      }
      properties = ScriptNodeProperties(config);
    } else if(options.type === '104') {
      properties = WebTaskProperties();
    } else if(options.type === '105') {
      properties = MessageTaskProperties();
    } else if(options.type === '106') { 
      const config = {
        name: "python任务",
        Type: "python任务",
        language: "python"
      }
      properties = ScriptNodeProperties(config);
    } else if(options.type === '107') {
      const config = {
        name: "shell任务",
        Type: "shell任务",
        language: "shell"
      }
      properties = ScriptNodeProperties(config);
    } else if(options.type === '108') {
      const config = {
        name: "SQL任务",
        Type: "SQL任务",
        language: "SQL"
      }
      properties = ScriptNodeProperties(config);
    } else if(options.type === '201') {
      properties = StartNodeProperties();
    } else if(options.type === '299') {
      properties = endNodeProperties();
    } else if(options.type === '301') {
      properties = JudgementNodeProperties();
    } else if(options.type === '302') {
      properties = JumpNodeProperties();
    } else {
      properties = NodeProperties();
    }
    this.attributes = getAttributes(properties);
    if(!this.attributes.BaseInfo) {
      this.attributes.BaseInfo = {};
    }
    this.attributes.BaseInfo.Name = this.name;
    this.attributes.BaseInfo.ID = this.id;
    // this.attributes.Type = this.type;
  } else {
    this.attributes = { ...options.attributes };
  }
}

export default Node;