<template>
  <div ref="designerPanel" class="ds-designer ds-seat-design">
    <div class="designer-head">
      <top-tools></top-tools>
    </div>
    <div class="designer-content">
      <div class="seat-designer-center">
        <left-tools></left-tools>
        <seat-canvas></seat-canvas>
        <right-tools></right-tools>
      </div>
    </div>
  </div>
</template>

<script>
  import topTools from "_platform/dsf/designer/seat/seatDesign/topTools.vue";
  import leftTools from "_platform/dsf/designer/seat/seatDesign/leftTools.vue";
  import rightTools from "_platform/dsf/designer/seat/seatDesign/rightTools.vue"
  import canvas from "_platform/dsf/designer/seat/seatDesign/canvas.vue";

  import eventBus from "_platform/dsf/designer/seat/eventBus";
  import {eventBusEvent} from "_platform/dsf/designer/seat/constant";

  export default {
    name: "DsfSeatDesign",
    components: {
      topTools,
      leftTools,
      "SeatCanvas": canvas,
      rightTools,
    },
    data() {
      return {
        keyStack: [],
      }
    },
    computed: {},

    beforeDestroy(){
      $(document).off("keydown");
      $(document).off("keyup");
    },
    mounted(){
      const self = this;
      let keyCode;
      $("body").on("keydown", function (evt) {
        keyCode = keyNum2Str(evt.keyCode);
        if (!keyCode) return;
        let index = self.keyStack.indexOf(keyCode);
        if (index === -1) self.keyStack.push(keyCode);
        let keyStr = self.keyStack.join("-");
        if (keyStr === "ctrl" || keyStr === "ctrl-d") {
          evt.stopPropagation();
          evt.preventDefault();
        }

        if (keyStr === "ctrl-d"){
          eventBus.emit(eventBusEvent.KEY_CTRL_D);
        }
        if (keyStr === "delete"){
          eventBus.emit(eventBusEvent.KEY_DELETE)
        }
      });
      $("body").on("keyup", function (evt) {
        evt.stopPropagation();
        evt.preventDefault();
        keyCode = keyNum2Str(evt.keyCode);
        _.pull(self.keyStack, keyCode);   //删除对应的keyCode
      });
      $("body").on("click", function () {
        eventBus.emit(eventBusEvent.BODY_EMPTY_CLICK);
      })
    },

    methods: {

    },
  }

  function keyNum2Str(keyCode) {
    switch (keyCode) {
      case 17:
        keyCode="ctrl";
        break;
      case 18:
        keyCode="alt";
        break;
      case 68:
        keyCode = "d";
        break;
      case 65:
        keyCode = "a";
        break;
      case 46:
        keyCode = "delete";
        break;
      case 16:
        keyCode = "shift";
        break;
      default: keyCode = null;
    }
    return keyCode
  }

</script>

<style type="text/scss" lang="scss">
  @import "~_platform/assets/styles/designer/seatDesign.scss";
</style>