<template>
  <div
    ref="nodes"
    class="nodes"
    :class="{ isSub: isSub, active: isSub && dragToSubNode === parentNode }"
    :style="{
      height: current
        ? isSub
          ? `calc(100% - ${toPx(parentNode.imgHeight + 2 * parentNode.padding + 2)})`
          : '100%'
        : 0,
      top: isSub ? toPx(parentNode.imgHeight + 2 * parentNode.padding) : 0,
      pointerEvents: current ? 'auto' : '',
    }"
  >
    <div
      v-for="node in nodes"
      :key="node.id"
      :data-id="node.id"
      :data-parent-id="isSub ? parentNode.id : ''"
      :draggable="draggable"
      class="nodeOuter"
      :class="{
        'task-node': node.sort === 'task' && node.open && node.open.show,
      }"
      :style="{
        top: showOuterPadding(node) ? toPx(node.point.y - 50) : toPx(node.point.y - node.padding),
        left: showOuterPadding(node) ? toPx(node.point.x - 50) : toPx(node.point.x - node.padding),
        padding: showOuterPadding(node)
          ? `${toPx(50) + ' ' + toPx(70) + ' ' + toPx(70) + ' ' + toPx(50)}`
          : toPx(node.padding),
        pointerEvents:
          (current && current.id !== node.id) ||
          (isDrewingLine === 'end' && (node.type === '202' || node.type === '203')) ||
          (isDrewingLine === 'start' && node.type === '299')
            ? 'none'
            : 'auto',
      }"
      @dragstart="drag($event, node)"
      @mousedown="selectNode($event, node)"
      @dblclick="showSub($event, node)"
    >
      <div
        v-if="node.sort !== 'task' || (node.sort === 'task' && node.open && !node.open.show)"
        :style="{
          width: toPx(node.width),
          height: toPx(node.height),
        }"
        class="node"
        :class="{
          selected: checked === node || selectedList.indexOf(node) > -1,
        }"
        draggable="false"
        :data-id="node.id"
      >
        <img
          :class="{
            selected: checked === node || selectedList.indexOf(node) > -1,
          }"
          :style="{
            width: toPx(node.imgWidth),
            height: toPx(node.imgHeight),
          }"
          :src="require(`_platform/assets/styles/themes/workFlow/${node.type}.png`)"
          :data-id="node.id"
          draggable="false"
        />
        <div v-show="isDrawLine" class="joinPoint l"></div>
        <div v-show="isDrawLine" class="joinPoint r"></div>
        <div v-show="isDrawLine" class="joinPoint t"></div>
        <div v-show="isDrawLine" class="joinPoint b"></div>
        <p
          :title="node.name"
          :style="{ width: toPx(node.nameWidth) }"
          class="nodename ellipsis"
          @dblclick="editNode($event, node)"
        >
          <span v-if="!node.edit">{{ node.name }}</span>
          <input
            v-if="node.edit"
            v-model="node.name"
            v-focus
            type="text"
            class="nodenameInput"
            @keyup.enter="node.edit = null"
            @blur="node.edit = null"
            @input="editName(node)"
            @mousedown="
              $event.stopPropagation();
              $event.preventDefault();
            "
            @dblclick="$event.stopPropagation()"
          />
        </p>
      </div>
      <div
        v-if="node.sort === 'task' && node.type != '102' && node.open && node.open.show"
        class="node"
        :class="{
          selected: checked === node || selectedList.indexOf(node) > -1,
          'normal-node': node.sort === 'task' && node.type !== '102',
        }"
        :style="{
          width: toPx(node.width),
          height: toPx(node.height),
          padding: '30px 15px',
          pointerEvents: isDrewingLine ? 'none' : '',
        }"
        :data-id="node.id"
        :data-parent-id="isSub ? parentNode.id : ''"
        draggable="false"
      >
        <img
          class="img"
          :class="{
            selected: checked === node || selectedList.indexOf(node) > -1,
          }"
          :style="{
            width: toPx(node.imgWidth),
            height: toPx(node.imgHeight),
          }"
          draggable="false"
          :src="require(`_platform/assets/styles/themes/workFlow/${node.type}.png`)"
        />
        <div v-show="isDrawLine" class="joinPoint l"></div>
        <div v-show="isDrawLine" class="joinPoint r"></div>
        <div v-show="isDrawLine" class="joinPoint t"></div>
        <div v-show="isDrawLine" class="joinPoint b"></div>
        <p :title="node.name" class="nodename ellipsis" @dblclick="editNode($event, node)">
          <span v-if="!node.edit">{{ node.name }}</span>
          <input
            v-if="node.edit"
            v-model="node.name"
            v-focus
            type="text"
            class="nodenameInput"
            @keyup.enter="node.edit = null"
            @blur="node.edit = null"
            @input="editName(node)"
            @mousedown="
              $event.stopPropagation();
              $event.preventDefault();
            "
            @dblclick="$event.stopPropagation()"
          />
        </p>
        <div class="task-msg" @dblclick="showRangeRule($event)">
          {{ node.taskMsg || "未设置发送范围" }}
        </div>
      </div>
      <div
        v-if="node.sort === 'task' && node.type == '102'"
        v-show="node.open && node.open.show"
        class="node"
        :class="{
          selected: checked === node || selectedList.indexOf(node) > -1,
          'sub-node': node.type === '102' && node.open && node.open.show,
        }"
        :style="{
          width: toPx(node.width),
          height: toPx(node.height),
          padding: toPx(30),
          pointerEvents: isDrewingLine ? 'none' : '',
        }"
        :data-id="node.id"
        :data-parent-id="isSub ? parentNode.id : ''"
        draggable="false"
      >
        <img
          class="img"
          :class="{
            selected: checked === node || selectedList.indexOf(node) > -1,
          }"
          :style="{
            width: toPx(node.imgWidth),
            height: toPx(node.imgHeight),
          }"
          draggable="false"
          :src="require(`_platform/assets/styles/themes/workFlow/${node.type}.png`)"
        />
        <div v-show="isDrawLine" class="joinPoint l"></div>
        <div v-show="isDrawLine" class="joinPoint r"></div>
        <div v-show="isDrawLine" class="joinPoint t"></div>
        <div v-show="isDrawLine" class="joinPoint b"></div>
        <p :title="node.name" class="nodename ellipsis" @dblclick="editNode($event, node)">
          <span v-if="!node.edit">{{ node.name }}</span>
          <input
            v-if="node.edit"
            v-model="node.name"
            v-focus
            type="text"
            class="nodenameInput"
            @keyup.enter="node.edit = null"
            @blur="node.edit = null"
            @input="editName(node)"
            @mousedown="
              $event.stopPropagation();
              $event.preventDefault();
            "
            @dblclick="$event.stopPropagation()"
          />
        </p>
        <subprocess
          ref="subprocess"
          :is-draw-line="isDrawLine"
          :lines="node.lines"
          :parent-node="node"
          :nodes="node.nodes"
          :events="node.events"
          :checked="checked"
          :config="config"
        ></subprocess>
        <span class="top-resize" @mousedown="dragResize($event, node, 'horizontal', 'top')"></span>
        <span class="right-resize" @mousedown="dragResize($event, node, 'vertical', 'right')"></span>
        <span class="bottom-resize" @mousedown="dragResize($event, node, 'horizontal', 'bottom')"></span>
        <span class="left-resize" @mousedown="dragResize($event, node, 'vertical', 'left')"></span>
      </div>
    </div>
  </div>
</template>
<script>
import Node from "./Node";
import Subprocess from "./Subprocess";
import { getGridPoint, toPx } from "./common";
import { nodeSize, childNodeSize } from "./tools";

export default {
  name: "Nodes",
  components: {
    Subprocess,
  },
  props: {
    nodes: {
      type: Array,
      default() {
        return [];
      },
    },
    isDrawLine: {
      type: Boolean,
      default() {
        return false;
      },
    },
    checked: {
      type: Object,
      default() {
        return {};
      },
    },
    config: {
      type: Object,
      default() {
        return {};
      },
    },
    isSub: {
      // 子流程
      type: Boolean,
      default() {
        return false;
      },
    },
    parentNode: {
      type: Object,
      default() {
        return null;
      },
    },
  },
  data() {
    return {
      dragToSubNode: null,
      current: null,
      edit: null,
      offset: {},
      moveObj: null,
      distance: 30, // 吸附距离
      resizeNode: null,
      mouseDownPonit: null,
      isDrewingLine: null,
      isSelected: false,
    };
  },

  computed: {
    draggable() {
      return !this.isDrawLine && this.checked instanceof Node && !this.resizeNode && this.selectedList.length <= 1;
    },
    scale() {
      return this.$store.state.flow.page.scale;
    },
    selectedList() {
      return this.$store.state.flow.selectedList;
    },
  },
  mounted() {
    this.$refs.nodes.ondragover = (e) => {
      // this.move(e);
      //取消事件的默认行为
      return false;
    };
    this.setNodePosition();
    this.$bus.$on("isMoveNode", (val) => {
      this.current = val;
      if (!val) {
        this.dragToSubNode = null;
        this.$bus.$emit("update-baseLine", {
          show: "",
          top: 0,
          left: 0,
        });
      }
    });
    this.$bus.$on(`moveCopyNode${this.isSub ? this.parentNode.id : ""}`, (e, node) => {
      this.move(e, node);
    });
    this.$bus.$on(`isDrewingLine${this.isSub ? this.parentNode.id : ""}`, (val) => {
      this.isDrewingLine = val;
    });
    this.$bus.$on("startMoveEvent", (val) => {
      this.moveObj = val;
    });
    this.$bus.$on("endMoveEvent", () => {
      this.moveObj = null;
    });
    this.$bus.$on("dragToSubNode", (node) => {
      this.dragToSubNode = node;
    });
    this.$bus.$on(`globalMove${this.isSub ? this.parentNode.id : ""}`, this.globalMove);
    this.$bus.$on(`selectGlobal${this.isSub ? this.parentNode.id : ""}`, this.selectGlobal);
    document.addEventListener("mousemove", (e) => {
      if (this.resizeNode) {
        this.moveResize(e);
      }
      // if(e.ctrlKey && this.mouseDownPonit) {
      //   if(this.selectedList.length > 1) {
      //     this.mouseDownPonit = null;
      //   }
      // }
    });
    document.addEventListener("mouseup", (e) => {
      if (this.resizeNode) {
        this.dropResize(e);
      }
      if (e.ctrlKey) {
        this.setSelectList(e);
      }
    });
  },
  methods: {
    toPx,
    showOuterPadding(node) {
      return this.moveObj && node.sort === "task";
    },
    getNamePostion(node, type) {
      const postion = {
        x: node.point.x < 0 ? 0 : node.point.x,
        y: node.point.y < 0 ? 0 : node.point.y,
      };
      const leftOffset = (node.width - node.nameWidth) / 2;
      const topOffset = node.height;
      const pos = {
        top: this.toPx(postion.y + topOffset),
        left: this.toPx(postion.x + leftOffset),
      };
      return pos[type];
    },
    drag(e, item) {
      e.dataTransfer && e.dataTransfer.setDragImage(new Image(), 0, 0); // 设置 拖动背景图为空
      e.stopPropagation();
      if (!this.draggable) {
        return;
      }
      if (e.type === "dragstart") {
        //根据e.type判断是PC端还是移动端
        e = event ? event : e;
      } else {
        if (event.touches) {
          e = event.touches[0] ? event.touches[0] : event;
        }
      }
      this.offset = {
        x: e.offsetX,
        y: e.offsetY,
      };
      this.$bus.$emit("update-checked", item);
      this.current = item;
      this.$bus.$emit("dragNode", e, this.current, this.offset, true);
    },
    move(e, node) {
      const mp = this.$store.getters["flow/getMousePointByCanvas"]({
        x: e.pageX,
        y: e.pageY,
      });
      let p = {
        x: mp.x - this.offset.x * this.scale < 0 ? 0 : mp.x - this.offset.x * this.scale,
        y: mp.y - this.offset.y * this.scale < 0 ? 0 : mp.y - this.offset.y * this.scale,
      };
      if (node.parentNodeId) {
        p.x = p.x - this.parentNode.point.x * this.scale;
        // 子节点相对于父节点的位置
        p.y =
          p.y -
          this.parentNode.point.y * this.scale -
          (this.parentNode.imgHeight + this.parentNode.padding * 2) * this.scale;
      }
      // p = getGridPoint(p);
      p = {
        x: p.x / this.scale,
        y: p.y / this.scale,
      };
      // const node = e.target;
      this.setNodePosition();
      this.showSamePositionNodeBaseLine(node, p);
    },
    dragResize(e, node, lineDirection, direction) {
      e.stopPropagation();
      this.$bus.$emit("update-checked", node);
      this.resizeNode = { ...node, lineDirection, direction };
      this.$bus.$emit("update-baseLine", {
        show: lineDirection,
        top: 0,
        left: 0,
      });
    },
    moveResize(e) {
      const page = this.$store.state.flow.page;
      const svgRoot = this.$store.state.flow.svgRoot;
      const lineDirection = this.resizeNode.lineDirection;
      const lanesWidth = this.$store.getters["flow/lanesWidth"];
      const lanesHeight = this.$store.getters["flow/lanesHeight"];
      let resize, minLeft, current, minTop, maxleft, maxTop;
      const scale = this.scale;
      switch (this.resizeNode.direction) {
        case "top":
          maxTop = this.resizeNode.point.y + this.resizeNode.height + lanesHeight - childNodeSize.height;
          current = e.pageY - page.top + svgRoot.scrollTop;
          resize = {
            top: current < maxTop * scale ? (current > 0 ? current : 0) : maxTop * scale,
            left: 0,
          };
          break;
        case "right":
          minLeft = this.resizeNode.point.x + childNodeSize.width + lanesWidth;
          current = e.pageX - page.left + svgRoot.scrollLeft;
          resize = {
            top: 0,
            left: current > minLeft * scale ? current : minLeft * scale,
          };
          break;
        case "bottom":
          minTop = this.resizeNode.point.y + childNodeSize.height + lanesHeight;
          current = e.pageY - page.top + svgRoot.scrollTop;
          resize = {
            top: current > minTop * scale ? current : minTop * scale,
            left: 0,
          };
          break;
        case "left":
          maxleft = this.resizeNode.point.x + this.resizeNode.width + lanesWidth - childNodeSize.width;
          current = e.pageX - page.left + svgRoot.scrollLeft;
          resize = {
            top: 0,
            left: current < maxleft * scale ? (current > 0 ? current : 0) : maxleft * scale,
          };
          break;
      }
      resize.top = resize.top / scale;
      resize.left = resize.left / scale;
      const baseLine = Object.assign(resize, {
        showV: lineDirection === "vertical",
        showH: lineDirection === "horizontal",
      });
      this.$bus.$emit("update-baseLine", baseLine);
      this.$bus.$emit("autoScroll", {
        type: "lane",
        data: this.resizeNode,
        mouse: {
          x: e.pageX,
          y: e.pageY,
        },
      });
    },
    dropResize(e) {
      if (!this.resizeNode || !this.checked || !(this.checked instanceof Node)) {
        return;
      }
      const page = this.$store.state.flow.page;
      const svgRoot = this.$store.state.flow.svgRoot;
      const lineDirection = this.resizeNode.lineDirection;
      const lanesWidth = this.$store.getters["flow/lanesWidth"];
      const lanesHeight = this.$store.getters["flow/lanesHeight"];
      const scale = this.scale;
      let resize, minLeft, current, minTop, maxleft, maxTop, beforeX, beforeY;
      switch (this.resizeNode.direction) {
        case "top":
          maxTop = this.resizeNode.point.y + this.resizeNode.height + lanesHeight - childNodeSize.height;
          current = e.pageY - page.top + svgRoot.scrollTop;
          beforeY = this.checked.point.y;
          this.checked.point.y = (current < maxTop * scale ? (current > 0 ? current : 0) : maxTop * scale) / scale;
          this.checked.height = this.checked.height + beforeY - this.checked.point.y;
          break;
        case "right":
          minLeft = this.resizeNode.point.x + childNodeSize.width + lanesWidth;
          current = e.pageX - page.left + svgRoot.scrollLeft;
          this.checked.width = (current > minLeft * scale ? current : minLeft * scale) / scale - this.checked.point.x;
          break;
        case "bottom":
          minTop = this.resizeNode.point.y + childNodeSize.height + lanesHeight;
          current = e.pageY - page.top + svgRoot.scrollTop;
          this.checked.height = (current > minTop * scale ? current : minTop * scale) / scale - this.checked.point.y;
          break;
        case "left":
          maxleft = this.resizeNode.point.x + this.resizeNode.width + lanesWidth - childNodeSize.width;
          current = e.pageX - page.left + svgRoot.scrollLeft;
          beforeX = this.checked.point.x;
          this.checked.point.x = (current < maxleft * scale ? (current > 0 ? current : 0) : maxleft * scale) / scale;
          this.checked.width = this.checked.width + beforeX - this.checked.point.x;
          break;
      }
      this.$bus.$emit("nodeMove", this.checked);
      // 根据鼠标点击的位置在整个页面上的坐标和在图片上的坐标来确定整个图片的坐标
      this.$bus.$emit("update-baseLine", {
        showV: false,
        showH: false,
        top: 0,
        left: 0,
      });
      this.$bus.$emit("resize");
      this.resizeNode = null;
    },
    selectNode(e, node) {
      this.mouseDownPonit = {
        x: e.pageX,
        y: e.pageY,
      };
      if (e.ctrlKey) {
        if (this.selectedList.length > 1 && this.selectedList.indexOf(node) === -1) {
          this.$store.commit("flow/addSelected", node);
          this.isSelected = true;
        }
        this.$bus.$emit("startCopyNodes", e);
        this.$bus.$emit("update-checked", node);
        e.stopPropagation();
      } else {
        e.stopPropagation();
        // 选中几个节点后 则启动全局拖拽
        if (!this.isDrawLine && this.selectedList.length > 1 && this.selectedList.indexOf(node) > -1) {
          this.$bus.$emit("startCopyNodes", e);
          this.$bus.$emit("update-checked", node);
          // this.$bus.$emit('startGlobalMove', e);
          return;
        }
        this.$bus.$emit("update-checked", node);
        if (this.isDrawLine && node && node.type !== "204") {
          // 结束事件只有进线 不出线
          setTimeout(() => {
            this.$bus.$emit(`startDraw${this.isSub ? this.parentNode.id : ""}`, e, node, this.parentNode);
          }, 0);
        }
        // 子节点不允许全局移动
        const selectedList = this.isSub ? [] : [node];
        this.$store.commit("flow/setSelectedList", selectedList);
      }
    },
    setSelectList(e) {
      // 子节点不允许全局移动
      if (this.isSub || !this.mouseDownPonit) {
        return;
      }
      const dis = Math.sqrt(
        Math.pow(this.mouseDownPonit.x - e.pageX, 2) + Math.pow(this.mouseDownPonit.y - e.pageY, 2)
      );
      if (!this.isSelected && dis < 5) {
        const index = this.selectedList.indexOf(this.checked);
        if (index > -1) {
          this.$store.commit("flow/deleteSelected", index);
          this.$bus.$emit("update-checked", flow);
        } else {
          this.$store.commit("flow/addSelected", this.checked);
        }
      }
      this.isSelected = false;
      this.mouseDownPonit = null;
    },
    showSub(e, node) {
      e.stopPropagation();
      // 子流程里面 子流程不能展开
      if (node.sort === "task" && (!node.parentNodeId || (node.parentNodeId && node.type !== "102"))) {
        node.open.show = !node.open.show;
        if (node.open.show) {
          node.width = node.open.width;
          node.height = node.open.height;
        } else {
          node.open.width = node.width;
          node.open.height = node.height;
          node.width = nodeSize.width;
          node.height = nodeSize.height;
        }
        this.$bus.$emit(`showSubNode${this.isSub ? this.parentNode.id : ""}`, node);
      }
    },
    editNode(e, node) {
      e.stopPropagation();
      this.$set(node, "edit", true);
    },
    // 设置节点所在位置
    setNodePosition() {
      this.nodePosition = {
        x: {},
        xCenter: {},
        y: {},
        yCenter: {},
      };
      for (let i = 0; i < this.nodes.length; i++) {
        const n = this.nodes[i];
        if (!this.nodePosition.x[n.point.x]) {
          this.nodePosition.x[n.point.x] = [];
        }
        if (!this.nodePosition.y[n.point.y]) {
          this.nodePosition.y[n.point.y] = [];
        }
        let center = getGridPoint({
          x: this.parentNode ? this.parentNode.point.x + n.point.x + n.width / 2 : n.point.x + n.width / 2,
          y: this.parentNode
            ? this.parentNode.point.y +
              (this.parentNode.imgHeight + this.parentNode.padding * 2) +
              n.point.y +
              n.height / 2
            : n.point.y + n.height / 2,
        });
        if (!this.nodePosition.xCenter[center.x]) {
          this.nodePosition.xCenter[center.x] = [];
        }
        if (!this.nodePosition.yCenter[center.y]) {
          this.nodePosition.yCenter[center.y] = [];
        }
        // 左对齐点
        this.nodePosition.x[n.point.x].push(n);
        // 上对齐点
        this.nodePosition.y[n.point.y].push(n);
        // x轴居中对齐点
        this.nodePosition.xCenter[center.x].push(n);
        // y轴居中对齐点
        this.nodePosition.yCenter[center.y].push(n);
      }
    },
    // 判断是否显示基准线
    showSamePositionNodeBaseLine(node, newPosition) {
      const result = this.checkSamePositionNode(node, newPosition);
      const offset = this.$store.getters["flow/getSvgOffset"];
      const baseLine = {
        showV: result.V,
        showH: result.H,
        left: result.V ? (result.xCenter ? result.x : newPosition.x + offset.left / this.scale) : 0,
        top: result.H ? (result.yCenter ? result.y : newPosition.y + offset.top / this.scale) : 0,
      };
      if (node.parentNodeId) {
        baseLine.left = baseLine.left + this.parentNode.point.x;
        // 子节点相对于父节点的位置
        baseLine.top =
          baseLine.top + this.parentNode.point.y + (this.parentNode.imgHeight + this.parentNode.padding * 2);
      }

      this.$bus.$emit("update-baseLine", baseLine);
    },
    //检测相同位置是否有其他节点
    checkSamePositionNode(node, newPosition) {
      const scale = this.scale;
      const nodePosition = this.nodePosition;
      const x = newPosition.x;
      const y = newPosition.y;
      let result = { V: false, H: false, xCenter: false, yCenter: false };
      if (nodePosition.x[x] && nodePosition.x[x].length > 0) {
        result.V = "vertical";
      } else if (nodePosition.x[x] && nodePosition.x[x].length == 1 && nodePosition.x[x][0] != node) {
        result.V = "vertical";
      }
      const center = getGridPoint({
        x: this.parentNode ? this.parentNode.point.x + x + node.width / 2 : x + node.width / 2,
        y: this.parentNode
          ? this.parentNode.point.y + (this.parentNode.imgHeight + this.parentNode.padding * 2) + y + node.height / 2
          : y + node.height / 2,
      });
      if (nodePosition.xCenter[center.x] && nodePosition.xCenter[center.x].length > 0) {
        const targetNode = nodePosition.xCenter[center.x][0];
        result.xCenter = true;
        result.V = "vertical";
        result.x = getGridPoint({
          x: targetNode.point.x + targetNode.width / 2,
        }).x;
      }
      if (nodePosition.y[y] && nodePosition.y[y].length > 0) {
        result.H = "horizontal";
      }
      if (nodePosition.yCenter[center.y] && nodePosition.yCenter[center.y].length > 0) {
        const targetNode = nodePosition.yCenter[center.y][0];
        result.yCenter = true;
        result.H = "horizontal";
        result.y = getGridPoint({
          y: targetNode.point.y + targetNode.height / 2,
        }).y;
      }
      return result;
    },
    globalMove(current_p, x_move, y_move, e) {
      if (!current_p) {
        return;
      }
      for (let i = 0; i < this.selectedList.length; i++) {
        let node = this.selectedList[i];
        if (x_move) {
          node.point.x += current_p.x;
        }
        if (y_move) {
          node.point.y += current_p.y;
        }
        if (x_move || y_move) {
          this.$bus.$emit("nodeMove", node);
          // node.render();
        }
      }
    },
    selectGlobal(start, end) {
      const selectedList = [];
      if (start && end) {
        this.nodes.forEach((node) => {
          // 判断节点在选中区域内
          // 大于起点坐标
          if (node.point.x > start.x && node.point.y > start.y) {
            // 小于终点坐标
            if (node.point.x + node.width < end.x && node.point.y + node.height < end.y) {
              selectedList.push(node);
            }
          }
        });
      }
      this.$store.commit("flow/setSelectedList", selectedList);
    },
    editName(node) {
      this.$set(node.attributes.BaseInfo, "Name", node.name);
      this.$bus.$emit(`updateNodeName${this.isSub ? this.parentNode.id : ""}`, this.checked);
    },
    showRangeRule(e) {
      e.stopPropagation();
      e.preventDefault();
      document.querySelector(".RangeRule").click();
    },
  },
};
</script>
<style lang="scss" scoped>
.nodes {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  &.isSub {
    border: 1px solid transparent;
    width: calc(100% - 2px);
    &.active {
      border: 1px dashed #000;
      border: 1px dashed #0268af;
    }
  }
  .nodeOuter {
    .joinPoint {
      display: none;
    }
    &:hover {
      .joinPoint {
        display: block;
      }
    }
    .selected {
      .joinPoint {
        display: block;
      }
    }

    &.task-node {
      .node {
        box-sizing: border-box;
        .top-resize {
          position: absolute;
          top: 0;
          width: 100%;
          height: 5px;
          left: 0px;
          cursor: ns-resize;
        }
        .right-resize {
          position: absolute;
          top: 0;
          height: 100%;
          width: 5px;
          right: 0px;
          cursor: ew-resize;
        }
        .bottom-resize {
          position: absolute;
          bottom: 0;
          width: 100%;
          height: 5px;
          left: 0px;
          cursor: ns-resize;
        }
        .left-resize {
          position: absolute;
          top: 0;
          height: 100%;
          width: 5px;
          left: 0px;
          cursor: ew-resize;
        }
      }
    }
  }
}
</style>
