<template>
  <div class="ds-question-formwrap">
    <div class="example">
      <el-form label-position="top">
        <el-form-item prop="value" :label="exampleLabel" :rules="exampleRules">
          <el-checkbox-group v-if="form.layout == '0'" :value="[]">
            <el-checkbox v-for="el in exampleItems" :key="el.value" :label="el.value">{{ el.text }}</el-checkbox>
          </el-checkbox-group>
          <el-checkbox-group v-else :value="[]">
            <el-row>
              <el-col v-for="el in exampleItems" :key="el.value" :span="getColSpan">
                <el-checkbox :label="el.value">{{ el.text }}</el-checkbox>
              </el-col>
            </el-row>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </div>
    <el-form
      ref="form"
      :model="form"
      :rules="rules"
      class="form"
      label-width="auto"
      label-position="left"
    >
      <el-row :gutter="15">
        <el-col :span="24">
          <el-form-item prop="questionBody" label-width="0">
            <el-input v-model="form.questionBody" placeholder="请输入题干"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="isOther" label="是否含有'其他'选项:">
            <el-switch v-model="form.isOther"></el-switch>
          </el-form-item>            
        </el-col>
        <el-col :span="12">
          <el-form-item prop="readOnly" label="本题不允许填写:">
            <el-switch v-model="form.readOnly"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="visible" label="本题是否显示:">
            <el-switch v-model="form.visible"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="required" label="必填标识:">
            <el-switch v-model="form.required"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="maxCheckNum" class="keep-bottom" label="最多选择数量:">
            <el-select v-model="form.maxCheckNum" placeholder="请选择">
              <el-option v-for="el in maxSelectOptions" :key="el.value" :label="el.label" :value="el.value"></el-option>
            </el-select>
          </el-form-item> 
        </el-col>
        <el-col :span="12">
          <el-form-item prop="layout" class="keep-bottom" label="排列样式:">
            <el-select v-model="form.layout" placeholder="请选择">
              <el-option label="自适应" value="0"></el-option>
              <el-option label="竖向排列" value="1"></el-option>
              <el-option-group label="横向排列">
                <el-option label="每行2列" value="2"></el-option>
                <el-option label="每行3列" value="3"></el-option>
                <el-option label="每行4列" value="4"></el-option>
                <el-option label="每行5列" value="5"></el-option>
              </el-option-group>
            </el-select>            
          </el-form-item>          
        </el-col> 
        <el-col :span="12">
          <el-form-item prop="mustCheckNum" label="限制选择数量:">
            <el-select v-model="form.mustCheckNum" placeholder="请选择">
              <el-option v-for="el in mustSelectOptions" :key="el.value" :label="el.label" :value="el.value"></el-option>
            </el-select>
          </el-form-item>             
        </el-col> 
        <el-col :span="12">
          <el-form-item>
            <el-button icon="el-icon-plus" @click="addOption">添加选项</el-button>
            <el-button icon="el-icon-circle-plus-outline" @click="batchAddOption">批量添加</el-button>            
          </el-form-item>
        </el-col>
        <el-col :span="24" style="padding: 15px 0">
          <el-table :data="form.items">
            <el-table-column
              type="index"
              width="50"
            >
            </el-table-column>
            <el-table-column label="选项">
              <template slot-scope="scope">
                <el-form-item size="small" :prop="`items.${scope.$index}.value`" label-width="0" :rules="[{ required: true , message: '请输入' }]">
                  <el-input v-model="form.items[scope.$index].text" placeholder="请输入" @change="(v) => { optionChange(form.items[scope.$index], v) }" />
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column
              label="操作"
              width="180"
              align="right"
            >
              <template slot-scope="scope">
                <el-button v-show="scope.$index === 0" circle size="small" icon="el-icon-circle-plus-outline" @click="addOption"></el-button>
                <el-button circle size="small" icon="el-icon-remove-outline" @click="remove(scope)"></el-button>
                <el-button circle size="small" icon="el-icon-top" :disabled="scope.$index === 0" @click="toggle(scope, 'up')"></el-button>
                <el-button circle size="small" icon="el-icon-bottom" :disabled="scope.$index === form.items.length - 1" @click="toggle(scope, 'down')"></el-button>
              </template>
            </el-table-column>                     
          </el-table>
        </el-col>
        <el-col :span="24">
          <div class="ljsz">
            <span class="label">逻辑设置:</span>
            <span class="btn" @click="showRela">题目关联</span>
          </div>
        </el-col>
      </el-row>                                
    </el-form>
    <el-dialog
      title="添加选项"
      :visible.sync="dialogVisible"
      :modal="true"
      :append-to-body="true"
      :modal-append-to-body="false"
      :before-close="handleClose"
      width="500px"
    >
      <el-input v-model="optionText" type="textarea" :rows="15" placeholder="多个选项使用换行分割"></el-input>
      <span slot="footer" class="dialog-footer">
        <dsf-button type="auxiliary" @click="dialogVisible = false">取 消</dsf-button>
        <dsf-button @click="sure">确 定</dsf-button>
      </span>
    </el-dialog>    
  </div>
</template>
<script>
import questionConfig from "_platform/dsf/mixins/questionConfig";
export default {
    mixins: [questionConfig],
    // eslint-disable-next-line vue/require-prop-types
    props: ["owner"],
    data() {
        var checkMaxNum = (rule, value, callback) => {
          if (this.form.mustCheckNum > 0 && value != this.form.mustCheckNum) {
            callback(new Error('最多选择数量必须等于限选数量'));
          } else {
            callback();
          }
        };
        var checkMustNum = (rule, value, callback) => {
          if (this.form.maxCheckNum > 0 && value > this.form.maxCheckNum) {
            callback(new Error('限选数量不能大于最多选择数量'));
          } else {
            callback();
          }
        };
        return {
            form: {
                questionBody: '请输入题干',
                readOnly: false,
                visible: true,
                required: true,
                isOther: false,
                layout: '0',
                items: [],
                mustCheckNum: 0,
                maxCheckNum: 0,
            },
            rules: {
                questionBody: [{
                    required: true,
                    message: '请输入题干',
                }],
                mustCheckNum: [
                  { validator: checkMustNum, trigger: 'change' }
                ],
                maxCheckNum: [
                  { validator: checkMaxNum, trigger: 'change' }
                ]
            },
            optionText: '',
            dialogVisible: false,
        }
    },
    computed: {
      mustSelectOptions() {
        const arr = this.exampleItems.map((it, index) => {
          const value = index + 1;
          return {
            label: `限选${value}项`,
            value,
          };
        });
        return [{
          label: '不限制',
          value: 0,
        }, ...arr]
      },
      maxSelectOptions() {
        const arr = this.exampleItems.map((it, index) => {
          const value = index + 1;
          return {
            label: `最多选${value}项`,
            value,
          };
        });
        return [{
          label: '不限制',
          value: 0,
        }, ...arr]
      },
      exampleItems() {
        let arr = this.form.items;
        if (this.form.isOther) {
          arr = [
            ...arr,
            {
              text: '其他',
              value: '其他',
            }
          ];
        }
        return arr;
      },
      getColSpan() {
        switch(this.form.layout) {
          case '1':
            return 24;
          case '2':
            return 12;
          case '3':
            return 8;
          case '4':
            return 6;
          case '5':
            return 4;
          default:
            return 24;
        }
      },
      exampleLabel() {
        return `${this.owner.questionIndex}.${this.form.questionBody}`;
      },
      exampleRules() {
        return this.form.required ? [{ required: true, trigger: 'blur' }] : null;
      }
    },
    created(){
      this.owner.layout=this.owner.layout+'';
    },
    methods: {
      optionChange(item, v) {
        item.value = v;
      },
      addOption() {
        const len = this.form.items.length;
        this.form.items.push({
          text: `选项${len + 1}`,
          value: `选项${len + 1}`,
          attach_text: ''
        });
      },
      batchAddOption() {
        this.optionText = '';
        this.dialogVisible = true;
      },
      handleClose() {
        this.dialogVisible = false;
      },
      toggle(scope, type) {
        const cindex = scope.$index;
        let nextIndex, arr = [...this.form.items], temp = this.form.items[cindex];
        if ((cindex === this.form.items.length - 1 && type === 'down') || (cindex === 0 && type === 'up')) {
          return;
        }
        if (type === 'up') {
          nextIndex = cindex - 1;
        } else {
          nextIndex = cindex + 1;
        }
        arr.splice(cindex, 1);
        arr.splice(nextIndex, 0, temp);
        this.form.items = arr;
      },
      remove(scope) {
        this.form.items.splice(scope.$index, 1);
      },
      sure() {
        const arr = this.optionText.replace(/^\s|\s$/g, '').split(/\n/).map(it => {
          const str = it.replace(/^\s|\s$/g, '').split('|');
          return {
            text: str[0],
            value: str[1]?str[1]:str[0],
            attach_text: ''
          }
        });
        this.form.items = [...this.form.items, ...arr];
        this.dialogVisible = false;
      },
    },
}
</script>