<template>
  <el-table :data="data" fit>
    <el-table-column label="组件名称">
      <template v-slot:default="scope">
        <span>{{ scope.row.label }}</span>
      </template>
    </el-table-column>
    <el-table-column label="元数据代码">
      <template v-slot:default="scope">
        <el-select v-model="scope.row.mdkey" @change="changeKey($event,scope.row)">
          <el-option v-for="(op) in dict" :key="op" :label="op" :value="op"></el-option>
        </el-select>
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
export default {
  name: "DesMetaDataSource",
  props: {
    source: {
      type: Array,
      default() {
        return [];
      }
    },
    dict: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
      data: []
    }
  },
  mounted() {
    this.data = _.map(this.source, (el, i) => {
      return {
        label: el.ctrl.label || el.ctrl.caption,
        ctrl: el.ctrl,
        mdkey: el.source,
        initKey: el.source,
        isChange: false
      }
    });
  },
  methods: {
    changeKey(key, row) {
      if (row.mdkey != row.initKey) {
        row.isChange = true
      }
      else {
        row.isChange = false;
      }
    },
    yes() {
      return this.data;
    }
  }
}
</script>