import { tools } from "./tools";
export default class BoundaryEvent {
  constructor(tool, ev) {
    this.name = tool.name;
    this.url = tool.url;
    this.type = tool.type;
    this.id = tool.id || tools.uuid();
    this.offset = tool.offset || (ev ? {
      x: ev.offsetX,
      y: ev.offsetY
    } : { x: 0, y: 0 });
    this.point = tool.point || {
      x: tool.x,
      y: tool.y
    };
    this.width = tool.width || 50; // 设置宽度
    this.height = tool.height || 50; // 设置高度
    this.img = tool.img || {
      width: 32,
      height: 32
    };
    if (tool.from) {
      this.from = tool.from;
    }
    this.direction = tool.direction;
    this.index = tool.index;
    this.out = tool.out || [];
    this.hide = tool.hide || false;
    this.attributes = tool.attributes || {
      Name: this.name,
      ID: this.id,
      Type: this.type
    };
    this.nodeId = tool.nodeId;
  }
  setAttributes(val) {
    this.attributes = val;
  }
  getAttributes() {
    return this.attributes;
  }
}