<template>
  <el-container>
    <el-header style="padding-right: 10px; text-align: right;">
      <DsfButton @click="addCol">新增</DsfButton>
    </el-header>
    <el-main style="padding: 0px;">
      <el-table
        class="ds-table"
        :border="true"
        :data="tableData"
        size="small"
        :highlight-current-row="true"
      >
        <el-table-column
          label="节点类型"
          :header-align="'center'"
          :align="'center'"
        >
          <template slot-scope="scope">
            <el-input
              v-model="scope.row.typeName"
              placeholder="请输入"
            ></el-input>
          </template>
        </el-table-column>
        <el-table-column
          label="图标类型"
          :header-align="'center'"
          :align="'center'"
        >
          <template slot-scope="scope">
            <el-select
              v-model="scope.row.imgType"
              placeholder="请选择"
              @change="changeProperty(scope.row, scope.$index)"
            >
              <el-option
                v-for="el in imgType"
                :key="el.value"
                :label="el.name"
                :value="el.value"
              ></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          label="节点图标"
          :header-align="'center'"
          :align="'center'"
        >
          <template slot-scope="scope">
            <el-button type="text" @click="openIconDialog(scope.row)">
              {{ scope.row.icon ? sliceIcon(scope.row.icon) : "选择图标" }}
            </el-button>
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          :header-align="'center'"
          :align="'center'"
        >
          <template slot-scope="scope">
            <el-button type="text" @click="deleted(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesTreeImgSet",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
      imgType: [
        { name: "请选择类型", value: "" },
        { name: "文件类型", value: "file" },
        { name: "用户类型", value: "user" },
        { name: "数据类型", value: "data" },
        { name: "组织类型", value: "unit" },
      ],
      path: "../../../assets/styles/themes/tree_img/",
    };
  },
  mounted() {
    if (!this.owner.iconMap) {
      return;
    }
    // console.log("当前数据", this.owner.iconMap);
    this.tableData = this.owner.iconMap;
  },
  methods: {
    sliceIcon(data) {
      return data ? data.slice(this.path.length) : "";
    },
    addCol() {
      let obj = {
        typeName: "",
        imgType: "",
        icon: "",
      };
      this.tableData.push(obj);
    },
    changeProperty(data, index) {
      if (data.icon) {
        this.tableData[index].icon = "";
      }
    },
    deleted(scope) {
      dsf.layer
        .confirm(`确定要删除【${scope.row.typeName}】节点类型吗?`)
        .then(() => {
          // 删除行
          this.tableData.splice(scope.$index, 1);
        })
        .catch((e) => e);
    },
    validate() {
      // 验证数据有没为空的
      if (_.findIndex(this.tableData, (o) => o.typeName === "") !== -1) {
        dsf.layer.message("请输入节点类型", false);
        return false;
      }
      // 验证数据有没重复
      if (this.isRepeat(_.map(this.tableData, "typeName"))) {
        dsf.layer.message("一种节点类型只能配置一次", false);
        return false;
      }
    },
    isRepeat(arr) {
      var hash = {};
      for (var i in arr) {
        if (hash[arr[i]]) return true;
        hash[arr[i]] = true;
      }
      return false;
    },
    yes() {
      // let urlPath = this.path;
      // _(this.tableData).forEach(function (value) {
      //   value.icon = value.icon ? urlPath + value.icon + ".png" : "";
      // });
      return this.tableData;
    },
    openIconDialog(item) {
      if (!item.imgType) {
        dsf.layer.message("请先选择图标类型", false);
        return false;
      }
      dsf.layer.openDialog({
        title: "树图标选择",
        fullscreen: false,
        content: "DesTreeImgList",
        params: {
          treeList: item,
        },
        width: "500px",
        height: "350px",
        btns: [
          {
            text: "确定",
            handler: (data) => {
              // console.log("选中", data);
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
  },
};
</script>
