<template>
  <el-container>
    <el-header style="padding:10px 0px;text-align:right">
      <DsfButton @click="addCol">新增列</DsfButton>
    </el-header>
    <el-main style="padding:0px">
      <el-table ref="table" class="ds-table" :border="true" :data="tableData" :highlight-current-row="true" @row-click="rowClickHandler">
        <el-table-column label="名称" :header-align="'center'" :align="'center'">
          <template v-slot:default="scope">
            第{{ scope.$index+1 }}格
          </template>
        </el-table-column>
        <el-table-column label="高度" :header-align="'center'" :align="'center'">
          <template v-slot:default="scope">
            <el-input v-model="scope.row.height"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="占用列数" :header-align="'center'" :align="'center'">
          <template v-slot:default="scope">
            <el-input v-model.number="scope.row.span"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="列数断点" :header-align="'center'" :align="'center'">
          <el-table-column label="≥1920px" :header-align="'center'" :align="'center'">
            <template v-slot:default="scope">
              <el-input v-model.number="scope.row.lg"></el-input>
            </template>
          </el-table-column>
          <el-table-column label="≥1200px" :header-align="'center'" :align="'center'">
            <template v-slot:default="scope">
              <el-input v-model.number="scope.row.xl"></el-input>
            </template>
          </el-table-column>
          <el-table-column label="≥992px" :header-align="'center'" :align="'center'">
            <template v-slot:default="scope">
              <el-input v-model.number="scope.row.md"></el-input>
            </template>
          </el-table-column>
          <el-table-column label="≥768px" :header-align="'center'" :align="'center'">
            <template v-slot:default="scope">
              <el-input v-model.number="scope.row.sm"></el-input>
            </template>
          </el-table-column>
          <el-table-column label="<768px" :header-align="'center'" :align="'center'">
            <template v-slot:default="scope">
              <el-input v-model.number="scope.row.xs"></el-input>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column label="操作" :header-align="'center'" :align="'center'" :width="120">
          <template v-slot:default="scope">
            <DsfButton :size="'small'" title="插入" icon="hao" btn-style="icon-text" style="min-width:auto;margin-left:5px" @click="insert(scope)"></DsfButton>
            <DsfButton :size="'small'" title="删除" icon="guanbi2" btn-style="icon-text" style="min-width:auto;margin-left:5px" @click="deleted(scope)"></DsfButton>
            <DsfButton :size="'small'" title="上移" icon="shang" btn-style="icon-text" style="min-width:auto;margin-left:5px" @click="up(scope)"></DsfButton>
            <DsfButton :size="'small'" title="下移" icon="xia" btn-style="icon-text" style="min-width:auto;margin-left:5px" @click="down(scope)"></DsfButton>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesFlexGrid",
  mixins: [propertyDialogSetting],
  data() {
    return {
      form: null,
      formNode: null,
      tableData: [],
      sizeMap: [
        { 'size': 'lg', 'desc': '≥1920px' },
        { 'size': 'xl', 'desc': '≥1200px' },
        { 'size': 'md', 'desc': '≥992px' },
        { 'size': 'sm', 'desc': '≥768px' },
        { 'size': 'xs', 'desc': '<768px' }
      ]
    }
  },
  created() {

  },
  beforeMount() {

  },
  mounted() {
    let slots = this.owner.$$getComponent().slots;
    slots = dsf.mix(true, [], slots);
    if (slots[0]) {
      let data = slots[0].controls;
      _.each(data, (el) => {
        this.tableData.push(el);
      });
    }
  },
  methods: {
    rowClickHandler() {
      // let c = row;
      // _.each(this.sizeMap, (el) => {
      //   if (!row[el.size]) {
      //     row[el.size] = {
      //       height: '',
      //       span: ''
      //     }
      //   }
      // });
      // this.form = c;
    },
    addCol() {
      // let keys = _.map(this.sizeMap, 'size');
      let obj = { span: 6, height: 'auto' }
      this.tableData.push(
        dsf.designer.createComponent('dsf.col', obj)
      )
    },
    vaildate() {
      if (data.length == 0) {
        dsf.layer.pc.message("至少存在一格", false);
        return false
      }
      return true;
    },
    deleted(scope) {
      if (scope.row.slots[0].controls.length > 0) {
        let controls = scope.row.slots[0].controls
        dsf.layer.pc.confirm("栅格中存在" + controls.length + "个子组件,删除该单元格子组件将一起被删除。<br/>是否确定要继续?").then(() => {
          dsf.array.remove(this.tableData, scope.row)
        }).catch(() => { })
      } else {
        dsf.array.remove(this.tableData, scope.row)
      }

    },
    up(scope) {
      if (scope.$index > 0) {
        let curr = this.tableData[scope.$index];
        let currIndex = scope.$index;
        dsf.array.remove(this.tableData, curr);
        this.tableData.splice(currIndex - 1, 0, curr);
      }
    },
    down(scope) {
      if (this.tableData.length - 1 > scope.$index) {
        let curr = this.tableData[scope.$index];
        let currIndex = scope.$index;
        dsf.array.remove(this.tableData, curr);
        this.tableData.splice(currIndex + 1, 0, curr);
      }
    },
    insert(scope) {
      let obj = { span: 6, height: 'auto' }
      this.tableData.splice(scope.$index + 1, 0, dsf.designer.createComponent('dsf.col', obj));
    },
    yes() {
      let data = this.tableData;
      _.each(data, (el) => {
        this.checkMaxSpan(el, 'span');
        let keys = _.map(this.sizeMap, 'size');
        _.each(keys, (k) => {
          this.checkMaxSpan(el, k);
        });

      });
      this.owner.slots[0].controls = [];
      this.$nextTick(() => {
        let controls = this.owner.slots[0].controls;
        _.each(data, (el) => {
          controls.push(el);
        });
      })

    },
    checkMaxSpan(row, key) {
      row[key] = dsf.isNumber(row[key]) ? Number(row[key]) : null;
      if (row[key] && row[key] > 24) {
        row[key] = 24;
      }
      if (row[key] && row[key] < 0) {
        row[key] = null;
      }
    }
  }
}
</script>