<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  name: 'DesignerApp',
  data() {
    return {
      themes: dsf.config.defaultTheme
    }
  },
  beforeCreate() {
  },
  created() {

  },
  mounted() {
    //读取配置文件中的皮肤，并将皮肤样式名称附加到body
    document.body.classList.add(this.themes);
  }
}
</script>