<template>
  <MonacoEditor ref="editor" :language="language" @mounted="editorLoaded"></MonacoEditor>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";

export default {
  name: 'DesCodeEdit',
  mixins: [propertyDialogSetting],
  data() {
    return {
      code: "",
      language: ""
    }
  },
  created() {
    this.language = this.config.language || "javascript";
  },
  mounted() {

  },
  methods: {
    yes() {
      if (this.language == "html") {
        return dsf.escapeFilter(this.$refs.editor.getContent());
      }
      else {
        return this.$refs.editor.getContent();
      }

    },
    editorLoaded() {
      let v = this.currentData
      if (this.language == "html") {
        v = dsf.unescapeHTML(v);
      }
      this.$refs.editor.setContent(v);

    }
  }
}
</script>