import { getAttributes } from "./common";
import { LineProperties } from "./propertySource";

const Line = function(config, opts) {
  let elementList = [];
  let options = {
    path: [],
    from: null,
    to: null,
    strokeWidth: config.Path.strokeWidth,
    stroke: config.Path.stroke,
    arrowEnd: config.Path.arrow,
  };
  let selected = false;
  Object.assign(options, opts);
  this.id = options.id || null;
  this.name = options.name || "";
  this.type = options.type || 0;
  this.path = options.path ?
    $.inArray(options.path) ?
    options.path :
    [options.path] :
    [];
  this.from = options.from;
  this.fromType = options.fromType;
  this.to = options.to;
  this.strokeWidth = options.strokeWidth;
  this.stroke = options.stroke;
  this.arrowEnd = options.arrowEnd;
  this.linkInfo = options.linkInfo || {};
  this.oldstroke = null;
  this.isHide = options.isHide || false;

  if (options.attributes) {
    let lineProperties=getAttributes(LineProperties())
    this.attributes = dsf.mix(true, {},lineProperties, options.attributes);
  }


  this.remove = function() {
    if (elementList) {
      for (let i = elementList.length - 1; i >= 0; i--) {
        elementList[i].remove();
      }
    }
    elementList = [];
  };
  this.getSegmenList = function() {
    return elementList;
  };
  this.refresh = function() {
    if (elementList) {
      for (let i = 0; i < elementList.length; i++) {
        elementList[i].refresh();
      }
    }
  };

  this.addElement = function(list) {
    elementList.push(list);
  };

  this.isSelected = function() {
    return selected;
  };
  this.selected = function() {
    selected = true;
    this.oldstroke = this.stroke;
    this.stroke = "#333";
    this.refresh();
  };
  this.unSelected = function() {
    selected = false;
    this.stroke = this.getLineStroke.call(this);
    this.refresh();
  };
  this.hide = function() {
    if (elementList) {
      for (let i = elementList.length - 1; i >= 0; i--) {
        elementList[i].hide();
      }
    }
  };
  this.show = function() {
    if (elementList) {
      for (let i = elementList.length - 1; i >= 0; i--) {
        elementList[i].show();
      }
    }
  };

  this.getPreLine = function(s) {
    return elementList[s.index - 1];
  };
  this.getNextLine = function(s) {
    return elementList[s.index + 1];
  };

  this.getLineStroke = function() {
    if (this.isSelected()) {
      return "#333";
    }
    var lineConfig = config.Lines["Line_" + this.type];
    if (lineConfig) {
      return lineConfig.stroke;
    } else {
      return config.Path.stroke;
    }
  };

  this.setAttributes = function(fnode, tnode) {
    if (!this.attributes) {
      this.attributes = getAttributes(LineProperties());
    } else {
      this.attributes = { ...this.attributes };
    }
    this.attributes.BaseInfo.StartNodeID = this.from;
    this.attributes.BaseInfo.EndNodeID = this.to;
    this.name = this.name || fnode.name + "→" + tnode.name;
    this.attributes.BaseInfo.Name = this.name;
    this.attributes.BaseInfo.ID = this.id;
    this.attributes.BaseInfo.Type = this.type;
    if (fnode.type == "6") {
      const attributes = fnode.attributes;
      //如果有发送条件
      if (
        attributes.OutLine.SendConditions &&
        attributes.OutLine.SendConditions.Condition
      ) {
        let lines = null;
        try {
          lines = attributes.OutLine.SendConditions.Condition.Switch.ItemValue;
        } catch (ex) {
          lines = null;
        }
        if (lines) {
          let isExist = false;
          for (let n = 0; n < lines.length; n++) {
            let l = lines[n];
            if (l.LineID == ui.id) {
              isExist = true;
              break;
            }
          }
          if (!isExist) {
            const lineValue = {
              Value: "",
              Type: "8",
              LineID: ui.id,
              LineName: ui.name,
            };
            lines.push(lineValue);
          }
        }
      }
    }
  };
};

export default Line;