<template>
  <div class="summary">
    <p>
      <span>汇总比例：</span>
      <el-input-number
        v-model="config.Proportion"
        controls-position="right"
        :min="0"
        :max="100"
      ></el-input-number>
      <span>%</span>
    </p>
    <div class="table">
      <SelectTable
        :data="nodesData"
        :select.sync="selectNodes"
        :columns="columns"
      ></SelectTable>
    </div>
  </div>
</template>
<script>
import SelectTable from "./selectTable";
export default {
  name: "Summary",
  components: {
    SelectTable,
  },
  props: {
    lines: {
      type: Array,
      default() {
        return [];
      },
    },
    checked: {
      type: Object,
      default() {
        return {};
      },
    },
    value: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      config: {
        Proportion: 0,
        GatherRange: "",
      },
      nodesData: [],
      selectNodes: [],
      columns: [
        {
          selection: true,
          type: "selection",
          width: 50,
        },
        {
          prop: "label",
          label: "连线名称",
          align: "left",
        },
      ],
    };
  },
  created() {
    this.initTabel();
  },
  mounted() {},
  methods: {
    getContent() {
      this.config.GatherRange = this.selectNodes
        .map((item) => item.value)
        .join(",");
      return this.config;
    },
    initTabel() {
      const { GatherRange, Proportion } = this.value;
      const ids = (GatherRange && GatherRange.split(",")) || [];
      this.config.Proportion = Proportion || 0;
      this.selectNodes = [];
      this.nodesData = [];
      for (let i = 0; i < this.lines.length; i++) {
        const line = this.lines[i];
        if (line.id != this.checked.id) {
          const item = {
            value: line.id,
            label: line.name,
          };
          this.nodesData.push(item);
          if (ids && ids.length > 0) {
            ids.forEach((id) => {
              if (id === line.id) {
                this.selectNodes.push(item);
              }
            });
          }
        }
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.summary {
  /deep/.el-input-number {
    width: 166px;
    margin-right: 10px;
  }
  .table {
    padding: 10px 0;
  }
}
</style>
