<template>
  <div class="seat-item-container">
    <!--参考线-->
    <div v-if="verLine.visible" class="vertical-line" :style="verLineSty"></div>
    <div v-if="horLine.visible" class="horizontal-line" :style="horLineSty"></div>

    <!--select框-->
    <div v-if="selectPosition.visible" class="selected-box" :style="selectSty" @mousedown.left="selectMove">
      <div class="select">
        <div class="anchor circle" @mousedown.left="selectRotate"></div>
        <div class="anchor line"></div>
        <div class="anchor lt" @mousedown.left="selectScale($event, 'lt')"></div>
        <div class="anchor t" @mousedown.left="selectScale($event, 't')"></div>
        <div class="anchor rt" @mousedown.left="selectScale($event, 'rt')"></div>
        <div class="anchor l" @mousedown.left="selectScale($event, 'l')"></div>
        <div class="anchor r" @mousedown.left="selectScale($event, 'r')"></div>
        <div class="anchor lb" @mousedown.left="selectScale($event, 'lb')"></div>
        <div class="anchor b" @mousedown.left="selectScale($event, 'b')"></div>
        <div class="anchor rb" @mousedown.left="selectScale($event, 'rb')"></div>
        <div class="anchor info">
          宽:{{ selectPosition.width }};高:{{ selectPosition.height }};角度:{{ selectPosition.angle }}
        </div>
      </div>
    </div>

    <!--seat-->
    <div
      v-for="(item, it) in items"
      :key="item.id"
      class="seat-box"
      :style="getItemSty(item)"
      :class="{active: activeId === item.id}"
      @mousedown.left="preMove($event, item)"
      @click.right="popMenu($event, item)"
    >
      <div class="seat">
        <div class="anchor circle" @mousedown.left="preRotate($event, item)"></div>
        <div class="anchor line"></div>
        <div class="anchor lt" @mousedown.left="preScale($event, item, 'lt')"></div>
        <div class="anchor t" @mousedown.left="preScale($event, item, 't')"></div>
        <div class="anchor rt" @mousedown.left="preScale($event, item, 'rt')"></div>
        <span :style="getSpanSty(item)">{{ item.seatTxt }}</span>
        <div class="anchor l" @mousedown.left="preScale($event, item, 'l')"></div>
        <div class="anchor r" @mousedown.left="preScale($event, item, 'r')"></div>
        <div class="anchor lb" @mousedown.left="preScale($event, item, 'lb')"></div>
        <div class="anchor b" @mousedown.left="preScale($event, item, 'b')"></div>
        <div class="anchor rb" @mousedown.left="preScale($event, item, 'rb')"></div>
        <div class="anchor info">宽:{{ item.width }};高:{{ item.height }};角度:{{ item.angle }}</div>
      </div>
    </div>

    <!--group-->
    <div
      v-for="(group, it) in groups"
      :key="group.id"
      class="group-box"
      :style="getItemSty(group)"
      :class="{active: activeId === group.id}"
      @mousedown.left="preMove($event, group)"
    >
      <div class="group">
        <div class="anchor circle" @mousedown.left="preRotate($event, group)"></div>
        <div class="anchor line"></div>
        <div class="anchor lt" @mousedown.left="preScale($event, group, 'lt')"></div>
        <div class="anchor t" @mousedown.left="preScale($event, group, 't')"></div>
        <div class="anchor rt" @mousedown.left="preScale($event, group, 'rt')"></div>
        <div class="anchor l" @mousedown.left="preScale($event, group, 'l')"></div>
        <div class="anchor r" @mousedown.left="preScale($event, group, 'r')"></div>
        <div class="anchor lb" @mousedown.left="preScale($event, group, 'lb')"></div>
        <div class="anchor b" @mousedown.left="preScale($event, group, 'b')"></div>
        <div class="anchor rb" @mousedown.left="preScale($event, group, 'rb')"></div>
        <div class="anchor info">宽:{{ group.width }};高:{{ group.height }};角度:{{ group.angle }}</div>
        <div v-for="(member, m) in group.member" :key="member.id" :style="getItemSty(member)" class="member"></div>
      </div>
    </div>
  </div>
</template>

<script>
  import {mapState} from 'vuex';
  import eventBus from "_platform/dsf/designer/seat/eventBus"
  import {eventBusEvent} from "_platform/dsf/designer/seat/constant";
  import {updateGroupMemberPosition} from "_platform/dsf/designer/seat/func"

  export default {
    name: "SeatItem",
    data() {
      return {
        items: [],      //所有座位
        selectedItems: [],  //被选中座位(注:selectedItems各项与items各项引用的是同一个对象，改变selectedItems就会影响到items)
        originWidth: 100,
        originHeight: 100,
        rectMinSize: [40, 40],    //缩放seat时的最小宽高
        selectPosition: {   //框选的位置
          top: 0,
          left: 0,
          width: 0,
          height: 0,
          visible: false
        },
        //垂直对齐线
        verLine: {
          left: 0,
          visible: false
        },
        //水平对齐线
        horLine: {
          top: 0,
          visible: false
        },
        groups: [],   //所有组
      }
    },
    computed: {
      ...mapState("seatDesign", {
        panelSize: "panelSize",
        newSeatItem: "newSeatItem",
        newGroupItem: "newGroupItem",
        activeId: "activeId",
        regionPosition: "regionPosition",
        seatInfo: "seatInfo",
      }),

      selectSty() {
        const {scale} = this.panelSize;
        return {
          width: this.selectPosition.width * scale + "px",
          height: this.selectPosition.height * scale + "px",
          left: this.selectPosition.left * scale + "px",
          top: this.selectPosition.top * scale + "px",
          transform: `rotate(${this.selectPosition.angle}deg)`
        }
      },
      verLineSty() {
        return {
          left: `${this.verLine.left * this.panelSize.scale}` + "px"
        }
      },
      horLineSty() {
        return {
          top: `${this.horLine.top * this.panelSize.scale}` + "px"
        }
      },
    },

    watch: {
      newSeatItem(val) {
        //获取创建seat时，鼠标在panel_outer 里面得位置
        const pos = dsf.math.convertLocalCoordinate("#panel_outer", {
          x: val.pageX,
          y: val.pageY
        }, this.panelSize.scale);
        let seat = {
          id: val.id,
          type: val.type,
          left: pos.x,
          top: pos.y,
          width: this.originWidth,
          height: this.originHeight,
          angle: 0,
          showText: 1,  //是否显示文本 1是 | 2否
          seatTxt: val.seatTxt
        }
        this.items.push(seat);
      },
      newGroupItem(val) {
        //这里复制一个新的group对象, 避免 vue里面 newGroupItem的迷之改变行为
        const groupItem = _.assign({}, val)
        this.groups.push(groupItem);
      },
      regionPosition(val) {
        const self = this;
        //如果 regionPosition 的范围大小为0
        if (val.width === 0 || val.height === 0) {
          self.selectedItems = [];
          restSelectPostion.call(self);
          return;
        }

        const allItems = _.concat(self.items, self.groups)
        self.selectedItems = _.filter(allItems, item => judgePos.call(self, item));

        if (self.selectedItems.length === 0) {
          restSelectPostion.call(self);
          return
        }

        if (self.selectedItems.length === 1) {
          restSelectPostion.call(self);
          self.$store.commit("seatDesign/changeActiveId", self.selectedItems[0].id)
        } else if (self.selectedItems.length > 1) {
          self.selectPosition = getSelectPosition(self.selectedItems);
        }
      },
      seatInfo: {
        handler(val) {
          if (val.type === 'group') {
            let index = _.findIndex(this.groups, item => item.id === val.id);
            this.$set(this.groups, index, val);
          } else {
            let index = _.findIndex(this.items, item => item.id === val.id);
            this.$set(this.items, index, val);
          }
        },
        deep: true
      },
    },
    beforeDestroy() {
      $(document).off("mouseup.scaleSeatItem");
      $(document).off("mousemove.scaleSeatItem");
    },
    mounted() {
      eventBus.on(eventBusEvent.KEY_CTRL_D, () => {
        restSelectPostion.call(this);
        this.selectedItems = [];
        this.$store.commit("seatDesign/changeActiveId", "");
      })
      eventBus.on(eventBusEvent.KEY_DELETE, () => {
        this.deleteActiveSeatItem();
      })
      eventBus.on(eventBusEvent.GET_ACTIVE_SEAT, (callback) => {
        const allItems = _.concat(this.items, this.groups);
        const seat = allItems.find(item => item.id === this.activeId);
        callback && callback(seat);
      })
    },
    methods: {
      getItemSty(item) {
        const {scale} = this.panelSize;
        return {
          width: item.width * scale + "px",
          height: item.height * scale + "px",
          left: item.left * scale + "px",
          top: item.top * scale + "px",
          transform: `rotate(${item.angle}deg)`
        }
      },

      getSpanSty(item) {
        const {scale} = this.panelSize;
        return {
          width: item.width * scale + "px",
          opacity: item.showText === 1 ? 1 : 0.4
        }
      },
      preScale(event, seatItem, action) {
        event.stopPropagation();
        event.preventDefault();
        const self = this;
        const oldRect = _.assign({}, seatItem);
        const newRect = _.assign({}, seatItem);

        //获取seat 缩放时参考的锚点
        const basePoint = getRectBasePointByScale(oldRect, action);

        function handleScale(evt) {
          if (!action || !basePoint) return;
          let mousePosition = dsf.math.convertLocalCoordinate("#panel_outer", {
            x: evt.pageX,
            y: evt.pageY
          }, self.panelSize.scale);
          let afterScaleRect = scaleRect(
            basePoint, oldRect, newRect, mousePosition, action,
            seatItem.type !== 'group' ? self.rectMinSize : getGroupMinSize(oldRect)
          );
          _.assign(seatItem, afterScaleRect);

          if (seatItem.type === 'group') {
            updateGroupMemberPosition(afterScaleRect);
          }
        }

        function handleUp() {
          $(document).off("mousemove.scaleSeatItem");
          if (seatItem.type === 'group') {
            (seatItem.rows === 1) && (seatItem.height = oldRect.height);
            (seatItem.cols === 1) && (seatItem.width = oldRect.width);
          }
        }

        $(document).off("mousemove.scaleSeatItem");
        $(document).on("mousemove.scaleSeatItem", function (evt) {
          evt.preventDefault();
          evt.stopPropagation();
          handleScale(evt);
        });
        $(document).one("mouseup.scaleSeatItem", function () {
          handleUp();
        })
      },
      preMove(evt, seat) {
        evt.preventDefault();
        evt.stopPropagation();
        const self = this;
        setRect2Active.call(self, seat);
        self.verLine.visible = false;
        self.horLine.visible = false;


        const _otherSeatPoints = getOtherSeatPoints.call(self);
        const otherSeatXy = getPointsXy(_otherSeatPoints);

        //用于计算seat的位置
        const old_x = seat.left;
        const old_y = seat.top;
        let downPos = dsf.math.convertLocalCoordinate('#panel_outer', {
          x: evt.pageX,
          y: evt.pageY
        }, self.panelSize.scale);

        function handleMove(evt) {
          let localPos = dsf.math.convertLocalCoordinate('#panel_outer', {
            x: evt.pageX,
            y: evt.pageY
          }, self.panelSize.scale);
          let moveDir = {
            x: localPos.x - downPos.x,
            y: localPos.y - downPos.y
          };
          let left = old_x + moveDir.x;
          let top = old_y + moveDir.y;
          seat.left = parseInt(left);
          seat.top = parseInt(top);


          //计算对齐线位置
          self.verLine.visible = false;
          self.horLine.visible = false;
          const _seatPoints = getRectPoint(seat);
          const seatXy = getPointsXy(_seatPoints);
          let a = computeAlignLine(seatXy, otherSeatXy);
          if (!a) return;
          self.horLine.top = a.top;
          self.horLine.visible = _.isNumber(a.top);
          self.verLine.left = a.left;
          self.verLine.visible = _.isNumber(a.left);
        }

        function handleUp() {
          $(document).off("mousemove.moveSeatItem");
          self.verLine.visible = false;
          self.horLine.visible = false;
        }

        $(document).off("mousemove.moveSeatItem");
        $(document).on("mousemove.moveSeatItem", function (evt) {
          evt.preventDefault();
          evt.stopPropagation();
          handleMove(evt)
        });
        $(document).one("mouseup.moveSeatItem", function () {
          handleUp()
        })
      },
      preRotate(event, seat) {
        event.stopPropagation();
        event.preventDefault();
        $(".ds-seat-design").addClass("is-rotating");
        const self = this;
        const center = getRectCenter(seat);

        function handleRotate(evt) {
          let localPos = dsf.math.convertLocalCoordinate('#panel_outer', {
            x: evt.pageX,
            y: evt.pageY
          }, self.panelSize.scale);
          seat.angle = rotateRect(center, localPos);
        }

        function handleUp() {
          $(document).off("mousemove.rotateSeatItem");
          $(".ds-seat-design").removeClass("is-rotating");
        }

        $(document).off("mousemove.rotateSeatItem");
        $(document).on("mousemove.rotateSeatItem", function (evt) {
          evt.stopPropagation();
          evt.preventDefault();
          handleRotate(evt);
        });
        $(document).one("mouseup.rotateSeatItem", function () {
          handleUp()
        })
      },
      selectRotate(evt) {
        evt.stopPropagation();
        evt.preventDefault();
        $(".ds-seat-design").addClass("is-rotating");
        const self = this;
        const oldRect = _.assign({}, self.selectPosition);
        const center = getRectCenter(oldRect);
        const seatObj = {};
        const allItems = _.concat(self.items, self.groups);
        allItems.forEach(seat => {
          seatObj[seat.id] = _.assign({}, seat);
        });

        function handleRotate(evt) {
          let localPos = dsf.math.convertLocalCoordinate('#panel_outer', {
            x: evt.pageX,
            y: evt.pageY
          }, self.panelSize.scale);
          let angle = rotateRect(center, localPos);
          let offsetAngle = angle - oldRect.angle;
          self.selectPosition.angle = angle;

          //旋转被框选的seat
          self.selectedItems.forEach(seat => {
            let oldSeat = seatObj[seat.id];
            let p = dsf.math.getRotatedPiont(oldSeat.left, oldSeat.top, center.x, center.y, angle);
            let l = p.x + oldSeat.width / 2;
            let t = p.y + oldSeat.height / 2;
            let c = dsf.math.getRotatedPiont(l, t, p.x, p.y, angle);
            p = dsf.math.getRotatedPiont(p.x, p.y, c.x, c.y, -angle);
            seat.top = parseInt(p.y);
            seat.left = parseInt(p.x);
            seat.angle = (oldSeat.angle + offsetAngle) % 360;
            if (oldSeat.angle < 0) seat.angle = 360 + oldSeat.angle;
          })
        }

        function handleUp() {
          $(".ds-seat-design").removeClass("is-rotating");
          self.selectPosition = getSelectPosition(self.selectedItems);
          $(document).off("mousemove.rotateSelect")
        }

        $(document).off("mousemove.rotateSelect");
        $(document).on("mousemove.rotateSelect", function (evt) {
          evt.preventDefault();
          evt.stopPropagation();
          handleRotate(evt)
        })

        $(document).one("mouseup.rotateSelect", function () {
          handleUp()
        })

      },
      selectScale(evt, action) {
        evt.stopPropagation();
        evt.preventDefault();
        const self = this;
        const oldRect = _.assign({}, self.selectPosition);
        const newRect = _.assign({}, self.selectPosition);

        //各个座位的信息
        const seatObj = {};
        const allItems = _.concat(self.items, self.groups);
        allItems.forEach(seat => {
          seatObj[seat.id] = _.assign({}, seat);
        });

        //获取选框缩放时参考的锚点
        const basePoint = getRectBasePointByScale(oldRect, action);
        //获取seat与选框的宽高比
        const ratio = getRatioBySeat2SelectBox(self.selectedItems, self.selectPosition);

        function handleScale(evt) {
          if (!action || !basePoint) return;
          let mousePosition = dsf.math.convertLocalCoordinate("#panel_outer", {
            x: evt.pageX,
            y: evt.pageY
          }, self.panelSize.scale);
          self.selectPosition = scaleRect(basePoint, oldRect, newRect, mousePosition, action, self.rectMinSize);

          //缩放被框选的seat
          self.selectedItems.forEach(item => {
            let newSeat = getNewSize(seatObj[item.id], oldRect, self.selectPosition, action, ratio);
            if (newSeat.type === 'group') {
              updateGroupMemberPosition(newSeat);
            }
            _.assign(item, newSeat)
          })
        }

        function handleUp() {
          $(document).off("mousemove.scaleSelect");
          self.selectedItems.forEach(item => {
            if (item.type === 'group') {
              (item.rows === 1) && (item.height = seatObj[item.id].height);
              (item.cols === 1) && (item.width = seatObj[item.id].width);
            }
          })
          self.selectPosition = getSelectPosition(self.selectedItems);
        }

        $(document).off("mousemove.scaleSelect");
        $(document).on("mousemove.scaleSelect", function (evt) {
          evt.preventDefault();
          evt.stopPropagation();
          handleScale(evt);
        });
        $(document).one("mouseup.scaleSelect", function () {
          handleUp();
        })

      },
      selectMove(evt) {
        evt.preventDefault();
        evt.stopPropagation();
        const self = this;
        const old_x = self.selectPosition.left;
        const old_y = self.selectPosition.top;

        //各个座位的信息
        const seatObj = {};
        const allItems = _.concat(self.items, self.groups);
        allItems.forEach(seat => {
          seatObj[seat.id] = _.assign({}, seat);
        });
        let downPos = dsf.math.convertLocalCoordinate('#panel_outer', {
          x: evt.pageX,
          y: evt.pageY
        }, self.panelSize.scale);

        function handleMove(evt) {
          let localPos = dsf.math.convertLocalCoordinate('#panel_outer', {
            x: evt.pageX,
            y: evt.pageY
          }, self.panelSize.scale);
          let moveDir = {
            x: localPos.x - downPos.x,
            y: localPos.y - downPos.y
          };
          let left = old_x + moveDir.x;
          let top = old_y + moveDir.y;
          self.selectPosition.left = parseInt(left);
          self.selectPosition.top = parseInt(top);

          self.selectedItems.forEach(item => {
            item.left = parseInt(seatObj[item.id].left + moveDir.x);
            item.top = parseInt(seatObj[item.id].top + moveDir.y);
          })
        }

        function handleUp() {
          $(document).off("mousemove.moveSelect");
        }

        $(document).off("mousemove.moveSelect");
        $(document).on("mousemove.moveSelect", function (evt) {
          evt.preventDefault();
          evt.stopPropagation();
          handleMove(evt)
        });
        $(document).one("mouseup.moveSelect", function () {
          handleUp()
        })
      },
      popMenu(evt, item) {
        if (evt.which !== 3) return;
        evt.preventDefault();
        evt.stopPropagation();
        let downPos = dsf.math.convertLocalCoordinate('.seat-designer-page', {x: evt.pageX, y: evt.pageY});

        setRect2Active.call(this, item);
        eventBus.emit(eventBusEvent.SEAT_POP, {
          position: {
            ...downPos
          },
          seat: item
        })
      },
      deleteActiveSeatItem() {
        if (this.selectedItems.length === 0) return;
        this.items = _.differenceWith(this.items, this.selectedItems, (a, b) => _.isEqual(a.id, b.id))
        this.selectedItems = [];
        restSelectPostion.call(this);
        this.$store.commit("seatDesign/changeActiveId", "");
      }
    },
  }

  //激活单个座位
  function setRect2Active(seat) {
    if (this.activeId === seat.id) return;
    //重置selectPosition
    restSelectPostion.call(this);
    //清空已选的座位
    this.selectedItems = [seat];
    this.$store.commit("seatDesign/changeActiveId", seat.id);
  }

  //判断座位是否处于region 边界内
  function judgePos(seat) {
    let seatPoints = getRectPoint(seat);
    let pointRange = getPointRange(seatPoints);
    let {left, top, scale} = this.panelSize;
    let {left: regionLeft, top: regionTop, width: regionWid, height: regionHit} = this.regionPosition;

    let anchor = $(".seat-designer-page").offset();
    let small_position = {
      x: regionLeft + anchor.left,
      y: regionTop + anchor.top
    }
    let large_position = {
      x: regionLeft + anchor.left + regionWid,
      y: regionTop + anchor.top + regionHit
    }
    small_position = dsf.math.convertLocalCoordinate("#panel_outer", small_position, scale);
    large_position = dsf.math.convertLocalCoordinate("#panel_outer", large_position, scale);

    let a = pointRange.min_x >= small_position.x;
    let b = pointRange.max_x <= large_position.x;
    let c = pointRange.min_y >= small_position.y;
    let d = pointRange.max_y <= large_position.y;
    return a && b && c && d
  }

  /**
   * 获取对象上下左右四个边界点
   */
  function getRectPoint(obj) {
    let angle = obj.angle;
    let left = obj.left;
    let top = obj.top;
    let right = obj.left + obj.width;
    let bottom = obj.top + obj.height;

    let pos = {
      lt: 0,
      lb: 0,
      rt: 0,
      rb: 0
    };

    let xCenter = (right - left) * (1 / 2) + left;
    let yCenter = (bottom - top) * (1 / 2) + top;

    pos.lt = dsf.math.getRotatedPiont(left, top, xCenter, yCenter, angle);
    pos.lb = dsf.math.getRotatedPiont(left, bottom, xCenter, yCenter, angle);
    pos.rt = dsf.math.getRotatedPiont(right, top, xCenter, yCenter, angle);
    pos.rb = dsf.math.getRotatedPiont(right, bottom, xCenter, yCenter, angle);
    return pos;
  }

  //获取rect 缩放时参考的锚点
  function getRectBasePointByScale(rect, action) {
    let basePoint = {};
    if (action.length > 1) {
      if (action.indexOf("l") >= 0) {
        basePoint.x = rect.left + rect.width;
      } else {
        basePoint.x = rect.left;
      }
      if (action.indexOf("t") >= 0) {
        basePoint.y = rect.top + rect.height;
      } else {
        basePoint.y = rect.top;
      }
    } else {
      if (action === "r" || action === "b") {
        basePoint.x = rect.left;
        basePoint.y = rect.top;
      } else if (action === "l" || action === "t") {
        basePoint.x = rect.left + rect.width;
        basePoint.y = rect.top + rect.height;
      }
    }

    let center = {
      x: rect.left + rect.width / 2,
      y: rect.top + rect.height / 2
    };
    basePoint = dsf.math.getRotatedPiont(basePoint.x, basePoint.y, center.x, center.y, rect.angle);
    return basePoint
  }

  /**
   * 此函数用于求出Rect缩放之后的 left/top/width/height
   * basePoint Rect缩放时的参考点
   * oldRect rect缩放前的拷贝，此对象不变
   * newRect 初始值等于rect缩放前的拷贝，此对象用于计算缩放后的rect
   * 这里传两个rect是为了不在 scaleRect函数每次运行的时候都去声明 newRect，节约性能
   * mousePosition 当前鼠标在 #panel_outer当中的坐标
   * action 缩放的具体动作
   * minSize [minWidth, minHeight]  最小宽高
   * */
  function scaleRect(basePoint, oldRect, newRect, mousePosition, action, minSize) {
    let dist = dsf.math.getDistance(basePoint, mousePosition);
    let angle = dsf.math.getRadina(basePoint, mousePosition, -dsf.math.a2r(oldRect.angle));
    let pos = {
      x: Math.cos(angle) * dist + oldRect.left,
      y: Math.sin(angle) * dist + oldRect.top
    };

    let w, h;
    if (action.indexOf("r") >= 0) {
      w = pos.x - oldRect.left;
    } else if (action.indexOf("l") >= 0) {
      w = oldRect.left - pos.x;
    }
    if (action.indexOf("b") >= 0) {
      h = pos.y - oldRect.top;
    } else if (action.indexOf("t") >= 0) {
      h = oldRect.top - pos.y;
    }
    //四个对角度
    if (action.length > 1) {
      let sc = w / oldRect.width;
      newRect.width = oldRect.width * sc;
      newRect.height = oldRect.height * sc;
    } else {
      if (dsf.isDef(w)) {
        newRect.width = w;
      } else {
        newRect.width = oldRect.width;
      }
      if (dsf.isDef(h)) {
        newRect.height = h;
      } else {
        newRect.height = oldRect.height;
      }
    }
    if (newRect.width < minSize[0]) {
      newRect.width = minSize[0];
    }
    if (newRect.height < minSize[1]) {
      newRect.height = minSize[1]
    }

    let U = {};
    if (action.length > 1) {
      if (action.indexOf("r") >= 0) {
        U.x = basePoint.x + newRect.width;
      } else {
        if (action.indexOf("l") >= 0) {
          U.x = basePoint.x - newRect.width;
        } else {
          U.x = basePoint.x;
        }
      }
      if (action.indexOf("b") >= 0) {
        U.y = basePoint.y + newRect.height;
      } else {
        if (action.indexOf("t") >= 0) {
          U.y = basePoint.y - newRect.height;
        } else {
          U.y = basePoint.y;
        }
      }
    } else {
      if (action === "r" || action === "b") {
        U.x = basePoint.x + newRect.width;
        U.y = basePoint.y + newRect.height;
      } else if (action === "l" || action === "t") {
        U.x = basePoint.x - newRect.width;
        U.y = basePoint.y - newRect.height;
      }
    }

    //获取对角距离
    let K = dsf.math.getRotatedPiont(U.x, U.y, basePoint.x, basePoint.y, oldRect.angle)
    //获取旋转中心点
    let M = {
      x: 0.5 * (basePoint.x + K.x),
      y: 0.5 * (basePoint.y + K.y)
    };

    let S = dsf.math.getRotatedPiont(basePoint.x, basePoint.y, M.x, M.y, -oldRect.angle);

    if (action.length > 1) {
      if (action.indexOf("r") >= 0) {
        newRect.left = S.x;
      } else {
        if (action.indexOf("l") >= 0) {
          newRect.left = S.x - newRect.width;
        } else {
          newRect.left = S.x - newRect.width / 2;
        }
      }
      if (action.indexOf("b") >= 0) {
        newRect.top = S.y
      } else {
        if (action.indexOf("t") >= 0) {
          newRect.top = S.y - newRect.height;
        } else {
          newRect.top = S.y - newRect.height / 2;
        }
      }
    } else {
      if (action === "r" || action === "b") {
        newRect.left = S.x;
        newRect.top = S.y;
      } else if (action === "l" || action === "t") {
        newRect.left = S.x - newRect.width;
        newRect.top = S.y - newRect.height;
      }
    }

    __parseIntSize(newRect);

    return newRect;
  }

  /**此函数用于求出Rect旋转之后的 angle
   *center 旋转中心点
   * mousePosition 当前鼠标在 #panel_outer当中的坐标
   * */
  function rotateRect(center, mousePosition) {
    let d = dsf.math.getRadina(center, mousePosition);
    let angle = 360 - 270 + (dsf.math.r2a(d));
    angle = Math.ceil(angle) % 360;
    return angle
  }

  //获取seat与选框的宽高比
  function getRatioBySeat2SelectBox(seats, selectBox) {
    const ratio = {};
    _.forEach(seats, item => {
      let obj = _.assign({}, item);
      ratio[item.id] = {
        left: (obj.left - selectBox.left) / selectBox.width,
        top: (obj.top - selectBox.top) / selectBox.height,
        width: obj.width / selectBox.width,
        height: obj.height / selectBox.height
      }
    })
    return ratio
  }

  //在选框大小变化之后获取座位新的size
  function getNewSize(seat, oldSelectBox, newSelectBox, action, ratio) {
    const _seat = _.assign({}, seat);
    let ratioSize = ratio[_seat.id];
    _seat.left = newSelectBox.left + newSelectBox.width * ratioSize.left;
    _seat.top = newSelectBox.top + newSelectBox.height * ratioSize.top;

    //如果拖动的是4个端点
    if (action.length === 2) {
      let sc = newSelectBox.width / oldSelectBox.width;
      let sc1 = newSelectBox.height / oldSelectBox.height;
      sc = 0.5 * (sc + sc1);
      _seat.width = sc * seat.width;
      _seat.height = sc * seat.height;
    } else if (action.length === 1) {
      _seat.width = newSelectBox.width * ratioSize.width;
      _seat.height = newSelectBox.height * ratioSize.height;
    }

    __roundSize(_seat);
    return _seat;
  }

  //获取rect的中心锚点
  function getRectCenter(rect) {
    return {
      x: rect.left + rect.width / 2,
      y: rect.top + rect.height / 2
    }
  }

  //获取rect 当中4个点中最小的x,y 和最大的x,y
  function getPointRange(rectPoints) {
    let min_x, max_x, min_y, max_y;
    _.forIn(rectPoints, val => {
      //第一次取值
      min_x = min_x || val.x;
      max_x = max_x || val.x;
      min_y = min_y || val.y;
      max_y = max_y || val.y;

      min_x = min_x <= val.x ? min_x : val.x;
      max_x = max_x >= val.x ? max_x : val.x;
      min_y = min_y <= val.y ? min_y : val.y;
      max_y = max_y >= val.y ? max_y : val.y;
    })

    return {min_x: min_x, min_y: min_y, max_x: max_x, max_y: max_y}
  }

  //取整
  function __parseIntSize(rect) {
    rect.top = parseInt(rect.top);
    rect.left = parseInt(rect.left);
    rect.width = parseInt(rect.width);
    rect.height = parseInt(rect.height);
  }

  function __roundSize(rect) {
    rect.top = Math.round(rect.top);
    rect.left = Math.round(rect.left);
    rect.width = Math.round(rect.width);
    rect.height = Math.round(rect.height);
  }

  //获取选框的位置
  function getSelectPosition(selectedItems) {
    let min_x, max_x, min_y, max_y, rectPoints;
    _.forEach(selectedItems, item => {
      rectPoints = getRectPoint(item);
      _.forIn(rectPoints, val => {
        //第一次取值
        min_x = min_x || val.x;
        max_x = max_x || val.x;
        min_y = min_y || val.y;
        max_y = max_y || val.y;

        min_x = min_x <= val.x ? min_x : val.x;
        max_x = max_x >= val.x ? max_x : val.x;
        min_y = min_y <= val.y ? min_y : val.y;
        max_y = max_y >= val.y ? max_y : val.y;
      })
    });
    return {
      left: getExtNum(min_x),
      top: getExtNum(min_y),
      width: getExtNum(max_x - min_x),
      height: getExtNum(max_y - min_y),
      angle: 0,
      visible: true
    }
  }

  //小于等于0取floor, 大于0取ceil
  function getExtNum(num) {
    return num < 0 ? Math.floor(num) : Math.ceil(num)
  }

  //重置selectPosition
  function restSelectPostion() {
    this.selectPosition = {
      top: 0,
      left: 0,
      width: 0,
      height: 0,
      angle: 0,
      visible: false
    }
  }

  //获取除了activeSeat之外，其它所有seats的各个点位置
  function getOtherSeatPoints() {
    const otherSeat = _.filter(this.items, seat => seat.id !== this.activeId);
    return otherSeat.map(seat => getRectPoint(seat));
  }

  //计算对齐线的位置
  function computeAlignLine(xy, otherXy) {
    let obj = {}, times = 0;
    for (let i = 0; i < otherXy.length; i++) {
      if (otherXy[i].left === xy.left) {
        obj.left = xy.left;
        times += 1;
      } else if (otherXy[i].top === xy.top) {
        obj.top = xy.top;
        times += 1;
      }

      if (times === 2) break;
    }
    return (obj.left || obj.top) ? obj : null
  }

  //将points的x, y坐标提取出来
  function getPointsXy(points) {
    let res = [], obj = {};
    if (_.isArray(points)) {
      _.forEach(points, item => {
        obj = {
          left: Math.round(item.lt.x),
          top: Math.round(item.lt.y)
        }
        res.push(obj);
      })
      return _.uniqWith(res, _.isEqual);
    } else if (_.isObject(points)) {
      return {
        left: Math.round(points.lt.x),
        top: Math.round(points.lt.y)
      }
    }
  }

  //获取group 的最小size
  function getGroupMinSize(group) {
    const member = group.member[0];
    return [group.cols * member.width, group.rows * member.height]
  }
</script>