<template>
  <div class="ds-range-select">
    <el-select v-model="startValue" placeholder="请选择" @change="valueChange">
      <el-option v-for="(option, $index) in start" :key="`start-${$index}`" :label="option.text" :value="option.value"></el-option>
    </el-select>
    <span class="seperate">至</span>
    <el-select v-model="endValue" placeholder="请选择" @change="valueChange">
      <el-option v-for="(option, $index) in end" :key="`start-${$index}`" :label="option.text" :value="option.value"></el-option>
    </el-select>
  </div>
</template>
<script>
export default {
  props: {
    value: {
      type: Array,
      default: () => [],
    },
    dictionary: {
      type: Array,
      default: () => [],
    }
  },
  data() {
    return {
      startValue: '',
      endValue: '',
    }
  },
  computed: {
    start() {
      return this.dictionary;
    },
    end() {
      const index = this.dictionary.findIndex(it => it.value === this.startValue);
      return this.dictionary.slice(index);
    }
  },
  watch: {
    value() {
      this.initValue();
    }
  },
  created() {
    this.initValue();
    this.valueChange();
  },
  methods: {
    initValue() {
      this.startValue = this.value.length ? this.value[0] : this.dictionary[0].value;
      this.endValue = this.value.length ? this.value[1] : this.dictionary[this.dictionary.length - 1].value;
    },
    valueChange() {
      if (!this.end.some(it => it.value === this.endValue)) {
        this.endValue = this.end[0].value;
        return;
      }
      const value = [this.startValue, this.endValue];
      this.$emit('input', value);
      this.$emit('change', value);
    }
  }
}
</script>