<template>
  <div>
    <div class="ds-control ds-form-item ds-radio-box ds-has-label">
      <label class="ds-form-label">范围：</label>
      <div class="ds-form-block">
        <el-radio-group v-model="scope">
          <el-radio label="0">无限制</el-radio>
          <el-radio label="1">我的</el-radio>
          <el-radio label="2">本机构的</el-radio>
        </el-radio-group>
      </div>
    </div>
    <div v-show="scope=='2'" class="ds-control ds-form-item ds-radio-box ds-has-label">
      <label class="ds-form-label">本机构范围：</label>
      <div class="ds-form-block">
        <el-checkbox-group v-model="unit">
          <el-checkbox label="1">上级机构</el-checkbox>
          <el-checkbox label="2">下级机构</el-checkbox>
        </el-checkbox-group>
      </div>
    </div>
  </div>
</template>
<script>
import propertyDialogSetting from '_platform/dsf/mixins/propertyDialogSetting';
export default {
  name: 'BrowseScopeEdit',
  mixins: [propertyDialogSetting],
  data() {
    return {
      scope: 2,
      unit: []
    };
  },
  created() {
    this.scope = this.currentData.scope;
    this.unit = this.currentData.unit;
  },
  methods: {
    yes() {
      return {
        scope: this.scope,
        unit: this.scope != '2' ? [] : this.unit
      };
    }
  }
};
</script>
