<template>
  <div>
    <SelectTree
      :tree-data="formData"
      :select.sync="selectForm"
      :checked="checked"
      :show-search="true"
    ></SelectTree>
  </div>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import SelectTree from "./selectTree";

export default {
  name: "ChooseForm",
  components: {
    SelectTree,
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      formData: [],
      selectForm: null,
      checked: [],
    };
  },
  created() {
    this.initFlow();
  },
  mounted() {},
  methods: {
    yes() {
      return this.selectForm;
    },
    validate() {
      if (!this.selectForm) {
        dsf.layer.message("请选择表单", false);
        return false;
      }
      return true;
    },
    // 获取表单
    initFlow() {
      this.$http.get("oua/getPage", {}, $$webRoot).then((response) => {
        if (response.data.success) {
          const data = response.data.data;
          const arr = [];
          data.forEach((item) => {
            arr.push(this.formatData(item, "0"));
          });
          // const data = this.formatData(response.data.data, "0");
          this.formData = arr;

          // if (this.currentData && this.currentData.dsfa_rm_id) {
          //   this.checked = [this.currentData.dsfa_rm_id];
          //   this.selectFlow = this.currentData;
          // }
        }
      });
    },
    formatData(data, disabledVaule) {
      data.label = data.name;
      data.id = data.dsfa_rm_id;
      if (disabledVaule) {
        data.disabled = data.type_value === disabledVaule ? true : false;
      }
      if (data.children) {
        for (var i = 0; i < data.children.length; i++) {
          this.formatData(data.children[i], disabledVaule);
        }
      }
      return data;
    },
  },
};
</script>
<style lang="scss" scoped>
.dsf-tree-search {
  /deep/ .el-input__suffix {
    cursor: pointer;
  }
}
</style>
