<template>
  <div class="content">
    <div class="ds-tree-box fl-box">
      <div class="dsf-tree-search">
        <el-input v-model="searchKey" placeholder="输入名称">
          <i slot="suffix" class="iconfont el-input__icon icon-Magnifier"></i>
        </el-input>
        <el-button class="ds-button small chooseForm" @click="chooseFlow()">
          选择流程
        </el-button>
      </div>
      <SelectTree
        :tree-data="flowData"
        :select.sync="selectFlow"
        :checked="checked"
        :show-search="false"
        :form-search-key="searchKey"
      ></SelectTree>
    </div>
    <div class="fr-box">
      <div class="top">
        <div class="inline">
          <label class="form-label">表单名称：</label>
          <div class="input-inline" :title="curNode && curNode.pageTitle">
            {{ (curNode && curNode.pageTitle) || "暂无" }}
          </div>
        </div>
        <el-button class="ds-button small chooseForm" @click="chooseForm()">
          选择表单
        </el-button>
        <el-button class="ds-button small applyTo" @click="applyTo()">
          应用于
        </el-button>
      </div>
      <el-tabs v-model="selectTab" class="tabs">
        <el-tab-pane label="元素控制" name="0" class="pa">
          <SelectTable :data="(curNode && curNode.ctrls) || []" :columns="eleColumns"></SelectTable>
        </el-tab-pane>
        <el-tab-pane label="操作控制" name="1">
          <SelectTable :data="(curNode && curNode.btns) || []" :columns="btnsColumns"></SelectTable>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import SelectTree from "./selectTree";
import SelectTable from "./selectTable";

export default {
  name: "LinkSet",
  components: {
    SelectTree,
    SelectTable,
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      searchKey: "",
      flowData: [],
      selectFlow: null,
      checked: [],
      sID: "",
      curNode: null,
      selectTab: 0,
      tempFlow: null,
      data: [],
      default: null,
      eleColumns: [
        {
          prop: "labelCaption",
          label: "控件名称",
        },
        {
          prop: "readOnly",
          label: "只读",
          width: 120,
          isSelect: true,
          readOnlyAll: false,
          isIndeterminate: false,
        },
        {
          prop: "hidden",
          label: "隐藏",
          width: 120,
          isSelect: true,
          hiddenAll: false,
          isIndeterminate: false,
        },
      ],
      btnsColumns: [
        {
          prop: "text",
          label: "按钮名称",
        },
        {
          prop: "readOnly",
          label: "可用",
          width: 120,
          isSelect: true,
          readOnlyAll: false,
          isIndeterminate: false,
        },
      ],
    };
  },
  watch: {
    selectFlow(val) {
      const readChecked = val.ctrls.filter((item) => item.readOnly);
      const hiddenChecked = val.ctrls.filter((item) => item.hidden);
      const btnReadChecked = val.btns.filter((btn) => btn.readOnly);
      this.eleColumns[1].readOnlyAll = readChecked.length === val.ctrls.length;
      this.eleColumns[2].hiddenAll = hiddenChecked.length === val.ctrls.length;
      this.btnsColumns[1].readOnlyAll = btnReadChecked.length === val.btns.length;
      this.curNode = val;
    },
  },
  created() {
    this.sID = this.owner.chooseFlow && this.owner.chooseFlow.dsfa_rm_id;
    this.data = this.currentData;
    this.initFlow();
  },
  mounted() {},
  methods: {
    yes() {
      if (this.tempFlow) {
        this.owner.chooseFlow = this.tempFlow;
      }
      const data = dsf.mix(true, {}, this.flowData[0]);
      data.children.forEach((node) => {
        if (node.ctrls && node.ctrls.length > 0) {
          node.ctrls.forEach((item) => {
            this.hiddenToVisible(item);
          });
        }
      });
      data.NodeList = data.children;
      delete data.children;
      return this.$http.post("wfk/saveExWF", { sData: JSON.stringify(data) }).then((response) => {
        if (response.data.state == 20000) {
          return [data];
        } else {
          dsf.layer.message(response.message || "环节设置异常", false);
        }
      });
    },
    validate() {
      return true;
    },
    hiddenToVisible(item) {
      item.visible = !item.hidden;
      delete item.hidden;
    },
    visibleToHidden(item) {
      item.hidden = !item.visible;
    },
    // 获取form树
    initFlow() {
      if (this.data && this.data.length > 0) {
        const data = dsf.mix(true, [], this.data);
        data[0].NodeList.forEach((node) => {
          if (node.ctrls && node.ctrls.length > 0) {
            node.ctrls.forEach((item) => {
              this.visibleToHidden(item);
            });
          }
          if (node.btns && node.btns.length > 0) {
            node.btns.forEach((btn) => {
              btn.readOnly = btn.readOnly ? true : false;
            });
          }
        });
        data[0].children = data[0].NodeList;
        this.checked = [data[0].children[0].id];
        this.curNode = data[0].children[0];
        this.flowData = data;
        return;
      }
      this.$http.get("wfk/getWFNodeList", { sID: this.sID }).then((response) => {
        if (response.data.success) {
          this.initDefault();
          const data = this.formatData(response.data.data, undefined);
          if (data && data.children) {
            this.checked = [data.children[0].id];
            this.curNode = data.children[0];
          }
          this.flowData = [data];

          // if (this.currentData && this.currentData.dsfa_rm_id) {
          //   this.checked = [this.currentData.dsfa_rm_id];
          //   this.selectFlow = this.currentData;
          // }
        }
      });
    },
    initDefault() {
      let ctrls = [];
      let btns = [];
      let pageTitle;
      let isAllSave = true;
      dsf.recursionComponentProps(this.owner.$$getComponent().$page.$props, (data) => {
        if (data.isFormControl && data.ctrlName !== "DsfFlowInfo" && data.ctrlName !== "DsfDisposeIdea") {
          if (!data.metadata_fullcode) {
            isAllSave = false;
            dsf.layer.message("请先保存表单后再操作", false);
            return;
          }
          ctrls.push({
            id: data.id,
            label: data.label,
            labelCaption: `${data.label}(${data.caption})`,
            caption: data.caption,
            readOnly: false,
            hidden: false,
            visible: true,
            metadataCode: data.metadata_fullcode,
          });
        }
        if (data.caption === "page" && data.ctrlName === "DsfPage") {
          pageTitle = data.title;
        }
        if (data.ctrlName && data.ctrlName === "DsfButtonBar") {
          data.buttons.forEach((btn) => {
            btn.readOnly = false;
            btns.push(btn);
          });
        }
      });
      this.default = !isAllSave
        ? null
        : {
            pageTitle: pageTitle,
            ctrls: ctrls,
            btns: btns,
          };
    },
    formatData(data, disabledVaule, isChild) {
      data.label = data.WFName || data.NodeName;
      data.id = data.WFID || data.NodeID;
      if (isChild) {
        const defaultData = JSON.parse(JSON.stringify(this.default));
        data.pageTitle = (defaultData && defaultData.pageTitle) || "";
        data.ctrls = (defaultData && defaultData.ctrls) || [];
        data.btns = (defaultData && defaultData.btns) || [];
        const B = dsf.url.queryString("B");
        const M = dsf.url.queryString("M");
        const pname = dsf.url.queryString("pname");
        data.NameSpace = `${B}.${M}.${pname}`;
      }
      if (disabledVaule === undefined) {
        data.disabled = data.NodeType === disabledVaule ? true : false;
      }
      data.children = data.NodeList;
      if (data.children) {
        for (var i = 0; i < data.children.length; i++) {
          this.formatData(data.children[i], disabledVaule, true);
        }
      }
      return data;
    },
    chooseForm() {
      let params = {
        type: "form",
      };
      dsf.layer.pc.openDialog({
        title: "选择表单",
        width: "400px",
        height: "400px",
        overflow: "hidden",
        content: "ChooseForm",
        params: params,
        btns: [
          {
            text: "确定",
            handler: (component) => {
              if (component.validate()) {
                const form = component.yes();
                let path = form.ID;
                path = path.replace("." + form.code, "");
                const url = dsf.url.getWebPath(`/pc/views/${path}/${form.code}.layout.html`);
                this.$http.get(url, null, { responseType: "text" }).then((res) => {
                  if (res.data) {
                    let dom = $("<div>" + res.data + "</div>");
                    let layoutStr = dom.children("script[id='layout']").html() || "";
                    let layoutFn = new Function(
                      ["var layout=" + (layoutStr || "null") + ";", "return layout;"].join("\n")
                    );
                    const layout = layoutFn();
                    this.curNode.pageTitle = layout.title;
                    this.curNode.ctrls = this.getFormctrls(layout); // 列表项
                    this.curNode.btns = this.getBtns(layout); // 按钮
                    this.curNode.NameSpace = form.ID;
                  }
                });

                return true;
              }
              return false;
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
    applyTo() {
      let params = { data: this.flowData };
      dsf.layer.pc.openDialog({
        title: "应用于",
        width: "400px",
        height: "400px",
        overflow: "hidden",
        content: "ApplyTo",
        params: params,
        btns: [
          {
            text: "确定",
            handler: (component) => {
              const nodes = component.yes();
              nodes.forEach((node) => {
                if (node.NodeID) {
                  this.flowData.forEach((data) => {
                    data.children.forEach((item) => {
                      if (node.id === item.id) {
                        item.pageTitle = this.curNode.pageTitle;
                        item.ctrls = dsf.mix(true, [], this.curNode.ctrls);
                        item.btns = dsf.mix(true, [], this.curNode.btns);
                        item.NameSpace = this.curNode.NameSpace;
                      }
                    });
                  });
                }
              });
              return true;
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
    getFormctrls(data) {
      let arr = [];
      if (data.isFormControl) {
        arr.push({
          id: data.id,
          label: data.label,
          labelCaption: `${data.label}(${data.caption})`,
          caption: data.caption,
          readOnly: false,
          hidden: false,
        });
      } else {
        data.slots.forEach((slot) => {
          slot.controls.forEach((control) => {
            const item = this.getFormctrls(control);
            arr.push(item);
          });
        });
      }
      return arr.flat(Infinity);
    },
    getBtns(data) {
      let arr = [];
      if (data.ctrlName && data.ctrlName === "DsfButtonBar") {
        arr.push(...data.buttons);
      } else {
        data.slots.forEach((slot) => {
          slot.controls.forEach((control) => {
            const item = this.getBtns(control);
            console.log(item);
            arr.push(item);
          });
        });
      }
      return arr.flat(Infinity);
    },
    chooseFlow() {
      let params = {
        currentData: this.tempFlow || this.owner.chooseFlow,
      };
      dsf.layer.pc.openDialog({
        title: "流程设置",
        width: "400px",
        height: "400px",
        overflow: "hidden",
        content: "ChooseFlow",
        params: params,
        btns: [
          {
            text: "确定",
            handler: (component) => {
              if (component.validate()) {
                const flow = component.yes();
                this.tempFlow = flow;
                this.sID = flow.dsfa_rm_id;
                this.data = [];
                this.initFlow();
                return true;
              }
              return false;
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.content {
  position: relative;
  height: 100%;
  .dsf-tree-search {
    margin-bottom: 20px;
  }
  /deep/ .el-input {
    display: inline-block;
    width: 55%;
    margin-right: 10px;
  }
}
.ds-tree-box {
  float: left;
  width: 35%;
  height: 100%;
  padding: 10px;
  box-sizing: border-box;
}
.fr-box {
  position: relative;
  width: 65%;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
  border-left: 1px solid #e6e6e6;
  float: left;
  height: 100%;
  box-sizing: border-box;
  .tabs {
    padding: 0 20px;
  }
}
.dsf-tree-search {
  /deep/ .el-input__suffix {
    cursor: pointer;
  }
}
.top {
  .inline {
    margin: 0;
    overflow: hidden;
    display: inline-block;
    vertical-align: middle;
    .form-label {
      float: left;
      display: block;
      font-weight: 400;
      line-height: 20px;
      text-align: right;
      padding: 9px 9px;
      width: 92px;
    }
    .input-inline {
      display: inline-block;
      float: none;
      line-height: 38px;
      white-space: nowrap;
      width: 176px;
      text-overflow: ellipsis;
      overflow: hidden;
    }
  }
}
</style>
