<template>
  <div class="ds-enum-editor-item">
    <div class="ds-enum-editor-item-left" :class="{ fill: hideRight }">
      <dsf-tree-select
        :tree-data="treeData"
        :node-key="idKey"
        :node-name="nameKey"
        :loading="treeLoading"
        :default-checked-key="defaultCheckedKey"
        search-control
        default-expand-all
        @choose-node="chooseNode"
        @choose-key="radioId = $event"
      />
    </div>
    <div v-if="!hideRight" class="ds-enum-editor-item-main">
      <el-table
        ref="singleTable"
        border
        stripe
        max-height="378px"
        style="width: 100%"
        row-key="value"
        default-expand-all
        highlight-current-row
        :data="tableList"
        :empty-text="loading ? '数据加载中...' : emptyText"
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
        :row-class-name="tableRowClassName"
        @current-change="handleCurrentChange"
      >
        <el-table-column type="index" :index="startIndex + 1" label="序号" width="80" />
        <el-table-column prop="text" label="文本" width="224" />
        <el-table-column prop="value" label="值" />
      </el-table>
      <div class="ds-enum-editor-item-main-pagination">
        <el-pagination
          :current-page="pageNum"
          :page-size="pageSize"
          :pager-count="5"
          layout="total, prev, pager, next, jumper"
          :total="total"
          background
          small
          @current-change="pageNum = $event"
        />
      </div>
    </div>
  </div>
</template>

<script>
import DsfTreeSelect from './treeSelect';

export default {
  name: 'EnumEditorItem',
  components: {
    DsfTreeSelect
  },
  props: {
    value: {
      type: Object,
      required: true
    },
    emptyText: {
      type: String,
      default: '请选择'
    },
    treeLoading: {
      type: Boolean,
      default: false
    },
    treeData: {
      type: Array,
      default() {
        return [];
      }
    },
    tablePost: {
      type: Function,
      default: () => {}
    },
    type: {
      type: String,
      required: true
    },
    hideRight: {
      type: Boolean,
      default: false
    },
    nameKey: {
      type: String,
      default: 'label',
      desc: '树节点已哪个字段作为名称'
    },
    idKey: {
      type: String,
      default: 'ID'
    },
    defaultCheckedKey: {
      type: [String, Number],
      default: ''
    }
  },
  data() {
    return {
      loading: false,
      radioId: null,
      pageSize: 100,
      pageNum: 1,
      metaTree: {},
      currentRowKey: '',
      val: this.value.code ? this.value : { type: this.type, code: null, attach: [] }
    };
  },
  computed: {
    total() {
      let v = this.value;
      if (this.metaTree.attach) {
        v = this.metaTree;
      }
      return v.attach.length;
    },
    startIndex() {
      return this.pageNum * this.pageSize - this.pageSize;
    },
    tableList() {
      let v = this.value;
      if (this.metaTree.attach) {
        v = this.metaTree;
      }
      return v.attach.slice(this.startIndex, this.startIndex + this.pageSize);
    }
  },
  watch: {
    radioId(to, from) {
      if (this.type === 'meta' || this.type === 'ds') {
        this.val['chooseKey'] = to;
      } else {
        const newData = {
          type: this.type,
          code: this.radioId,
          attach: []
        };
        this.$emit('input', newData);
        this.$emit('change', newData);
        this.pageNum = 1;
        if (to && to !== from) {
          this.loading = true;
          this.tablePost(to, data => {
            this.updateValue(data);
            this.loading = false;
          });
        }
      }
    }
  },
  created() {
    if (this.value) {
      let chooseKey = this.value.chooseKey || this.value.code;
      if (chooseKey) {
        this.radioId = chooseKey;
        this.loading = true;
        this.tablePost(chooseKey, data => {
          this.updateValue(data);
          this.loading = false;
        });
      }
    }
  },
  methods: {
    tableRowClassName ({row}) {
      if (this.currentRowKey === row['value']) {
        return 'current-row';
      }
      return '';
    },
    chooseNode(data) {
      if (this.type === 'meta') {
        // 树导航里面的元数据设置
        let items = [];
        for (let k in data.metadata) {
          let obj = $.extend({}, data.metadata[k]);
          obj['text'] = obj.name;
          obj['value'] = obj.id;
          if (obj.valueAttributes && obj.valueAttributes.length > 1) {
            obj['children'] = obj.valueAttributes.map(v => {
              v.text = v.name;
              v.value = obj.id + '_' + v.code;
              v.at = obj.at;
              return v;
            });
          }
          items.push(obj);
        }
        items = items.sort((v1, v2) => {
          let order1 = v1.order ? parseInt(v1.order) : 0;
          let order2 = v2.order ? parseInt(v2.order) : 0;
          if (order1 < order2) {
            return -1;
          } else {
            return 1;
          }
        });
        this.updateValue(items);
      } else if (this.type === 'ds') {
        // 树导航里面的数据源选择
        let v = {
          type: this.type,
          code: data.dsfa_rm_id,
          attach: [],
          chooseKey: data[this.idKey]
        };
        this.$emit('input', v);
        this.$emit('change', v);
      }
    },
    updateValue(data) {
      const newData = {
        type: this.type,
        code: this.radioId,
        attach: data
      };
      if (this.type === 'meta') {
        this.metaTree = newData;
      }
      this.$emit('input', newData);
      this.$emit('change', newData);
    },
    handleCurrentChange(tr) {
      // 元数据选择
      if (tr && tr.children) {
        this.$refs.singleTable.setCurrentRow();
        this.$refs.singleTable.toggleRowExpansion(tr);
        this.currentRowKey = '';
        this.val.code = null;
      } else if (tr) {
        this.currentRowKey = tr['value'];
        this.val.code = tr;
      }
    },
    getResult() {
      this.$emit('input', this.val);
      this.$emit('change', this.val);
    },
    initMetaTable() {
      if (this.radioId && this.type === 'meta') {
        // {"unit":{},"code":"text","defaultValue":null,"encrypt":"false","name":"文本","length":"50","type":{"text":"文本","value":"string"},"text":"文本","value":"dsfa.concern.ywlx_text","at":"dsfa.concern"}
        this.$refs.singleTable.setCurrentRow(this.value.code);
      }
    }
  }
};
</script>