<template>
  <div v-if="showTool" class="right-tool-box">
    <div class="box-item" @click="showGroup(0)">
      <el-popover
        :value="groupVisible[0]"
        placement="left-start"
        width="340"
        trigger="manual"
        popper-class="ds-seat-design-popover"
      >
        <div class="header">
          度量
          <i class="el-icon-arrow-right" @click="closeGroup(0)"></i>
        </div>
        <div class="inner">
          <div class="title">
            <label class="label-box">当前对象：</label>
            <span v-text="getInfoTypeName()"></span>
          </div>
          <div class="inner-item">
            <label class="label-box">X</label>
            <el-input-number
              v-model="info.left"
              size="mini"
              :controls="false"
            ></el-input-number>
            <label style="margin-left: 5px" class="label-box">Y</label>
            <el-input-number
              v-model="info.top"
              size="mini"
              :controls="false"
            ></el-input-number>
          </div>
          <div class="inner-item">
            <label class="label-box">宽</label>
            <el-input-number
              v-model="info.width"
              size="mini"
              :min="0"
              :controls="false"
            ></el-input-number>
            <label style="margin-left: 5px" class="label-box">高</label>
            <el-input-number
              v-model="info.height"
              size="mini"
              :min="0"
              :controls="false"
            ></el-input-number>
          </div>
          <div class="inner-item">
            <label class="label-box">角度</label>
            <el-input-number
              v-model="info.angle"
              size="mini"
              :min="0"
              :max="360"
              :controls="false"
            ></el-input-number>
          </div>
          <template v-if="info.type === 'group'">
            <div class="inner-item">
              <label class="label-box">成员宽</label>
              <el-input-number
                v-model="memberWidth"
                size="mini"
                :controls="false"
              ></el-input-number>
              <label class="label-box">成员高</label>
              <el-input-number
                v-model="memberHeight"
                size="mini"
                :controls="false"
              ></el-input-number>
            </div>
            <div class="inner-item">
              <label class="label-box">行间距</label>
              <el-input-number
                :value="memberSpaceTop"
                size="mini"
                :controls="false"
                @change="printHang"
              ></el-input-number>
              <label class="label-box">列间距</label>
              <el-input-number
                v-model="memberSpaceLeft"
                size="mini"
                :controls="false"
              ></el-input-number>
            </div>
          </template>
        </div>
        <i slot="reference" class="iconfont icon-resize-"></i>
      </el-popover>
    </div>
    <div class="box-item" @click="showGroup(1)">
      <el-popover
        :value="groupVisible[1]"
        placement="left"
        width="340"
        trigger="manual"
        popper-class="ds-seat-design-popover"
      >
        <div class="header">
          属性
          <i class="el-icon-arrow-right" @click="closeGroup(1)"></i>
        </div>
        <div class="inner">
          <div v-if="info.type === 'bed' || info.type === 'bedGroup' " class="inner-item">
            <label class="label-box">宿舍</label>
            <el-input v-model="info.dormName" size="mini"></el-input>
            <label style="margin-left: 5px" class="label-box">楼层</label>
            <el-input v-model="info.floorName" size="mini"></el-input>
          </div>
          <div v-if="info.type === 'bed' || info.type === 'bedGroup' " class="inner-item">
            <label class="label-box">类型</label>
            <el-select v-model="info.dormType" size="mini">
              <el-option v-for="(sex, s) in sexList" :key="s" :label="sex.txt" :value="sex.val"></el-option>
            </el-select>
            <label style="margin-left: 5px" class="label-box">备注</label>
            <el-input v-model="info.remark" size="mini"></el-input>
          </div>
          <div v-if="info.type === 'seat' || info.type === 'seatGroup' " class="inner-item">
            <label class="label-box">房间号</label>
            <el-input v-model="info.roomNum" size="mini"></el-input>
            <label style="margin-left: 5px" class="label-box">备注</label>
            <el-input v-model="info.remark" size="mini"></el-input>
          </div>
          <div class="inner-item">
            <label class="label-box" style="width: 80px;margin-right: 10px">显示文本</label>
            <el-radio-group v-model="info.showText">
              <el-radio :label="1">是</el-radio>
              <el-radio :label="2">否</el-radio>
            </el-radio-group>
          </div>

          <div v-if="info.memberList && info.memberList.length>0" class="inner-item">
            <div v-for="(item, it) in info.memberList" :key="it">
              <div class="title">1行1列:</div>
              <label class="label-box">序号</label>
              <el-input-number size="mini" :min="0" :controls="false"></el-input-number>
              <label class="label-box">文本</label>
              <el-input-number size="mini" :min="0" :controls="false"></el-input-number>
            </div>
          </div>
          <div class="inner-item">
            <label class="label-box">序号</label>
            <el-input-number
              v-model="info.seatNum"
              size="mini"

              :min="0"
              :controls="false"
            ></el-input-number>
            <label class="label-box">文本</label>
            <el-input v-model="info.seatTxt" size="mini"></el-input>
          </div>
        </div>
        <i slot="reference" class="iconfont icon-xingzhuang-wenzi"></i>
      </el-popover>
    </div>
    <div class="box-item" @click="showGroup(2)">
      <el-popover
        :value="groupVisible[2]"
        placement="left-start"
        width="365"
        trigger="manual"
        popper-class="ds-seat-design-popover"
      >
        <input ref="uploadInput" type="file" accept="image/*" multiple style="display: none">
        <div class="header">
          上传
          <i class="el-icon el-icon-s-tools" @click="dialogVisible = true"></i>
          <i class="el-icon-arrow-right" @click="closeGroup(2)"></i>
        </div>
        <div class="inner" style="padding-top: 15px; padding-right: 0">
          <div v-for="(state, s) in stateList" :key="state.statusValue" class="upload-box">
            <img :src="getImagePath(state.img)" @click="uploadMultiple(state)">
            <p>{{ state.statusText }}</p>
          </div>
        </div>
        <i slot="reference" class="iconfont icon-tupiantianjia"></i>
      </el-popover>
    </div>

    <el-dialog
      title="座位状态管理"
      :visible.sync="dialogVisible"
      :modal="false"
      width="35%"
    >
      <div class="dialog-box">
        <p class="title">说明</p>
        <p>statusText - 座位状态</p>
        <p>statusValue - 座位状态对应的值</p>
        <p>couldSeat - 此状态下人员能否入座（1能、2否）</p>
        <p class="title">默认状态</p>
        <p><span>正常|1|1</span><span>已排|2|2</span></p>
        <p><span>禁用|3|2</span><span>锁定|4|2</span></p>

        <p class="title">自定义座位状态</p>
        <el-input
          v-model="statusKv"
          type="textarea"
          :rows="5"
          placeholder="statusText | statusValue | couldSeat 以|分隔，一行定义一种状态"
        ></el-input>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="dialogVisible = false">取 消</el-button>
        <el-button size="small" type="primary" @click="saveStatus">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import eventBus from "_platform/dsf/designer/seat/eventBus";
  import {eventBusEvent} from "_platform/dsf/designer/seat/constant";
  import {updateGroupMemberPosition, uploadImg} from "../func"
  import {mapState} from "vuex"

  export default {
    name: "RightTools",
    props: {},
    data() {
      return {
        showTool: false,
        sexList: [
          {val: null, txt: "请选择"},
          {val: 1, txt: "男宿舍"},
          {val: 2, txt: "女宿舍"},
        ],
        defaultStateList: [
          {
            img: "",
            statusText: "正常",
            statusValue: 1,
            couldSeat: 1,
          },
          {
            img: "",
            statusText: "已排",
            statusValue: 2,
            couldSeat: 2
          },
          {
            img: "",
            statusText: "禁用",
            statusValue: 3,
            couldSeat: 2
          },
          {
            img: "",
            statusText: "锁定",
            statusValue: 4,
            couldSeat: 2
          },
        ],
        stateList: [],
        info: {},
        groupVisible: [false, false, false],
        visiblePointer: null,
        oldVisibleStatus: false,

        dialogVisible: false,
        statusKv: ""
      }
    },
    computed: {
      ...mapState("seatDesign", {
        activeId: "activeId",
      }),

      memberWidth: {
        set(val) {
          if (!this.info || !this.info.id) return;
          updateMemberPosition4Width.call(this, val);
        },
        get() {
          if (_.isArray(this.info.member)) {
            return this.info.member[0].width
          } else {
            return undefined
          }
        }
      },
      memberHeight: {
        set(val) {
          if (!this.info || !this.info.id) return;
          updateMemberPosition4Height.call(this, val);
        },
        get() {
          if (_.isArray(this.info.member)) {
            return this.info.member[0].height
          } else {
            return undefined
          }
        }
      },
      memberSpaceTop: {
        set(val) {
          if (!this.info || !this.info.id) return;
          updateGroupSize4MemberTop.call(this, val);
        },
        get() {
          if (_.isArray(this.info.member)) {
            return getMemberSpaceTop(this.info)
          } else {
            return undefined
          }
        }
      },
      memberSpaceLeft: {
        set(val) {
        },
        get() {
          if (_.isArray(this.info.member)) {
            return getMemberSpaceLeft(this.info)
          } else {
            return undefined
          }
        }
      },

    },
    watch: {
      activeId(val) {
        if (!val) {
          this.showTool = false;
          const arr = new Array(3);
          this.groupVisible = _.fill(arr, false);
        } else {
          let activeSeat;
          eventBus.emit(eventBusEvent.GET_ACTIVE_SEAT, seat => {
            activeSeat = seat
          });
          this.info = activeSeat;
          this.showTool = true;
          this.$nextTick(() => {
            this.$set(this.groupVisible, this.visiblePointer, this.oldVisibleStatus);
          })
        }
      },

      stateList: {
        handler(val) {
          this.info.stateList = val
        },
        deep: true
      },

      info: {
        handler(val) {
          this.$store.commit("seatDesign/changeSeatInfo", val);
        },
        deep: true
      }
    },
    created() {
      if (!_.isArray(this.stateList) || this.stateList.length === 0) this.stateList = this.defaultStateList;
    },
    methods: {
      printHang(a, b){
        console.log(a)
        console.log(b)
      },
      uploadMultiple(item) {
        const self = this;
        const upDOM = this.$refs.uploadInput;
        $(upDOM).click();
        $(upDOM).one("change", function () {
          if (upDOM.files.length === 0) return;

          const formData = new FormData();
          for (let i = 0; i < upDOM.files.length; i++) {
            let file = upDOM.files[i];
            formData.append("file_" + i, file);
          }
          uploadImg.call(self, formData, res => {
            if (res === "error") return false;
            if (res.length === 1) {
              item.img = res[0].relativePath;
            } else {
              self.stateList.forEach((item, index) => {
                item.img = res[index].relativePath;
              })
            }
          });
          $(upDOM).val("");
        })
      },
      closeGroup(pointer) {
        this.visiblePointer = pointer;
        this.$set(this.groupVisible, pointer, false);
        this.oldVisibleStatus = false;
      },
      showGroup(pointer) {
        this.visiblePointer = pointer;
        const arr = new Array(3);
        this.groupVisible = _.fill(arr, false);
        this.$set(this.groupVisible, pointer, true);
        this.oldVisibleStatus = true;
      },
      saveStatus() {
        let arr = this.statusKv.split("\n");
        let temp, obj, result = [];
        arr.forEach(item => {
          temp = item.split("|").map(str => {
            return str.replace(/(^\s*)|(\s*$)/g, "");    //去掉首尾的空格
          });
          if (+temp[2] !== 1 && +temp[2] !== 2) {
            dsf.layer.alert("statusText|statusValue|couldSeat  当中的couldSeat只能为1或2");
            return false
          }
          obj = {
            img: "",
            statusText: temp[0],
            statusValue: +temp[1],
            couldSeat: +temp[2]
          };
          result.push(obj);
        });
        const diffArr = _.differenceBy(this.defaultStateList, result, "statusValue");
        this.stateList = _.concat(diffArr, result);
        this.dialogVisible = false;
      },
      getImagePath(path) {
        return path ? dsf.url.getWebPath(path) : null;
      },
      getInfoTypeName() {
        switch (this.info.type) {
          case 'seat':
            return '座位'
          case 'bed':
            return '床位'
          case 'group':
            if (this.info.memberType === 'seat') return '座位组'
            if (this.info.memberType === 'bed') return '床位组'
        }
      },
    }
  }

  function updateMemberPosition4Width(width) {
    const group = _.cloneDeep(this.info);
    group.member.forEach(item => {
      item.width = width;
    })
    updateGroupMemberPosition(group);
    this.info = group;
  }

  function updateMemberPosition4Height() {
    //console.log("updateMemberPosition4Height")
  }

  function updateGroupSize4MemberTop() {
    console.log("updateGroupSize4MemberTop")
  }

  function getMemberSpaceLeft(group) {
    const member = group.member[0];
    let totalLeft = group.width - member.width * group.cols;
    return group.cols === 1 ? 0 : totalLeft / (group.cols - 1)
  }

  function getMemberSpaceTop(group) {
    const member = group.member[0];
    let totalTop = group.height - member.height * group.rows;
    return group.rows === 1 ? 0 : totalTop / (group.rows - 1)
  }
</script>