<template>
  <el-container style="height:100%">
    <el-aside width="200px">
      <el-tree :data="valueAttributes" :props="treeConfig" :highlight-current="true" :node-key="'code'" :current-node-key="currentNodeKey" @node-click="nodeClickHandler"></el-tree>
    </el-aside>
    <el-main style="overflow:auto">
      <el-form :model="model" label-width="130px">
        <el-card style="margin-bottom:10px">
          <template v-slot:header>
            必填规则
          </template>
          <el-form-item label="是否必填">
            <el-switch v-model="model.required"></el-switch>
          </el-form-item>
          <el-form-item label="错误提示">
            <el-input v-model="model.errorMsg.required"></el-input>
          </el-form-item>
        </el-card>
        <el-card style="margin-bottom:10px">
          <template v-slot:header>
            类型规则
          </template>
          <el-form-item label="是否是数字">
            <el-switch v-model="model.isNumber"></el-switch>
          </el-form-item>
          <el-form-item label="错误提示">
            <el-input v-model="model.errorMsg.isNumber"></el-input>
          </el-form-item>
        </el-card>
        <el-card style="margin-bottom:10px">
          <template v-slot:header>
            精度规则
          </template>
          <template v-if="model.isNumber">
            <el-form-item label="最小数">
              <el-input v-model="model.numberMin" @change="checkNum('numberMin')"></el-input>
            </el-form-item>
            <el-form-item label="错误提示">
              <el-input v-model="model.errorMsg.numberMin"></el-input>
            </el-form-item>
            <el-form-item label="最大数">
              <el-input v-model="model.numberMax" @change="checkNum('numberMax')"></el-input>
            </el-form-item>
            <el-form-item label="错误提示">
              <el-input v-model="model.errorMsg.numberMax"></el-input>
            </el-form-item>
            <el-form-item label="小数点后保留位数">
              <el-input v-model="model.precision" @change="checkNum('precision',true)"></el-input>
            </el-form-item>
            <el-form-item label="错误提示">
              <el-input v-model="model.errorMsg.precision"></el-input>
            </el-form-item>
          </template>
          <template v-else>
            <el-form-item label="字符串最小长度">
              <el-input v-model="model.min" :min="0" @change="checkNum('min',true)"></el-input>
            </el-form-item>
            <el-form-item label="错误提示">
              <el-input v-model="errorMsg.min"></el-input>
            </el-form-item>
            <el-form-item label="字符串最大长度">
              <el-input v-model="model.max" :min="0" @change="checkNum('max',true)"></el-input>
            </el-form-item>
            <el-form-item label="错误提示">
              <el-input v-model="model.errorMsg.max"></el-input>
            </el-form-item>
          </template>
        </el-card>
        <el-card style="margin-bottom:10px">
          <template v-slot:header>
            格式规则
          </template>
          <el-form-item label="格式">
            <el-select v-model="model.express" style="width:100%">
              <el-option v-for="(option,index) in expressOptions" :key="index" :label="option.text||'无'" :value="option.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="错误提示">
            <el-input v-model="model.errorMsg.express"></el-input>
          </el-form-item>
        </el-card>
      </el-form>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";

export default {
  name: "DesValidateRule",
  mixins: [propertyDialogSetting],
  data() {
    return {
      activeName: "base",
      valueAttributes: [],
      currentNodeKey: "",
      treeConfig: {
        label: "name"
      },
      formModel: {},
      errorMsg: {
        required: "该项必须填写",
        isNumber: "该项必须填写数字",
        min: "该项长度不可小于@[min]个字符",
        max: "该项长度不可大于@[max]个字符",
        numberMin: "该项不可小于@[min]",
        numberMax: "该项不可大于@[max]",
        express: "@[expressName]格式不正确",
        precision: "小数点后最多保留@[precision]位小数"
      },
      javascript: "",
      expressOptions: [
        { 'expressName': "", 'text': "", "value": "" },
        { 'expressName': "身份证号码", 'text': "身份证号码", "value": "isIDCard" },
        { 'expressName': "身份证号码", 'text': "身份证号码(强校验)", "value": "isIDCardFull" },
        { 'expressName': "手机号码", 'text': "手机号码", "value": "isMobile" },
        { 'expressName': "电话号码", 'text': "电话号码", "value": "isTelPhone" },
        { 'expressName': "电话/手机号码", 'text': "电话/手机号码", "value": "isMobileOrTelPhone" },
        { 'expressName': "电子邮箱", 'text': "电子邮箱", "value": "isEmail" },
        { 'expressName': "车牌号", 'text': "车牌号", "value": "isPlateNumber" }
      ],
      metaDataValidateConfig: null
    }
  },
  computed: {
    ownerCtrl() {
      return this.owner.$$getComponent();
    },
    model() {
      return this.formModel[this.currentNodeKey];
    }
  },
  created() {
    // let data = dsf.mix(true, {}, this.currentData.config);
    this.valueAttributes = dsf.mix(true, [], this.ownerCtrl.metadata.valueAttributes)
    this.currentNodeKey = this.valueAttributes[0].code;
    if (this.ownerCtrl.metadata.validate) {
      if (this.ownerCtrl.metadata.validate.config) {
        this.metaDataValidateConfig = dsf.mix(true, {}, this.ownerCtrl.metadata.validate.config);
      }
    }
    if (!this.metaDataValidateConfig) {
      this.metaDataValidateConfig = {};
    }
    _.each(this.valueAttributes, (va) => {
      let code = va.code;
      if (this.ownerCtrl.metadata) {
        this.$set(this.metaDataValidateConfig, code, this.createValideConfig(code))
        this.setFormModel(code, this.metaDataValidateConfig[code])
      }
    });
    this.javascript = this.currentData ? (this.currentData.code || "") : "";
  },
  methods: {
    yes() {
      let result = {
        config: null,
        code: null
      };
      let code = "";//this.$refs.javascriptEditor.getContent();
      if (code) {
        result.code = code;
        return result;
      }
      else {
        let rule = {};
        for (let k in this.formModel) {
          let r = this.formModel[k];
          let required = r.required;
          let isNumber = r.isNumber;
          let min = r.isNumber ? r.numberMin : r.min;
          let max = r.isNumber ? r.numberMax : r.max;
          let precision = r.isNumber ? r.precision : "";
          let express = r.express;
          if (!required && !isNumber && !min && !max && !precision && !express) {
            continue;
          }
          let curExpess = _.filter(this.expressOptions, (op) => op.value == r.express);
          r.expressName = _.map(curExpess, "expressName");
          rule[k] = r;
        }
        if (Object.keys(rule).length == 0) {
          return null;
        }
        else {
          result.config = rule;
        }
        return result;
      }

    },
    createValideConfig(key) {
      let obj = {
        required: false,
        isNumber: false,
        min: "",
        max: "",
        express: "",
        expressName: "",
        precision: "",
        errorMsg: dsf.mix({}, this.errorMsg)
      }
      if (!this.metaDataValidateConfig[key]) {
        return dsf.mix({}, obj);
      }
      else {
        let current = this.metaDataValidateConfig[key];
        dsf.mix(true, obj, current);
        return dsf.mix(current, obj);
      }
    },
    setFormModel(key, data) {
      let formModel = {};
      formModel.required = data.required;
      formModel.isNumber = data.isNumber;
      if (data.isNumber) {
        formModel.numberMin = data.min;
        formModel.numberMax = data.max;
        formModel.precision = data.precision;
      }
      else {
        formModel.min = data.min;
        formModel.max = data.max;
        formModel.precision = "";
      }
      formModel.express = data.express;
      formModel.expressName = data.expressName;
      formModel.errorMsg = data.errorMsg;
      this.$set(this.formModel, key, formModel);
    },
    tabsCliekHandler(tab) {
      this.$nextTick(() => {
        let name = tab.name;
        let key = name + "Editor";
        if (this.$refs[key]) {
          this.$refs[key].layout();
        }
      })
    },
    editorLoaded(key) {
      let name = key + "Editor";
      if (this.$refs[name]) {
        this.$refs[name].setContent(this.javascript);
      }
    },
    checkNum(key, isInt) {
      if (this.model[key]) {
        let toNumber = isInt ? parseInt : parseFloat;
        this.model[key] = toNumber(this.model[key]).toString();
        if (!dsf.isNumber(this.model[key])) {
          this.model[key] = ""
        }
      }
    },
    nodeClickHandler(data) {
      this.currentNodeKey = data.code
    }
  }
};
</script>
