<template>
  <div>
    <SelectTree :tree-data="flowData" :select.sync="selectFlow" :checked="checked" :show-search="true"></SelectTree>
  </div>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import SelectTree from "./selectTree";

export default {
  name: "ChooseFlow",
  components: {
    SelectTree,
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      flowData: [],
      selectFlow: null,
      checked: [],
    };
  },
  created() {
    this.initFlow();
  },
  mounted() {},
  methods: {
    yes() {
      if (this.owner) {
        this.owner.metadata.defaultValue = {
          wf_id: this.selectFlow.ID,
          wf_name: this.selectFlow.name,
        };
      }
      return this.selectFlow;
    },
    validate() {
      if (!this.selectFlow) {
        dsf.layer.message("请选择流程", false);
        return false;
      }
      return true;
    },
    // 获取角色树
    initFlow() {
      this.$http.get("rm/list/wf", {}).then((response) => {
        if (response.data.success) {
          const data = response.data.data[0];
          const formatData = data ? this.formatData(response.data.data[0], "0") : null;
          this.flowData = data ? [formatData] : [];

          if (this.currentData && this.currentData.dsfa_rm_id) {
            this.checked = [this.currentData.dsfa_rm_id];
            this.selectFlow = this.currentData;
          }
        }
      });
    },
    formatData(data, disabledVaule) {
      data.label = data.name;
      data.id = data.dsfa_rm_id;
      if (disabledVaule) {
        data.disabled = data.type_value === disabledVaule ? true : false;
      }
      if (data.type_value != "4") {
        data.nocheck = true;
      }
      if (data.type_value != "4" && !data.children) {
        data.iconSkin = "_special";
      }
      if (data.children) {
        for (var i = 0; i < data.children.length; i++) {
          this.formatData(data.children[i], disabledVaule);
        }
      }
      return data;
    },
  },
};
</script>
<style lang="scss" scoped></style>
