<template>
  <el-container>
    <el-header>
      <div style="text-align:right;height:auto;margin:0px -20px">
        <DsfButton @click="add">新增</DsfButton>
      </div>
    </el-header>
    <el-main style="padding:0px">
      <el-table :data="tableData" border :highlight-current-row="true">
        <el-table-column :prop="'code'" :label="'元数据代码'" :header-align="'center'" :align="'center'">
          <template v-slot:default="scope">
            <el-input v-model="scope.row.code"></el-input>
          </template>
        </el-table-column>
        
        <el-table-column :prop="'name'" :label="'描述'" :header-align="'center'" :align="'center'">
          <template v-slot:default="scope">
            <el-input v-model="scope.row.name" :disabled="!scope.row.isAttach"></el-input>
          </template>
        </el-table-column>
        <el-table-column :prop="'valueExpress'" :label="'对应字段'" :header-align="'center'" :align="'center'">
          <template v-slot:default="scope">
            <el-input v-model="scope.row.dataField"></el-input>
          </template>
        </el-table-column>
        <el-table-column :label="'操作'" width="80px" :header-align="'center'" :align="'center'">
          <template v-slot:default="scope">
            <el-link v-if="scope.row.isAttach" type="primary" @click="remove(scope.row)">删除</el-link>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "NavTreeEdit",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
      type: 0
    };
  },
  created() {
    let value = this.currentData || []
    this.tableData = value;
  },
  mounted() {

  },
  methods: {
    add() {
      this.tableData.push({
        "code": "",
        "isAttach": true,
        "alias":"",
        "name": "",
        "type": {"text":"文本","value":"string"},
        "length": "100",
        "dataField":"",
        "defaultValue": null,
        "unit": null
      })
    },
    remove(row) {
      dsf.array.remove(this.tableData, row)
    },
    yes() {
      return this.tableData;
    }
  }
}
</script>