<template>
  <el-container>
    <el-header style="padding: 10px 0px; text-align: right;">
      <DsfButton size="small" icon="zidingyi" @click="addCustomButton">新增状态</DsfButton>
      <DsfButton size="small" icon="Magnifier" type="auxiliary" @click="openIcon">查看按钮图标</DsfButton>
    </el-header>
    <el-main style="padding:0px">
      <el-table ref="table" class="ds-table" :border="true" :data="tableData">
        <el-table-column label="#" type="index" align="center"></el-table-column>
        <el-table-column label="状态值" :header-align="'center'" :align="'center'">
          <template v-slot:default="scope">
            <el-input v-model="scope.row.text"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="状态图标" :header-align="'center'" :align="'center'" :width="300">
          <template v-slot:default="scope">
            <div style="display: flex;align-items: center;">
              <i :class="['iconfont', scope.row.icon ]" :style="`color: ${scope.row.color}`"></i>
              <div style="width: 180px;margin:0 10px;">
                <el-input v-model="scope.row.icon"></el-input>
              </div>
              <el-color-picker v-model="scope.row.color"></el-color-picker>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template v-slot:default="scope">
            <DsfButton
              btn-style="icon-text"
              icon="shanchu"
              size="small"
              title="删除"
              @click="removeButton(scope)"
            ></DsfButton>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesListStatusEdit",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
    };
  },
  created() {
    if(this.currentData){
      _.forEach(this.currentData, (value, key) => {
        this.tableData.push(value)
      });
    }
  },
  methods: {
    addCustomButton() {
      let button =  dsf.mix(true, {}, {
        text: "状态值",
        icon: "icon-shezhi",
        color: "#5f5f5f",
      });
      this.tableData.push(button);
    },
    validate() {
      // 验证数据有没为空的
      if (_.findIndex(this.tableData, (o) => o.text === "") !== -1) {
        dsf.layer.message("请输入状态值", false);
        return false;
      }
      // 验证数据有没重复
      if (this.isRepeat(_.map(this.tableData, "text"))) {
        dsf.layer.message("一种状态值只能配置一次", false);
        return false;
      }
    },
    isRepeat(arr) {
      var hash = {};
      for (var i in arr) {
        if (hash[arr[i]]) return true;
        hash[arr[i]] = true;
      }
      return false;
    },
    yes() {
      let obj = {};
      _(this.tableData).forEach((v) => {
        obj[v.text] = v
      });
      return obj;
    },
    openIcon() {
      window.open(
        dsf.url.getWebPath("$/pc/iconfont/demo_index.html"),
        "icondemo"
      );
    },
    removeButton(scope) {
      this.tableData.splice(scope.$index, 1)
    },
  },
};
</script>