<template>
  <el-tabs v-model="tabsValue" @tab-click="handleClick">
    <el-tab-pane label="常用列表" name="0">
      <el-form ref="form">
        <el-form-item
          v-for="(item, index) in fileExtArr"
          :key="index"
          :label="item.itemName"
        >
          <el-checkbox-group
            v-model="fileExtArr[index].checkStatus"
            @change="handleCheckedChange"
          >
            <el-checkbox :label="item.file.value" :title="item.file.title">
              {{ item.file.title }}
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>
    </el-tab-pane>
    <el-tab-pane label="个性化配置" name="1">
      <!-- 允许上传文件类型 -->
      <el-form ref="formSel">
        <el-form-item label="允许上传文件类型">
          <el-select
            v-model="fileType"
            placeholder="请选择活动区域"
            @change="handleFileType"
          >
            <el-option value="file" label="file(所有文件)"></el-option>
            <el-option value="images" label="images（图片）"></el-option>
            <el-option value="video" label="video（视频）"></el-option>
            <el-option value="audio" label="audio（音频）"></el-option>
          </el-select>
        </el-form-item>
        <p>允许上传文件后缀</p>
        <el-form-item
          v-for="(item, index) in fileExtArr"
          :key="index"
          :label="item.itemName"
        >
          <el-checkbox-group
            v-model="fileExtArr[index].checkType"
            @change="handleCheckedChange"
          >
            <el-checkbox
              v-for="(val, id) in item.children"
              :key="`${id}_sel`"
              :label="`${val.title}&${val.value}`"
              :title="val.title"
            >
              {{ val.title }}
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <p></p>
        <el-form-item label="是否在文件筛选框中显示指定类型文件">
          <el-switch v-model="isLinkeMine" inactive-color="#d2d2d2"></el-switch>
        </el-form-item>
        <el-form-item v-if="choosenExtsStr" label="已选上传文件后缀">
          {{ choosenExtsStr }}
        </el-form-item>
        <p></p>
      </el-form>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesUploadFileAttr",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tabsValue: "0",
      allowExtObj: {},
      fileExtArr: [],
      isLinkeMine: true,
      extsSetting: {},
      fileType: "file",
      choosenExtsStr: "",
    };
  },
  created() {
    this.tabsValue = this.owner.extsSetting.tabIndex || "0";
    this.getExtObj();
  },
  methods: {
    // 获取文件后缀
    getExtObj() {
      this.$http
        .get("./static/pd_other/uploadFileExt.json")
        .done((res) => {
          this.allowExtObj = res;
          this.fileExtArr = res.file;
          _.map(this.fileExtArr, (item) => {
            if (this.owner.extsSetting.CheckedNameStr) {
              // 常用列表回显
              let check = this.owner.extsSetting.CheckedNameStr;
              item.checkStatus = check.includes(item.itemKey);
            } else {
              // 个性化回显
              let check = this.owner.extsSetting.extStr;
              //   console.log("有数据", check);
              if (check) {
                item.checkType = _.map(item.children, (value) => {
                  return check.includes(value.name)
                    ? value.title + "&" + value.value
                    : "";
                });
                this.choosenExtsStr = _.filter(
                  check.split(","),
                  (data) => data !== ""
                ).join();
                this.isLinkeMine = this.owner.extsSetting.isLinkeMine;
                this.fileType = this.owner.extsSetting.fileType;
              }
            }
            item.file = {
              title: _.map(item.children, (value) => {
                return value.title;
              }).join(),
              value: _.map(item.children, (value) => {
                return value.value;
              }).join(),
            };
          });
          //   console.log("------", this.fileExtArr);
        })
        .error((err) => {
          dsf.error(err);
        });
    },
    handleFileType(value) {
      this.fileType = value;
    },
    handleCheckedChange() {
      let CheckedNameStr = "",
        extStr = "",
        mineStr = "";
      if (this.tabsValue == "1") {
        let data = _.filter(this.fileExtArr, (list) => list.checkType.length);
        // console.log("data", data);
        _.map(data, (value) => {
          _(value.checkType).forEach((item) => {
            extStr += item.split("&")[0].substr(1) + ",";
            mineStr += item.split("&")[1] + ",";
          });
        });
        this.choosenExtsStr = _.filter(
          extStr.split(","),
          (data) => data !== ""
        ).join();
        this.extsSetting = {
          CheckedNameStr: CheckedNameStr,
          extStr: extStr,
          mineStr: mineStr,
        };
      } else {
        let data = _.filter(this.fileExtArr, (list) => list.checkStatus);
        _(data).forEach((item) => {
          CheckedNameStr += item.itemKey + ",";
          extStr += item.file.title + ",";
          mineStr += item.file.value + ",";
        });
        this.extsSetting = {
          CheckedNameStr: CheckedNameStr,
          extStr: extStr,
          mineStr: mineStr,
        };
      }
    },
    handleClick(tab) {
      this.tabsValue = tab.index;
    },
    yes() {
      this.owner.extsSetting = Object.assign({}, this.extsSetting, {
        fileType: this.fileType,
        isLinkeMine: this.isLinkeMine,
        tabIndex: this.tabsValue,
      });
      return this.owner.extsSetting;
    },
  },
};
</script>
<style lang="scss" scoped>
.el-form-item__label {
  font-size: 16px;
  font-weight: bold;
}
.el-form-item {
  margin-bottom: 4px;
}
</style>
