<template>
  <div>
    <component :is="componentId" ref="form" :owner="property" @showDialog="showDialog" />
    <el-dialog 
      title="题目关联"
      :visible.sync="dialogVisible"
      :modal="true"
      :append-to-body="true"
      :modal-append-to-body="false"
      :before-close="closeRelaDialog"
      :closed="resetRela"
      width="500px"
    >
      <el-form :model="relaForm" label-width="80px">
        <el-form-item label="当前题目:">
          <el-input style="border:none" :value="owner.questionIndex+'题'" readonly></el-input>
        </el-form-item>
        <el-form-item label="关联题目:">
          <el-select v-model="relaForm.relaIndex" @change="chooseRela">
            <el-option value="no-rela" label="无关联"></el-option>
            <el-option v-for="item in prevObjs" :key="item.questionIndex" :label="item.questionIndex+'题'" :value="item.questionIndex"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <div class="rela-option-box">
            <template v-if="relaItem">
              <p>当“关联题目”选择下面的选项：</p>
              <template v-if="relaItem.ctrlType=='dsf.qiradio'">
                <el-radio-group v-model="relaForm.relaRadio">
                  <el-radio v-for="option in relaItem.items" :key="option.value" :label="option.value">{{ option.text }}</el-radio>
                </el-radio-group>
              </template>
              <template v-else-if="relaItem.ctrlType=='dsf.qicheckbox'">
                <el-checkbox-group v-model="relaForm.relaCheckBox">
                  <el-checkbox v-for="option in relaItem.items" :key="option.value" :label="option.value">{{ option.text }}</el-checkbox>
                </el-checkbox-group>
              </template>
              <template v-else></template>
              <p>“当前题目”才出现</p>
            </template>
          </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <dsf-button @click="setRela">确 定</dsf-button>
        <dsf-button type="auxiliary" @click="closeRelaDialog">取 消</dsf-button>
      </span>
    </el-dialog>
    <el-dialog 
      title="跳题逻辑"
      :visible.sync="logicVisible"
      :modal="true"
      :append-to-body="true"
      :modal-append-to-body="false"
      :before-close="closeLogicDialog"
      :closed="resetLogic"
      width="600px"
    >
      <div class="ds-logic-option-box">
        <table v-if="owner.items&&owner.items.length>0" class="el-table el-table--border" cellspacing="0" cellpadding="0" border="0">
          <thead>
            <th></th>
            <th>选项</th>
            <th>选项值</th>
            <th>跳转到</th>
          </thead>
          <tbody>
            <tr v-for="item in owner.items" :key="item.value">
              <td><b>选择</b></td>
              <td>{{ item.text }}</td>
              <td>{{ item.value }}</td>
              <td>
                <el-select v-model="logic[item.value]" @change="chooseRela">
                  <el-option value="" label="不跳题"></el-option>
                  <el-option v-for="q in nextObjs" :key="q" :value="q" :label="q+'题'"></el-option>
                </el-select>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
      <span slot="footer" class="dialog-footer">
        <dsf-button @click="setLogic">确 定</dsf-button>
        <dsf-button type="auxiliary" @click="closeLogicDialog">取 消</dsf-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import QaForm from './qa';
import FillForm from './fill';
import QsCheckBoxForm from './qsCheckBox';
import QsHelloMsgForm from './qsHelloMsg';
import QsRadioForm from './qsRadio';
import QsRateForm from './qsRate';
import QsSliderForm from './qsSlider';

export default {
    name: 'DesQuestionConfig',
    mixins: [propertyDialogSetting],
    data(){
        return {
            dialogVisible:false,
            logicVisible:false,
            relaForm:{
              relaIndex:'no-rela',//选中关联题的index
              relaCheckBox:[],//多选题的关联选项
              relaRadio:''//单选的关联选项
            },
            question:{},//当前题数据对象
            prevObjs:[],//当前题目之前可进行关联的题目集合(多选与单选)
            relaItem:null,//当前选择关联的题目实例
            nextObjs:[],//当前题目之后所有题目index集合
            logic:{},//跳题逻辑
        }
    },
    computed: {
        componentId() {
            let target = null;
            if (!this.owner.ctrlType) {
                return target;
            }
            switch(this.owner.ctrlType.toLowerCase().replace('dsf.', '')) {
                case 'qiqa':
                    target = QaForm;
                    break;
                case 'qirate':
                    target = QsRateForm;
                    break;
                case 'qifill':
                    target = FillForm;
                    break;
                case 'qicheckbox':
                    target = QsCheckBoxForm;
                    break;
                case 'qiradio':
                    target = QsRadioForm;
                    break;
                case 'qihellomsg':
                    target = QsHelloMsgForm;
                    break;
                case 'qislider':
                    target = QsSliderForm;

            }
            return target;
        },
        property() {
            return {
                questionIndex: this.owner.questionIndex,
                questionBody: this.owner.questionBody,
                required: this.owner.required,
                readOnly: this.owner.readOnly,
                items: this.owner.items,
                visible: this.owner.visible,
                layout: this.owner.layout,
                mustCheckNum: this.owner.mustCheckNum,
                maxCheckNum: this.owner.maxCheckNum,
                isTextArea: this.owner.isTextArea,
                maxLength: this.owner.maxLength,
                isOther: this.owner.isOther,
                showText: this.owner.showText,
                starLength: this.owner.starLength,
                min: this.owner.min,
                max: this.owner.max,
                minText: this.owner.minText,
                maxText: this.owner.maxText,     
                logicRela:this.owner.logicRela,
                logic:this.owner.logic           
            };  
        }
    },
    methods: {
        validate() {
            let flag = false;
            if (!this.$refs.form.form){
                flag = true;
            } else {
                this.$refs.form.$refs.form.validate(valid => {
                    flag = valid;
                })                
            }
            return flag;
        },
        yes() {
            let data;
            if (this.$refs.form.form) {
              data = JSON.parse(JSON.stringify(this.$refs.form.form));
              if (data.items) {
                  this.owner.metadata.dataSource = {
                      type: 'static',
                      code: data.items,
                  };
              }
            } else {
              data = { questionBody: this.$refs.form.$ue.getContent() };
            }
            Object.assign(this.owner, data)
            return null;
        },
        showDialog(type) {
          if(type==='rela'){
            this.getPrevRelaQuestions();
            if(!window._.isEmpty(this.owner.logicRela)){
              let index = this.owner.logicRela.Q;
              this.relaForm.relaIndex = this.owner.logicRela.Q;
              this.relaItem=window._.find(this.prevObjs,function (item) {
                  if(item.questionIndex==index){
                      return item
                  }
              });
              this.relaForm.relaRadio = this.owner.logicRela.trig[0];
              this.relaForm.relaCheckBox = window._.clone(this.owner.logicRela.trig);
            }
            this.dialogVisible = true
          }else if(type==='logic'){
            let logic={},_this=this;
            window._.forEach(this.owner.items,function(one) {
              logic[one.value] = _.isUndefined(_this.owner.logic[one.value])?'':_this.owner.logic[one.value];
            });
            this.logic = logic;
            this.getNextQuestions();
            this.logicVisible = true
          }
        },
        // getVal(val) {
        //   let item = window._.find(this.owner.logic,function(item) {
        //     if(item['trig'][0]==val){
        //       return true
        //     }
        //   })
        //   return (item?item['Q']:"")
        // },
        closeRelaDialog() {
          this.dialogVisible = false
        },
        resetRela() {
          this.relaItem = null;
          this.prevObjs = [];
          this.relaForm.relaCheckBox = [];
          this.relaForm.relaRadio = "";
          this.relaForm.relaIndex = "no-rela";
        },
        setRela() {
          if(this.relaForm.relaIndex != 'no-rela'){
            let trig = null;
            if(this.relaItem.ctrlType=='dsf.qiradio'){
              if(this.relaForm.relaRadio==""){
                dsf.layer.message("请选择需要关联的选项", false);
              }else{
                trig = [this.relaForm.relaRadio]
              }
            }else{
              if(this.relaForm.relaCheckBox.length===0){
                dsf.layer.message("请选择需要关联的选项", false);
              }else{
                trig = window._.clone(this.relaForm.relaCheckBox)
              }
            }
            this.$set(this.owner,'logicRela',{Q:this.relaForm.relaIndex,trig:trig});
          }else{
            this.$set(this.owner,'logicRela',{});
          }
          this.dialogVisible = false;
        },
        // 选择要关联那道题
        chooseRela(v) {
            this.relaItem=window._.find(this.prevObjs,function (item) {
                if(item.questionIndex==v){
                    return item
                }
            });
            this.relaForm.relaRadio = '';
            this.relaForm.relaCheckBox = [];
        },
        getPrevRelaQuestions() {
          let ctrls = $(this.owner.$el).prevAll().find(".ds-question-item[ctrl_type='dsf.qiradio'],.ds-question-item[ctrl_type='dsf.qicheckbox']"),prevObjs=[];
          window._.forEach(ctrls,function(item) {
              prevObjs.push(item.__vue__)
          });
          this.prevObjs = prevObjs;
        },
        getNextQuestions() {
          let ctrls = $(this.owner.$el).nextAll().find(".ds-question-item:not(.ds-hello)"),nexts=[];
          window._.forEach(ctrls,function(item) {
              item.__vue__.questionIndex&&nexts.push(item.__vue__.questionIndex)
          });
          this.nextObjs = nexts;
        },
        setLogic() {
          let logic = {},_this=this;
          window._.forEach(this.logic,function(q,k) {
            if(q != ""){
              logic[k] = q;
            }
          });
          this.$set(this.owner,'logic',logic);
          this.logicVisible = false
        },
        resetLogic() {
          this.logic = {};
        },
        closeLogicDialog() {
          this.logicVisible = false
        }
    },
}
</script>