<template>
  <el-container>
    <el-row style="width: 100%">
      <el-col :span="6">
        <el-checkbox v-model="checkAll" border :indeterminate="isIndeterminate" @change="chooseAll">全选</el-checkbox>
        <ul class="check-list">
          <li v-for="(item, it) in tools" :key="item.val">
            <el-checkbox :label="item.val" :value="item.checked" :checked="false" name="type" @change="chooseItem(item, it)">{{ item.text }}</el-checkbox>
          </li>
        </ul>
      </el-col>
      <el-col :span="18">
        <div class="ue-container">
          <div id="u_template"></div>
        </div>
      </el-col>
    </el-row>
  </el-container>
</template>

<script>
  import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";

  export default {
    name: "DesUeditorToolbar",
    mixins: [propertyDialogSetting],
    data() {
      return {
        checkAll: false,
        tools: [
          {val: "fullscreen", text: "全屏"},
          {val: "source", text: "源代码"},
          {val: "undo", text: "撤销"},
          {val: "redo", text: "重做"},
          {val: "bold", text: "加粗"},
          {val: "italic", text: "斜体"},
          {val: "underline", text: "下划线"},
          {val: "fontborder", text: "字符边框"},
          {val: "strikethrough", text: "删除线"},
          {val: "superscript", text: "上标"},
          {val: "subscript", text: "下标"},
          {val: "removeformat", text: "清除格式"},
          {val: "formatmatch", text: "格式刷"},
          {val: "autotypeset", text: "自动排版"},
          {val: "blockquote", text: "引用"},
          {val: "pasteplain", text: "纯文本粘贴模式"},
          {val: "forecolor", text: "字体颜色"},
          {val: "backcolor", text: "背景色"},
          {val: "insertorderedlist", text: "有序列表"},
          {val: "insertunorderedlist", text: "无序列表"},
          {val: "selectall", text: "全选"},
          {val: "cleardoc", text: "清空文档"},
          {val: "rowspacingtop", text: "段前距"},
          {val: "rowspacingbottom", text: "段后距"},
          {val: "lineheight", text: "行间距"},
          {val: "customstyle", text: "自定义标题"},
          {val: "paragraph", text: "段落"},
          {val: "fontfamily", text: "字体"},
          {val: "fontsize", text: "字号"},
          {val: "directionalityltr", text: "从左向右输入"},
          {val: "directionalityrtl", text: "从右向左输入"},
          {val: "indent", text: "首行缩进"},
          {val: "justifyleft", text: "居左对齐"},
          {val: "justifycenter", text: "居中对齐"},
          {val: "justifyright", text: "居右对齐"},
          {val: "justifyjustify", text: "两端对齐"},
          {val: "touppercase", text: "字母大写"},
          {val: "tolowercase", text: "字母小写"},
          {val: "link", text: "超链接"},
          {val: "unlink", text: "取消链接"},
          {val: "anchor", text: "锚点"},
          {val: "imagenone", text: "图片位置默认"},
          {val: "imageleft", text: "图片左浮动"},
          {val: "imageright", text: "图片右浮动"},
          {val: "imagecenter", text: "图片居中"},
          {val: "simpleupload", text: "单图上传"},
          {val: "insertimage", text: "多图上传"},
          {val: "emotion", text: "表情"},
          {val: "scrawl", text: "涂鸦"},
          {val: "insertvideo", text: "插入视频"},
          {val: "music", text: "音乐"},
          {val: "attachment", text: "附件"},
          {val: "map", text: "百度地图"},
          {val: "gmap", text: "Google地图"},
          {val: "insertframe", text: "插入Iframe"},
          {val: "insertcode", text: "插入代码"},
          {val: "webapp", text: "百度应用"},
          {val: "pagebreak", text: "分页"},
          {val: "template", text: "模板"},
          {val: "background", text: "背景"},
          {val: "horizontal", text: "分隔线"},
          {val: "date", text: "日期"},
          {val: "time", text: "时间"},
          {val: "spechars", text: "特殊字符"},
          {val: "snapscreen", text: "截图"},
          {val: "wordimage", text: "图片转存"},
          {val: "inserttable", text: "插入表格"},
          {val: "deletetable", text: "删除表格"},
          {val: "insertparagraphbeforetable", text: "表格前插入行"},
          {val: "insertrow", text: "表格插入行"},
          {val: "deleterow", text: "表格删除行"},
          {val: "insertcol", text: "表格插入列"},
          {val: "deletecol", text: "表格删除列"},
          {val: "mergecells", text: "表格合并多个单元格"},
          {val: "mergeright", text: "表格右合并单元格"},
          {val: "mergedown", text: "表格下合并单元格"},
          {val: "splittocells", text: "表格完全拆分单元格"},
          {val: "splittorows", text: "表格拆分成行"},
          {val: "splittocols", text: "表格拆分成列"},
          {val: "charts", text: "图表"},
          {val: "print", text: "打印"},
          {val: "preview", text: "预览"},
          {val: "searchreplace", text: "查询替换"},
          {val: "drafts", text: "从草稿箱加载"},
          {val: "help", text: "帮助"}
        ],
        checkedTools: this.owner.toolBar,
        isIndeterminate: false,    //全选不确定状态
        dialogUe: null
      }
    },
    watch:{
      checkedTools(){
        renderUEditor.call(this)
      }
    },
    created() {
      const self = this;
      const copy =  self.tools.map(item => {
        item.checked = false;
        return item
      });
      for (let checked of self.checkedTools){
        let index = copy.findIndex(item => item.val === checked);
        copy[index].checked = true
      }
      self.tools = copy;
      judgeCheckAllStatus.call(self);
    },
    mounted(){
      renderUEditor.call(this);
    },
    methods:{
      chooseItem(item, index){
        const self = this;
        const current = _.assign({}, item, {checked: !item.checked});
        self.tools.splice(index, 1, current);
        refreshCheckedArr.call(self);
        judgeCheckAllStatus.call(self);
      },
      chooseAll(){
        const self = this;
        self.isIndeterminate = false;
        if (self.checkAll) {
          self.tools.forEach(item => {
            item.checked = true;
          })
        }
        else {
          self.tools.forEach(item => {
            item.checked = false;
          })
        }

        refreshCheckedArr.call(self);
      },
      yes(){
        this.dialogUe.destroy();
        return this.checkedTools
      }
    },
  }

  function refreshCheckedArr() {
    const self = this;
    const arr = [];
    for (let item of self.tools){
      if (!item.checked) continue;
      arr.push(item.val)
    }
    self.checkedTools = arr;
  }

  function judgeCheckAllStatus() {
    const self = this;
    let checkAll = !self.tools.find(item => !item.checked);
    if (checkAll) {
      self.checkAll = true;
      self.isIndeterminate = false;
      return
    }

    let checksome = self.tools.findIndex(item => item.checked);
    if (checksome !== -1) {
      self.checkAll = false;
      self.isIndeterminate = true;
      return;
    }

    self.checkAll = false;
    self.isIndeterminate = false;
  }

  function renderUEditor() {
    const self = this;
    if (self.dialogUe) self.dialogUe.destroy();
    self.dialogUe = UE.getEditor("u_template", {
      autoHeightEnabled: false,
      toolbars: [self.checkedTools]
    });
    self.dialogUe.ready(()=>{
      initDialogUEditor.call(self)
    })
  }

  function initDialogUEditor() {
    const self = this;
    $("#u_template").css("width", "100%");
    self.dialogUe.setHeight(390);
  }
</script>

<style type="text/scss" lang="scss" scoped>
  .check-list{
    margin-top: 15px;
    height: 420px;
    width: 100%;
    overflow-y: scroll;
    box-sizing: border-box;
    padding-right: 5px;
    li{
      padding-top: 5px;
      width: 100%;
    }
    li + li{
      margin-top: 5px;
      border-top: 1px solid #eeeeee;
    }
  }

  .ue-container{
    width: 98%;
    padding-left: 2%;
    box-sizing: border-box;
  }
</style>