<template>
  <div class="ds-set-params">
    <SelectTable :data="data" :columns="columns"></SelectTable>
    <p class="add-btn">
      <el-button type="danger" @click="deleteAll">
        删除所有
      </el-button>
      <el-button type="success" @click="addRow">
        新增一行
      </el-button>
    </p>
  </div>
</template>
<script>
import SelectTable from "./selectTable";
export default {
  name: "SetParams",
  components: {
    SelectTable,
  },
  props: {
    value: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      data: [],
      columns: [
        {
          prop: "Key",
          label: "Key",
          fieldType: "editInput",
        },
        {
          prop: "Value",
          label: "Value",
          fieldType: "editInput",
        },
        {
          label: "操作",
          width: "100",
          operate: true,
        },
      ],
    };
  },
  created() {
    this.init();
  },
  methods: {
    getContent() {
      const data = this.data.map((item) => {
        return {
          Key: item.Key,
          Value: item.Value,
        };
      });
      return data;
    },
    init() {
      if (this.value && this.value.length > 0) {
        this.data = this.value;
      } else {
        this.addRow();
      }
    },
    deleteRow() {},
    deleteAll() {
      this.data = [];
    },
    addRow() {
      this.data.push({
        Key: "",
        KeyEdit: false,
        Value: "",
        ValueEdit: false,
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.add-btn {
  margin-top: 10px;
  text-align: center;
}
</style>
