<template>
  <el-table ref="table" :data="data">
    <el-table-column type="selection" width="55"></el-table-column>
    <el-table-column property="caption" label="组件标识"></el-table-column>
    <el-table-column property="type" label="组件类型"></el-table-column>
    <el-table-column property="label" label="组件名称"></el-table-column>
    <!-- <el-table-column props="caption"></el-table-column> -->
  </el-table>
</template>
<script>
import propertyDialogSetting from '_platform/dsf/mixins/propertyDialogSetting';
export default {
  name: "DesComponents",
  mixins: [propertyDialogSetting],
  props: {
    designer: {
      type: Object,
      default() {
        return null
      }
    }
  },
  data() {
    return {
      show: false,
      data: []
    }
  },
  mounted() {
    let controls = [];
    let page = this.designer.$refs.viewProxy
    dsf.designer.recursionLayoutTree(page, null, null, (el) => {
      if (el.isFormControl) {
        controls.push({
          'id': el.designId,
          'caption': el.caption,
          'label': el.label,
          'type': el.$options.ctrlCaption,
          'ctrl': el
        });
      }
      if (el.isOneToMany) {
        return false;
      }
    });
    // console.log(controls);
    this.data = controls;
    this.show = true;
  },
  methods: {
    validate() {
      let errors = [];
      let rows = this.$refs.table.selection;
      _.each(rows, (el) => {
        if (!el.label) {
          errors.push("请为" + el.caption + "起名后再试")
        }
        else {
          let result = dsf.designer.validateCaption(el.ctrl)
          errors.push(...result);
        }
      })
      if (errors.length) {
        dsf.layer.pc.message(errors[0], false)
        return false;
      }
    },
    yes() {
      let rows = this.$refs.table.selection;
      let list = _.map(rows, (el) => { return el.ctrl });
      let result = []
      _.each(list, (el) => {
        let exclude = el.$options.design.layout.exclude;
        let metadata = el.$options.design.metadata.create.call(el);
        let props = dsf.mix(true, {}, el.$props);
        let obj = {
          props: props,
          metadata: metadata
        }
        _.each(exclude, (p) => {
          delete props[p];
        });
        result.push(obj);
      });
      return result;
    }
  }

}
</script>