<template>
  <div
    ref="lines"
    class="lines"
    :style="{
      height: isSub
        ? `calc(100% - ${toPx(parentNode.imgHeight + 2 * parentNode.padding)})`
        : '100%',
      top: isSub ? toPx(parentNode.imgHeight + 2 * parentNode.padding) : 0,
    }"
  ></div>
</template>
<script>
import Raphael from "raphael";
import Line_proxy from "./Line_proxy";
import Node from "./Node";
import Line from "./Line";
import BoundaryEvent from "./BoundaryEvent";
import Segmen from "./Segmen";
import {
  getObjById,
  getObjByTarget,
  getObjectByTarget,
  getGridPoint,
  toPx,
} from "./common";
import {
  _Direction,
  _LineType,
  LineDirection,
  transitionRadius,
  tools,
} from "./tools";
import { getNodeLinkNodPoints, getNodeNotLinkePoints } from "./getPoints";

export default {
  name: "Lines",
  props: {
    lines: {
      type: Array,
      default() {
        return [];
      },
    },
    nodes: {
      type: Array,
      default() {
        return [];
      },
    },
    events: {
      type: Array,
      default() {
        return [];
      },
    },
    isDrawLine: {
      type: Boolean,
      default() {
        return false;
      },
    },
    checked: {
      type: Object,
      default() {
        return {};
      },
    },
    isSub: {
      // 子流程
      type: Boolean,
      default() {
        return false;
      },
    },
    parentNode: {
      type: Object,
      default() {
        return null;
      },
    },
    config: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      paper: null,
      start: false,
      currentMouseDrawingLine: null,
      startInfo: null,
      startPoint: null,
      down_direction: null,
      currentLineInfo: null,
      centerOffset: 10,
      dragSize: 20,
      maxWidth: 0,
      maxHeight: 0,
      maxSizeOffset: 50,
      currentDragLine: {},
      isDrag: false,
      type: "drawLine",
      mp: null,
    };
  },
  watch: {
    checked(line) {
      this.lines.forEach((item) => {
        if (item === line) {
          line.selected();
        } else {
          item.unSelected();
        }
      });
    },
  },
  mounted() {
    this.paper = new Raphael(this.$refs.lines, "100%", "100%");
    this.$refs.lines.addEventListener("mousedown", (e) => {
      this.mousedown(e);
      // return false;
    });
    document.addEventListener("mousemove", (e) => {
      this.mousemove(e);
    });
    document.addEventListener("mouseup", (e) => {
      this.mouseup(e);
      this.updateIntersection();
    });
    this.$bus.$on(
      `startDraw${this.isSub ? this.parentNode.id : ""}`,
      this.startDraw
    );
    this.$bus.$on(
      `nodeMove${this.isSub ? this.parentNode.id : ""}`,
      this.nodeMove
    );
    this.$bus.$on(
      `eventMove${this.isSub ? this.parentNode.id : ""}`,
      this.eventMove
    );
    this.$bus.$on(
      `addLine${this.isSub ? this.parentNode.id : ""}`,
      this.addLine
    );
    this.$bus.$on(
      `showSubNode${this.isSub ? this.parentNode.id : ""}`,
      this.showSubNode
    );
    this.$bus.$on("hideLines", this.hideLines);
    this.$bus.$on("updateIntersection", this.updateIntersection);
    // this.$bus.$on(`globalMove${this.isSub ? this.parentNode.id : ''}`, this.globalMove);
    this.$bus.$on(
      `updateNodeName${this.isSub ? this.parentNode.id : ""}`,
      this.resetLineName
    );
  },
  methods: {
    toPx,
    startDraw(e, node) {
      if (this.isDrawLine) {
        this.$bus.$emit(
          `isDrewingLine${this.isSub ? this.parentNode.id : ""}`,
          "end"
        );
      }
      // 节点出线
      if (this.isDrawLine && this.checked instanceof Node) {
        // const node = getObjByTarget(this.nodes, e.target);
        if (node) {
          this.start = true;
          this.mouseDrawLine(e);
          this.type = "drawLine";
        }
      }
      // 边界事件出线
      // const event = getObjByTarget(this.events, e.target) || getObjByTarget(this.events, e.target.parentElement);
      const event = node;
      if (this.isDrawLine && event && this.checked instanceof BoundaryEvent) {
        this.start = true;
        this.eventDrawLine(e);
        this.type = "drawLine";
      }
    },
    mousedown(e) {
      // 点击线
      if (e.target.tagName == "path") {
        e.stopPropagation();
        var segmen = getObjectByTarget(e.target);
        if (segmen) {
          const line = segmen.getLine();
          this.$bus.$emit("update-checked", line);
          if (line) {
            if (line.readOnly) {
              return;
            }
            this.lineMouseDownHandler(e, line);
          }
        }
      }
    },
    mousemove(evt) {
      if (!this.start) {
        return;
      }
      // 画线
      if (this.type === "drawLine") {
        this.drawLine(evt);
        return false;
      }
      // 拖动线
      this.dragLine(evt);
      return false;
    },
    addLine(opts) {
      const options = Object.assign(
        {
          type: 0,
        },
        opts
      );
      const line = new Line(this.config, options);
      let fn, tn;
      if (options.from) {
        if (options.fromType === "event") {
          fn = getObjById(this.events, options.from);
        } else {
          fn = getObjById(this.nodes, options.from);
        }
      }
      if (options.to) {
        tn = getObjById(this.nodes, options.to);
      }
      if (line.path.length === 0) {
        const points = this.getPathPoints(line, fn, tn);
        if (points && points.length > 0) {
          line.path = points;
        }
      }
      this.addToPanel(line, fn, tn);
    },
    showSubNode(node) {
      let foldIds = [];
      const fn = (line, offset) => {
        const linkinfo = line.linkInfo;
        if (node.open.show) {
          line.unFold = linkinfo[offset];
          if (line.fold) {
            linkinfo[offset] = line.fold;
          } else {
            linkinfo[offset] = linkinfo[offset];
          }
        } else {
          line.fold = linkinfo[offset];
          if (line.unFold) {
            linkinfo[offset] = line.unFold;
          } else {
            linkinfo[offset] = 0;
          }
        }
        foldIds.push(line.id);
      };
      this.lines.forEach((line) => {
        if (line.from === node.id || line.to === node.id) {
          // this.$set(line, 'isHide', !node.open.show);
          // node.open.show ? line.show() : line.hide();
          fn(line, line.from === node.id ? "fromOffset" : "toOffset");
          foldIds.push(line.id);
        }
      });
      foldIds.length > 0 && this.renderLines(foldIds);
      this.updateIntersection();
    },
    hideLines(ids, isHide) {
      ids.forEach((id) => {
        this.lines.forEach((line) => {
          if (id === line.id) {
            line.isHide = isHide;
            isHide ? line.hide() : line.show();
          }
        });
      });
    },
    drawLine(evt) {
      const scale = this.$store.state.flow.page.scale;
      const pos = this.parentNode
        ? {
            x: evt.pageX - this.parentNode.point.x * scale,
            y:
              evt.pageY -
              (this.parentNode.point.y +
                this.parentNode.imgHeight +
                this.parentNode.padding * 2) *
                scale,
          }
        : {
            x: evt.pageX,
            y: evt.pageY,
          };
      let mp = this.$store.getters["flow/getMousePointByCanvas"](pos);
      mp.x = mp.x / scale;
      mp.y = mp.y / scale;
      const target = evt.target;
      if (this.currentLineInfo) {
        this.currentLineInfo.endPoint = null;
        this.currentLineInfo.targetNode = null;
        this.currentLineInfo.targetDirection = null;
      }
      if (
        target.tagName == "IMG" ||
        (target.tagName == "DIV" &&
          target.className.indexOf("task-node") !== -1)
      ) {
        //如果接触到节点
        const node = getObjByTarget(this.nodes, target);
        if (node) {
          var info = this.getDrawLineEndPointInfo(node, pos);
          var isAlway = false;
          if (this.currentLineInfo.fromNode == node) {
            if (this.currentLineInfo.fromDirection != info.direction) {
              isAlway = true;
            }
          } else {
            isAlway = true;
          }
          if (isAlway) {
            this.currentLineInfo.endPoint = info.point;
            this.currentLineInfo.targetNode = node;
            this.currentLineInfo.targetDirection = info.direction;
            const points = getNodeLinkNodPoints(this.currentLineInfo);
            if (points) {
              this.currentMouseDrawingLine.path = points;
              this.currentMouseDrawingLine.render();
            }
          }
        }
      } else {
        const points = getNodeNotLinkePoints(this.currentLineInfo, mp);
        if (points) {
          this.currentMouseDrawingLine.path = points;
          this.currentMouseDrawingLine.render();
        }
      }
      this.$bus.$emit("autoScroll", {
        type: "line",
        data: this.currentMouseDrawingLine,
        mouse: {
          x: evt.pageX,
          y: evt.pageY,
        },
      });
    },
    dragLine(evt) {
      const mp = this.mp;
      const moveMp = this.$store.getters["flow/getMousePointByCanvas"]({
        x: evt.pageX,
        y: evt.pageY,
      });
      //移动为超过2像素视为无效
      if (Math.abs(moveMp.x - mp.x) < 2 && Math.abs(moveMp.y - mp.y) < 2) {
        return false;
      }
      const target = evt.target;
      let fromNode = null;
      let toNode = null;
      const currentDragLine = this.currentDragLine;
      const line = this.checked;
      if (currentDragLine.changeJoinDriection) {
        if (currentDragLine.dragPosition == "start") {
          toNode = getObjById(this.nodes, line.to);
          this.$bus.$emit(
            `isDrewingLine${this.isSub ? this.parentNode.id : ""}`,
            "start"
          );
        } else if (currentDragLine.dragPosition == "end") {
          fromNode = getObjById(this.nodes, line.from);
          // 结束点不能是定时事件和监听事件
          this.$bus.$emit(
            `isDrewingLine${this.isSub ? this.parentNode.id : ""}`,
            "end"
          );
        }
        if (
          target.tagName == "IMG" ||
          (target.tagName == "DIV" &&
            target.className.indexOf("task-node") !== -1)
        ) {
          //如果接触到节点
          const node = getObjByTarget(this.nodes, target);
          if (node) {
            if (currentDragLine.dragPosition == "start") {
              fromNode = node;
              toNode = getObjById(this.nodes, line.to);
            } else if (currentDragLine.dragPosition == "end") {
              if (line.fromType === "event") {
                fromNode = getObjById(this.events, line.from);
              } else {
                fromNode = getObjById(this.nodes, line.from);
              }
              toNode = node;
            }
          }
        } else {
          if (currentDragLine.dragPosition == "start") {
            toNode = getObjById(this.nodes, line.to);
          } else {
            if (line.fromType === "event") {
              fromNode = getObjById(this.events, line.from);
            } else {
              fromNode = getObjById(this.nodes, line.from);
            }
          }
        }
        this.mouseDragLine(fromNode, toNode, evt);
      } else {
        if (line.fromType === "event") {
          fromNode = getObjById(this.events, line.from);
        } else {
          fromNode = getObjById(this.nodes, line.from);
        }
        toNode = getObjById(this.nodes, line.to);
        this.mouseDragLine(fromNode, toNode, evt);
      }
      this.isDrag = true;
      this.$bus.$emit("autoScroll", {
        type: "line",
        data: currentDragLine,
        mouse: {
          x: evt.pageX,
          y: evt.pageY,
        },
      });
      evt.preventDefault();
    },
    mouseup(evt) {
      evt.stopPropagation();
      evt.preventDefault();
      this.start = false;
      this.$bus.$emit(
        `isDrewingLine${this.isSub ? this.parentNode.id : ""}`,
        null
      );
      if (this.type === "drawLine") {
        this.drawRealLine();
        return false;
      }
      this.dragRealLine();
      return false;
    },
    drawRealLine() {
      if (!this.currentLineInfo) {
        return false;
      }
      try {
        const fn = this.currentLineInfo.fromNode;
        const tn = this.currentLineInfo.targetNode;
        const options = {
          paper: this.paper,
        };
        if (fn && fn instanceof BoundaryEvent) {
          options.fromType = "event";
        }
        const line = new Line(this.config, options);
        if (this.isSub) {
          line.parentNodeId = this.parentNode.id;
        }
        line.path = this.currentMouseDrawingLine.path;
        if (tn) {
          this.updateLineLinkInfo(
            line,
            fn,
            tn,
            this.currentLineInfo.fromDirection,
            this.currentLineInfo.targetDirection
          );
          this.currentMouseDrawingLine.remove();
          this.addToPanel(line, fn, tn);
        } else {
          this.currentMouseDrawingLine.remove();
        }
      } catch (ex) {
        console.log(ex);
      } finally {
        this.currentLineInfo = null;
      }
    },
    addToPanel(line, fn, tn) {
      const attachSize = this.maxSizeOffset;
      line.paper = this.paper;
      this.lines.push(line);
      let changeFlowSize = false;
      for (let i = 0; i < line.path.length; i++) {
        let p = line.path[i];
        if (parseFloat(p.x) + attachSize > this.maxWidth) {
          this.maxWidth = parseFloat(p.x) + attachSize;
          changeFlowSize = true;
        }
        if (parseFloat(p.y) + attachSize > this.maxWidth) {
          this.maxHeight = parseFloat(p.y) + attachSize;
          changeFlowSize = true;
        }
      }
      if (changeFlowSize) {
        this.$bus.$emit("resize");
      }
      if (!line.id) {
        line.id = tools.uuid();
      }
      this.render(line);
      line.isHide ? line.hide() : line.show();
      this.updateIntersection();
      this.setNodeLinkNodeInfo(fn, tn, line);
      line.setAttributes(fn, tn);
    },
    dragRealLine() {
      let currentDragLine = this.currentDragLine;
      const line = this.checked;
      try {
        if (this.isDrag) {
          let from, to, fromDirection, toDirection;
          if (currentDragLine.changeJoinDriection) {
            if (currentDragLine.fromNode && currentDragLine.toNode) {
              if (currentDragLine.fromNode) {
                from = currentDragLine.fromNode;
              } else if (line.fromType === "event") {
                from = getObjById(this.events, line.from);
              } else {
                from = getObjById(this.nodes, line.from);
              }
              to = currentDragLine.toNode
                ? currentDragLine.toNode
                : getObjById(this.nodes, line.to);
              fromDirection = currentDragLine.fromDirection
                ? currentDragLine.fromDirection
                : line.linkInfo.fromDirection;
              toDirection = currentDragLine.toDirection
                ? currentDragLine.toDirection
                : line.linkInfo.toDirection;
              let isAlway = true;
              if (from == to) {
                if (fromDirection == toDirection) {
                  isAlway = false;
                }
              }
              if (isAlway) {
                const path = currentDragLine.proxy.path;
                if (path && path.length > 0) {
                  line.path = path;
                  this.render(line);
                }
                //清理线愿连线节点
                if (line.id) {
                  if (line.from) {
                    let fn;
                    if (line.fromType === "event") {
                      fn = getObjById(this.events, line.from);
                    } else {
                      fn = getObjById(this.nodes, line.from);
                    }
                    if (fn) {
                      const index = fn["out"].indexOf(line.id);
                      if (index >= 0) {
                        fn["out"].splice(index, 1);
                      }
                    }
                  }
                  if (line.to) {
                    let tn = getObjById(this.nodes, line.to);
                    if (tn) {
                      const index = tn["in"].indexOf(line.id);
                      if (index >= 0) {
                        tn["in"].splice(index, 1);
                      }
                    }
                  }
                }
              }
            }
          } else {
            if (line.fromType === "event") {
              from = getObjById(this.events, line.from);
            } else {
              from = getObjById(this.nodes, line.from);
            }
            to = getObjById(this.nodes, line.to);
            fromDirection = currentDragLine.fromDirection
              ? currentDragLine.fromDirection
              : line.linkInfo.fromDirection;
            toDirection = currentDragLine.toDirection
              ? currentDragLine.toDirection
              : line.linkInfo.toDirection;
            const path = currentDragLine.proxy.path;
            if (path && path.length > 0) {
              line.path = path;
              this.render(line);
            }
          }
          if (from && to) {
            this.updateLineLinkInfo(line, from, to, fromDirection, toDirection);
            this.setNodeLinkNodeInfo(from, to, line);
            line.setAttributes(from, to);
            this.updateIntersection();
          }
        }
      } catch (ex) {
        console.log(ex);
      } finally {
        this.isDrag = false;
        currentDragLine.proxy.remove();
        currentDragLine = null;
        this.$bus.$emit("resize");
      }
    },
    mouseDrawLine(evt) {
      const scale = this.$store.state.flow.page.scale;
      this.currentMouseDrawingLine = null;
      var node = this.checked;
      this.currentMouseDrawingLine = new Line_proxy(this.config, this.paper, {
        strokeWidth: 2,
        from: node.id,
      });
      const pos = this.parentNode
        ? {
            x: evt.pageX - this.parentNode.point.x * scale,
            y:
              evt.pageY -
              (this.parentNode.point.y +
                this.parentNode.imgHeight +
                this.parentNode.padding * 2) *
                scale,
          }
        : {
            x: evt.pageX,
            y: evt.pageY,
          };
      this.startInfo = this.getDrawLineStartPointInfo(node, pos);
      this.startPoint = this.startInfo.point;
      this.down_direction = this.startInfo.direction;
      this.currentMouseDrawingLine.path.push(this.startPoint);
      this.currentLineInfo = {
        startPoint: this.startPoint,
        fromDirection: this.down_direction,
        fromNode: node,
      };
    },
    eventDrawLine(evt) {
      this.currentMouseDrawingLine = null;
      var event = this.checked;
      this.currentMouseDrawingLine = new Line_proxy(this.config, this.paper, {
        strokeWidth: 2,
        from: event.id,
      });
      this.startInfo = this.getEventLineStartPointInfo(event, {
        x: evt.pageX,
        y: evt.pageY,
      });
      this.startPoint = this.startInfo.point;
      this.down_direction = this.startInfo.direction;
      this.currentMouseDrawingLine.path.push(this.startPoint);
      this.currentLineInfo = {
        startPoint: this.startPoint,
        fromDirection: this.down_direction,
        fromNode: event,
      };
    },
    getDrawLineStartPointInfo(node, point) {
      let downPoint = this.$store.getters["flow/getMousePointByCanvas"]({
        x: point.x,
        y: point.y,
      });
      const scale = this.$store.state.flow.page.scale;
      downPoint.x = downPoint.x / scale;
      downPoint.y = downPoint.y / scale;
      const down_direction = tools.getNodeDirection(node, downPoint);
      const nodeCenter = tools.getNodeCenter(node);
      if (down_direction == LineDirection.Right) {
        // const size = (node.type === '1' || node.type === '5') ? nodeTaskSize : nodeSize;
        downPoint.x = nodeCenter.x + node.width / 2;
        if (downPoint.y < node.point.y) {
          downPoint.y = node.point.y;
        }
        if (downPoint.y > node.point.y + node.height) {
          downPoint.y = node.point.y + node.height;
        }
        if (Math.abs(downPoint.y - nodeCenter.y) < this.centerOffset) {
          downPoint.y = nodeCenter.y;
        } else {
          downPoint.y = getGridPoint(downPoint).y;
        }
      }
      if (down_direction == LineDirection.Left) {
        downPoint.x = node.point.x;
        if (downPoint.y < node.point.y) {
          downPoint.y = node.point.y;
        }
        if (downPoint.y > node.point.y + node.height) {
          downPoint.y = node.point.y + node.height;
        }
        if (Math.abs(downPoint.y - nodeCenter.y) < this.centerOffset) {
          downPoint.y = nodeCenter.y;
        } else {
          downPoint.y = getGridPoint(downPoint).y;
        }
      } else if (down_direction == LineDirection.Bottom) {
        downPoint.y = node.point.y + node.height;

        if (downPoint.x < node.point.x) {
          downPoint.x = node.point.x;
        }
        if (downPoint.x > node.point.x + node.width) {
          downPoint.x = node.point.x + node.width;
        }
        if (Math.abs(downPoint.x - nodeCenter.x) < this.centerOffset) {
          downPoint.x = nodeCenter.x;
        } else {
          downPoint.x = getGridPoint(downPoint).x;
        }
      } else if (down_direction == LineDirection.Top) {
        downPoint.y = node.point.y;
        if (downPoint.x < node.point.x) {
          downPoint.x = node.point.x;
        }
        if (downPoint.x > node.point.x + node.width) {
          downPoint.x = node.point.x + node.width;
        }
        if (Math.abs(downPoint.x - nodeCenter.x) < this.centerOffset) {
          downPoint.x = nodeCenter.x;
        } else {
          downPoint.x = getGridPoint(downPoint).x;
        }
      }
      const p = {
        x: downPoint.x,
        y: downPoint.y,
      };
      const info = {
        point: p,
        direction: down_direction,
      };
      return info;
    },
    //获取完成画线信息
    getDrawLineEndPointInfo(node, point) {
      let overPoint = this.$store.getters["flow/getMousePointByCanvas"]({
        x: point.x,
        y: point.y,
      });
      const scale = this.$store.state.flow.page.scale;
      overPoint.x = overPoint.x / scale;
      overPoint.y = overPoint.y / scale;
      const nodeCenter = tools.getNodeCenter(node);
      const over_direction = tools.getNodeDirection(node, overPoint);
      if (over_direction == LineDirection.Left) {
        if (overPoint.y < node.point.y) {
          overPoint.y = node.point.y;
        }
        if (overPoint.y > node.point.y + node.height) {
          overPoint.y = node.point.y + node.height;
        }
        if (Math.abs(overPoint.y - nodeCenter.y) < this.centerOffset) {
          overPoint.y = nodeCenter.y;
        } else {
          overPoint.y = getGridPoint(overPoint).y;
        }
        overPoint.x = node.point.x;
      } else if (over_direction == LineDirection.Right) {
        if (overPoint.y < node.point.y) {
          overPoint.y = node.point.y;
        }
        if (overPoint.y > node.point.y + node.height) {
          overPoint.y = node.point.y + node.height;
        }
        if (Math.abs(overPoint.y - nodeCenter.y) < this.centerOffset) {
          overPoint.y = nodeCenter.y;
        } else {
          overPoint.y = getGridPoint(overPoint).y;
        }
        overPoint.x = node.point.x + node.width;
      } else if (over_direction == LineDirection.Top) {
        if (overPoint.x < node.point.x) {
          overPoint.x = node.point.x;
        }
        if (overPoint.x > node.point.x + node.width) {
          overPoint.x = node.point.x + node.width;
        }
        if (Math.abs(overPoint.x - nodeCenter.x) < this.centerOffset) {
          overPoint.x = nodeCenter.x;
        } else {
          overPoint.x = getGridPoint(overPoint).x;
        }
        overPoint.y = node.point.y;
      } else if (over_direction == LineDirection.Bottom) {
        if (overPoint.x < node.point.x) {
          overPoint.x = node.point.x;
        }
        if (overPoint.x > node.point.x + node.width) {
          overPoint.x = node.point.x + node.width;
        }
        if (Math.abs(overPoint.x - nodeCenter.x) < this.centerOffset) {
          overPoint.x = nodeCenter.x;
        } else {
          overPoint.x = getGridPoint(overPoint).x;
        }
        overPoint.y = node.point.y + node.height;
      }

      const p = {
        x: overPoint.x,
        y: overPoint.y,
      };
      const info = {
        point: p,
        direction: over_direction,
      };
      return info;
    },
    getEventLineStartPointInfo(event, point) {
      let downPoint = this.$store.getters["flow/getMousePointByCanvas"]({
        x: point.x,
        y: point.y,
      });
      const scale = this.$store.state.flow.page.scale;
      downPoint.x = downPoint.x / scale;
      downPoint.y = downPoint.y / scale;
      const down_direction = event.direction;
      if (down_direction == LineDirection.Right) {
        downPoint.x = event.point.x + event.width / 2 + event.img.width / 2;
        downPoint.y = event.point.y + event.img.width / 2;
      } else if (down_direction == LineDirection.Left) {
        downPoint.x = event.point.x + event.width / 2 - event.img.width / 2;
        downPoint.y = event.point.y + event.img.width / 2;
      } else if (down_direction == LineDirection.Top) {
        downPoint.x = event.point.x + event.width / 2;
        downPoint.y = event.point.y;
      } else {
        downPoint.x = event.point.x + event.width / 2;
        downPoint.y = event.point.y + event.img.width;
      }
      const p = {
        x: downPoint.x,
        y: downPoint.y,
      };
      const info = {
        point: p,
        direction: down_direction,
      };
      return info;
    },
    //更新连线的连接属性
    updateLineLinkInfo(line, fromNode, toNode, fromDirection, toDirection) {
      // var fromNode = this.getObjectById(line.from);
      // var toNode = this.getObjectById(line.to);
      if (typeof fromNode == "string") {
        fromNode = getObjById(this.nodes, fromNode);
      }
      if (typeof toNode == "string") {
        toNode = getObjById(this.nodes, toNode);
      }
      if (fromNode && toNode) {
        var f_offset = 0,
          t_offset = 0;
        var sp = {
          x: line.path[0].x,
          y: line.path[0].y,
        };
        var ep = {
          x: line.path[line.path.length - 1].x,
          y: line.path[line.path.length - 1].y,
        };
        var f_center = tools.getNodeCenter(fromNode);
        var t_center = tools.getNodeCenter(toNode);
        if (
          fromDirection == LineDirection.Top ||
          fromDirection == LineDirection.Bottom
        ) {
          f_offset = sp.x - f_center.x;
        } else if (
          fromDirection == LineDirection.Left ||
          fromDirection == LineDirection.Right
        ) {
          f_offset = sp.y - f_center.y;
        }
        if (
          toDirection == LineDirection.Top ||
          toDirection == LineDirection.Bottom
        ) {
          t_offset = ep.x - t_center.x;
        } else if (
          toDirection == LineDirection.Left ||
          toDirection == LineDirection.Right
        ) {
          t_offset = ep.y - t_center.y;
        }
        line.linkInfo = {
          fromDirection: fromDirection,
          toDirection: toDirection,
          fromOffset: f_offset,
          toOffset: t_offset,
        };
      }
    },
    //全局验证线段交错，如果有交错时水平线上增加圆弧
    updateIntersection() {
      const segments = this.getAllSegments();
      const v_segments = segments.v_segments;
      const h_segments = segments.h_segments;
      for (let n = 0; n < h_segments.length; n++) {
        let h_s = h_segments[n];
        let isInter = h_s.getIntersection().length > 0;
        h_s.clearIntersection();
        for (let i = 0; i < v_segments.length; i++) {
          let v_s = v_segments[i];
          let intr = tools.segmentsIntr(h_s.sp, h_s.ep, v_s.sp, v_s.ep);
          let alwaySize = transitionRadius + 5;
          if (intr) {
            if (
              Math.abs(intr.x - h_s.sp.x) <= alwaySize ||
              Math.abs(intr.x - h_s.ep.x) <= alwaySize
            ) {
              continue;
            }
            if (
              Math.abs(intr.y - v_s.sp.y) <= alwaySize ||
              Math.abs(intr.y - v_s.ep.y) <= alwaySize
            ) {
              continue;
            }

            var list = h_s.getIntersection();
            var isExits = false;
            for (let c = 0; c < list.length; c++) {
              if (list[c].x == intr.x && list[c].y == intr.y) {
                isExits = true;
                break;
              }
            }
            if (!isExits) {
              h_s.setIntersection(intr);
            }
          }
        }
        if (h_s.getIntersection().length > 0 || isInter) {
          h_s.render();
          const line = h_s.getLine();
          if (line.isHide) {
            h_s.hide();
          }
        }
      }
    },
    //获取所有线段
    getAllSegments() {
      var v_segments = [],
        h_segments = [];
      //整理水平线和垂直线
      for (let i = 0; i < this.lines.length; i++) {
        // var segments = this.lines[i].elementList;
        var segments = this.lines[i].getSegmenList();
        for (let n = 0; n < segments.length; n++) {
          var s = segments[n];
          var lineType = this.getLineType(s);
          if (lineType == _LineType.horizontal) {
            h_segments.push(s);
          } else {
            v_segments.push(s);
          }
        }
      }
      return {
        v_segments: v_segments,
        h_segments: h_segments,
      };
    },
    getLineType(segmen) {
      //水平线
      if (segmen.sp.y == segmen.ep.y) {
        return _LineType.horizontal;
      }
      if (segmen.sp.x == segmen.ep.x) {
        return _LineType.vertical;
      }
    },
    nodeMove(node) {
      const lineIds = [...node.in, ...node.out];
      this.renderLines(lineIds);
      this.updateIntersection();
    },
    renderLines(lineIds) {
      if (!lineIds || (lineIds && lineIds.length == 0)) {
        return;
      }
      for (let i = 0; i < lineIds.length; i++) {
        let lineId = lineIds[i];
        let line = getObjById(this.lines, lineId);
        let from;
        if (line.fromType === "event") {
          from = getObjById(this.events, line.from);
        } else {
          from = getObjById(this.nodes, line.from);
        }
        const to = getObjById(this.nodes, line.to);
        const points = this.getPathPoints(line, from, to);
        if (points && points.length > 0) {
          line.path = points;
          this.render(line);
          this.hideOrShowSubNodeLines(line, from, to);
        }
      }
    },
    getPathPoints(line, from, to) {
      const linkinfo = line.linkInfo;
      const sp = (line.fromType === "event"
        ? this.getEventPointByLineOffset
        : this.getPointByLineOffset)(line, from, linkinfo.fromDirection);
      const ep = this.getPointByLineOffset(line, to, linkinfo.toDirection);
      if (sp && ep) {
        const info = {
          fromNode: from,
          targetNode: to,
          startPoint: sp,
          endPoint: ep,
          fromDirection: linkinfo.fromDirection,
          targetDirection: linkinfo.toDirection,
        };
        return getNodeLinkNodPoints(info);
      }
      return [];
    },
    hideOrShowSubNodeLines(line, from, to) {
      line.isHide = false;
      if (line.fromType === "event" && (from.hide || to.hide)) {
        line.hide();
        line.isHide = true;
        // } else if(from.open && !from.open.show) {
        //   line.hide();
        //   line.isHide = true;
        // } else if(to.open && !to.open.show) {
        //   line.hide();
        //   line.isHide = true;
      } else {
        line.show();
      }
    },
    eventMove(event) {
      if (event.out && event.out.length > 0) {
        for (let i = 0; i < event.out.length; i++) {
          var lineId = event.out[i];
          var line = getObjById(this.lines, lineId);
          var from = getObjById(this.events, line.from);
          var to = getObjById(this.nodes, line.to);
          var linkinfo = line.linkInfo;
          var sp = 0,
            ep = 0;
          var sp = this.getEventPointByLineOffset(
            line,
            from,
            linkinfo.fromDirection
          );
          var ep = this.getPointByLineOffset(line, to, linkinfo.toDirection);
          if (sp && ep) {
            var info = {
              fromNode: from,
              targetNode: to,
              startPoint: sp,
              endPoint: ep,
              fromDirection: linkinfo.fromDirection,
              targetDirection: linkinfo.toDirection,
            };
            var points = getNodeLinkNodPoints(info);
            if (points && points.length > 0) {
              line.path = points;
              this.render(line);
              this.hideOrShowSubNodeLines(line, from, to);
            }
          }
        }
      }
      this.updateIntersection();
    },
    //根据连线在节点中心的偏移量计算绝对坐标
    getPointByLineOffset(line, node, direction) {
      let p = null;
      const center = tools.getNodeCenter(node);
      const state =
        line.from === node.id ? "from" : line.to === node.id ? "to" : null;
      if (!state) {
        return null;
      }
      const offset =
        state == "from" ? line.linkInfo.fromOffset : line.linkInfo.toOffset;
      if (direction == LineDirection.Top) {
        p = {
          x: center.x + offset,
          y: node.point.y,
        };
      } else if (direction == LineDirection.Bottom) {
        p = {
          x: center.x + offset,
          y: node.point.y + node.height,
        };
      } else if (direction == LineDirection.Left) {
        p = {
          x: node.point.x,
          y: center.y + offset,
        };
      } else {
        p = {
          x: node.point.x + node.width,
          y: center.y + offset,
        };
      }
      return p;
    },
    //根据连线在节点中心的偏移量计算绝对坐标
    getEventPointByLineOffset(line, event, direction) {
      let p = null;
      if (direction == LineDirection.Top) {
        p = {
          x: event.point.x + event.width / 2,
          y: event.point.y,
        };
      } else if (direction == LineDirection.Bottom) {
        p = {
          x: event.point.x + event.width / 2,
          y: event.point.y + event.img.height,
        };
      } else if (direction == LineDirection.Left) {
        p = {
          x: event.point.x + event.width / 2 - event.img.height / 2,
          y: event.point.y + event.img.height / 2,
        };
      } else {
        p = {
          x: event.point.x + event.width / 2 + event.img.height / 2,
          y: event.point.y + event.img.height / 2,
        };
      }
      return p;
    },
    // 根据展开后的
    getPointByFold(line, node, direction, show, state) {
      if (!show) {
        const offset =
          state === "from" ? line.linkInfo.fromOffset : line.linkInfo.toOffset;
        const center = tools.getNodeCenter(node);
        if (direction == LineDirection.Top) {
          p = {
            x: center.x + offset,
            y: node.point.y,
          };
        } else if (direction == LineDirection.Bottom) {
          p = {
            x: center.x + offset,
            y: node.point.y + node.height,
          };
        } else if (direction == LineDirection.Left) {
          p = {
            x: node.point.x,
            y: center.y + offset,
          };
        } else {
          p = {
            x: node.point.x + node.width,
            y: center.y + offset,
          };
        }
        return p;
      }
    },
    //拖动线
    lineMouseDownHandler(evt, line) {
      this.type = "dragline";
      var segmen = getObjectByTarget(evt.target);
      if (segmen) {
        this.start = true;
        this.isDrag = false;
        if (!line) {
          return false;
        }
        let currentDragLine = {
          segmen: segmen,
          line: line,
          proxy: new Line_proxy(this.config, this.paper, {
            "stroke-dasharray": ["-"],
          }),
          changeJoinDriection: false,
        };
        this.currentDragLine = currentDragLine;
        const mp = (this.mp = this.$store.getters["flow/getMousePointByCanvas"](
          {
            x: evt.pageX,
            y: evt.pageY,
          }
        ));

        if (line.getSegmenList().length == 1) {
          if (segmen.type == _LineType.horizontal) {
            if (Math.abs(segmen.sp.x - mp.x) <= this.dragSize) {
              currentDragLine.changeJoinDriection = true;
              currentDragLine.dragPosition = "start";
            } else if (Math.abs(segmen.ep.x - mp.x) <= this.dragSize) {
              currentDragLine.changeJoinDriection = true;
              currentDragLine.dragPosition = "end";
            }
          } else {
            if (
              segmen.index == 0 &&
              Math.abs(segmen.sp.y - mp.y) <= this.dragSize
            ) {
              currentDragLine.changeJoinDriection = true;
              currentDragLine.dragPosition = "start";
            } else if (
              segmen.isLast &&
              Math.abs(segmen.ep.y - mp.y) <= this.dragSize
            ) {
              currentDragLine.changeJoinDriection = true;
              currentDragLine.dragPosition = "end";
            }
          }
        } else if (line.getSegmenList().length > 1) {
          if (segmen.type == _LineType.horizontal) {
            if (
              segmen.index == 0 &&
              Math.abs(segmen.sp.x - mp.x) <= this.dragSize
            ) {
              currentDragLine.changeJoinDriection = true;
              currentDragLine.dragPosition = "start";
            } else if (
              segmen.isLast &&
              Math.abs(segmen.ep.x - mp.x) < this.dragSize
            ) {
              currentDragLine.changeJoinDriection = true;
              currentDragLine.dragPosition = "end";
            }
          } else {
            if (
              segmen.index == 0 &&
              Math.abs(segmen.sp.y - mp.y) <= this.dragSize
            ) {
              currentDragLine.changeJoinDriection = true;
              currentDragLine.dragPosition = "start";
            } else if (
              segmen.isLast &&
              Math.abs(segmen.ep.y - mp.y) <= this.dragSize
            ) {
              currentDragLine.changeJoinDriection = true;
              currentDragLine.dragPosition = "end";
            }
          }
        }
      }
    },
    mouseDragLine(fromNode, toNode, evt) {
      const currentDragLine = this.currentDragLine;
      if (currentDragLine) {
        if (!currentDragLine.changeJoinDriection) {
          var mp = this.$store.getters["flow/getMousePointByCanvas"]({
            x: evt.pageX,
            y: evt.pageY,
          });
          mp = getGridPoint(mp);
          if (this.parentNode) {
            mp.x = mp.x - this.parentNode.point.x;
            mp.y = mp.y - this.parentNode.point.y - this.parentNode.imgHeight;
          }
          const scale = this.$store.state.flow.page.scale;
          mp.x = mp.x / scale;
          mp.y = mp.y / scale;
          var segmen = currentDragLine.segmen;
          if (segmen) {
            var line = segmen.getLine();
            if (segmen.type == _LineType.vertical) {
              if (segmen.index == 0) {
                let node;
                if (line.fromType === "event") {
                  node = getObjById(this.events, line.from);
                } else {
                  node = getObjById(this.nodes, line.from);
                }
                if (mp.x >= node.point.x && mp.x <= node.point.x + node.width) {
                  segmen.ep.x = mp.x;
                  segmen.sp.x = mp.x;
                }
              } else if (segmen.isLast) {
                var node = getObjById(this.nodes, line.to);
                if (mp.x >= node.point.x && mp.x <= node.point.x + node.width) {
                  segmen.ep.x = mp.x;
                  segmen.sp.x = mp.x;
                }
              } else {
                segmen.ep.x = mp.x;
                segmen.sp.x = mp.x;
              }
            } else {
              if (segmen.index == 0) {
                let node;
                if (line.fromType === "event") {
                  node = getObjById(this.events, line.from);
                } else {
                  node = getObjById(this.nodes, line.from);
                }
                if (
                  mp.y >= node.point.y &&
                  mp.y <= node.point.y + node.height
                ) {
                  var fcenter = tools.getNodeCenter(node);
                  if (Math.abs(mp.y - fcenter.y) < this.centerOffset) {
                    mp.y = fcenter.y;
                  }
                  segmen.ep.y = mp.y;
                  segmen.sp.y = mp.y;
                }
              } else if (segmen.isLast) {
                var node = getObjById(this.nodes, line.to);
                if (
                  mp.y >= node.point.y &&
                  mp.y <= node.point.y + node.height
                ) {
                  var tcenter = tools.getNodeCenter(node);
                  if (Math.abs(mp.y - tcenter.y) < this.centerOffset) {
                    mp.y = tcenter.y;
                  }
                  segmen.ep.y = mp.y;
                  segmen.sp.y = mp.y;
                }
              } else {
                segmen.ep.y = mp.y;
                segmen.sp.y = mp.y;
              }
            }
            var el_list = line.getSegmenList();
            var path = this.covertPointBySegmen(el_list);
            currentDragLine.proxy.path = path;
            currentDragLine.proxy.render();
          }
        } else {
          var segmen = currentDragLine.segmen;
          if (segmen) {
            var line = segmen.getLine();
            var mp = this.$store.getters["flow/getMousePointByCanvas"]({
              x: evt.pageX,
              y: evt.pageY,
            });
            const scale = this.$store.state.flow.page.scale;
            mp.x = mp.x / scale;
            mp.y = mp.y / scale;
            if (currentDragLine.dragPosition == "start") {
              var f_node = fromNode; //getObjById(this.nodes, line.from);
              var t_node = toNode; // getObjById(this.nodes, line.to);
              var startInfo = null;
              var startPoint = null;
              var fromDirection = "";
              //如果没有开始节点，创建一个虚拟的对象
              if (f_node) {
                startInfo = this.getDrawLineStartPointInfo(f_node, {
                  x: evt.pageX,
                  y: evt.pageY,
                });
                startPoint = startInfo.point;
                fromDirection = startInfo.direction;
              } else {
                var f_p = getGridPoint(mp);
                f_node = {
                  width: 0,
                  height: 0,
                  point: f_p,
                  abstract: true,
                };
                startPoint = f_p;
                var d = tools.getNodeDirection(t_node, f_p);
                if (d == _Direction.Left) {
                  fromDirection = _Direction.Right;
                } else if (d == _Direction.Right) {
                  fromDirection = _Direction.Left;
                } else if (d == _Direction.Bottom) {
                  fromDirection = _Direction.Top;
                } else if (d == _Direction.Top) {
                  fromDirection = _Direction.Bottom;
                }
              }
              var ep = this.getPointByLineOffset(
                line,
                t_node,
                line.linkInfo.toDirection
              );
              var info = {
                startPoint: startPoint,
                fromDirection: fromDirection,
                fromNode: f_node,
                targetNode: t_node,
                targetDirection: line.linkInfo.toDirection,
                endPoint: ep,
              };
              var path = getNodeLinkNodPoints(info);
              if (!f_node.abstract) {
                currentDragLine.fromNode = f_node;
              } else {
                currentDragLine.fromNode = null;
              }
              currentDragLine.toNode = t_node;
              currentDragLine.fromDirection = fromDirection;
              currentDragLine.proxy.path = path;
              currentDragLine.proxy.render();
            } else if (currentDragLine.dragPosition == "end") {
              var f_node = fromNode;
              var t_node = toNode;
              var endInfo = null;
              var endPoint = null;
              var toDirection = null;
              if (t_node) {
                endInfo = this.getDrawLineStartPointInfo(t_node, {
                  x: evt.pageX,
                  y: evt.pageY,
                });
                endPoint = endInfo.point;
                toDirection = endInfo.direction;
              } else {
                var t_p = mp;
                t_node = {
                  width: 0,
                  height: 0,
                  point: t_p,
                  abstract: true,
                };
                endPoint = t_p;
                var d = tools.getNodeDirection(f_node, t_p);
                if (d == _Direction.Left) {
                  toDirection = _Direction.Right;
                } else if (d == _Direction.Right) {
                  toDirection = _Direction.Left;
                } else if (d == _Direction.Bottom) {
                  toDirection = _Direction.Top;
                } else if (d == _Direction.Top) {
                  toDirection = _Direction.Bottom;
                }
              }
              var sp = (f_node instanceof BoundaryEvent
                ? this.getEventPointByLineOffset
                : this.getPointByLineOffset)(
                line,
                f_node,
                line.linkInfo.fromDirection
              );
              var info = {
                startPoint: sp,
                fromDirection: line.linkInfo.fromDirection,
                fromNode: f_node,
                targetNode: t_node,
                targetDirection: toDirection,
                endPoint: endPoint,
              };
              var path = getNodeLinkNodPoints(info);
              currentDragLine.fromNode = f_node;
              if (!t_node.abstract) {
                currentDragLine.toNode = t_node;
              } else {
                currentDragLine.toNode = null;
              }
              currentDragLine.toDirection = toDirection;
              currentDragLine.proxy.path = path;
              currentDragLine.proxy.render();
            }
          }
        }
      }
    },
    //将线段数组转换成线段坐标
    covertPointBySegmen(segmenList) {
      var points = [];
      for (let i = 0; i < segmenList.length; i++) {
        var s = segmenList[i];
        points.push(s.sp);
        if (i == segmenList.length - 1) {
          points.push(s.ep);
        }
      }
      return points;
    },
    //设置连线后属性
    setNodeLinkNodeInfo(fromNode, targetNode, line) {
      if (fromNode["out"] && fromNode["out"].indexOf(line.id) < 0) {
        fromNode["out"].push(line.id);
      }
      if (targetNode["in"] && targetNode["in"].indexOf(line.id) < 0) {
        targetNode["in"].push(line.id);
      }
      line.from = fromNode.id;
      line.to = targetNode.id;
    },
    render(line) {
      line.remove();
      line.stroke = line.getLineStroke();
      var segmentArr = tools.convertSegmentByPoint(line.path);
      for (let i = 0; i < segmentArr.length; i++) {
        var p = segmentArr[i];
        var s = new Segmen(line, this.paper, i, {
          strokeWidth: line.strokeWidth,
          stroke: line.stroke,
        });

        s.sp = p.sp;
        s.ep = p.ep;
        if (i == segmentArr.length - 1) {
          s.isLast = true;
        }
        s.direction = tools.getDirection(s.sp, s.ep);
        line.addElement(s);
      }
      const elementList = line.getSegmenList();
      for (let i = 0; i < elementList.length; i++) {
        elementList[i].render();
      }
    },
    globalMove(current_p, x_move, y_move) {
      if (!current_p) {
        return;
      }
      for (let i = 0; i < this.lines.length; i++) {
        let line = this.lines[i];
        for (let n = 0; n < line.path.length; n++) {
          if (x_move) {
            line.path[n].x += current_p.x;
          }
          if (y_move) {
            line.path[n].y += current_p.y;
          }
        }
        if (x_move || y_move) {
          this.render(line);
        }
      }
    },
    resetLineName(node) {
      this.lines.forEach((line) => {
        if (line.from === node.id) {
          const fromNames = line.name.split("→");
          if (fromNames.length === 2) {
            line.name = node.name + "→" + fromNames[1];
            line.attributes.BaseInfo.Name = line.name;
          }
        }
        if (line.to === node.id) {
          const toNames = line.name.split("→");
          if (toNames.length === 2) {
            line.name = toNames[0] + "→" + node.name;
            line.attributes.BaseInfo.Name = line.name;
          }
        }
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.lines {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
</style>
