<template>
  <div class="des-default-value">
    <div v-if="multiple" style="text-align:right;margin-bottom: 15px;">
      <DsfButton text="增加默认值" size="mini" @click="addData"></DsfButton>
    </div>
    <div style="max-height: 60vh; overflow: auto">
      <template v-for="(item, index) in tableData">
        <el-table :key="index" :data="item" :border="true" style="width: 100%;margin-bottom: 15px;">
          <el-table-column prop="text" label="名称" width="150"></el-table-column>
          <el-table-column prop="value" label="值">
            <template v-slot:default="scope">
              <el-input v-model="scope.row.value" />
            </template>
            <div v-if="multiple" slot="header">
              <span>值</span>
              <DsfButton style="float: right" text="删除" size="mini" @click="removeData(index)"></DsfButton>
            </div>
          </el-table-column>
        </el-table>
      </template>
    </div>
  </div>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesDefaultValue",
  mixins: [propertyDialogSetting],
  data() {
    return {
      multiple: false,
      tableData: []
    };
  },
  created() {
    this.multiple = this.config.multiple || false;
    this.valueAttributes = dsf.mix(true, [], this.owner.metadata.valueAttributes);
    this.init();
  },
  methods: {
    yes() {
      let result = this.tableData.map(rows => {
        let obj = {};
        rows.forEach(row => {
          obj[row.code] = row.value || "";
        })
        return obj
      })
      if (result.length == 1) {
        return result[0]
      }
      return result;
    },
    init() {
      let dataList = this.owner.metadata.defaultValue;
      let data = this.currentData;
      let rows;
      if (dsf.type(data) == "array") {
        rows = dsf.mix(true, [], data);
      } else if (dsf.type(data) == "object") {
        rows = [dsf.mix(true, {}, data)];
      } else if (dsf.type(dataList) == "array") {
        rows = dsf.mix(true, [], dataList);
      } else {
        rows = [dsf.mix(true, {}, dataList)];
      }
      rows.forEach(r => this.addData(r));
    },
    addData(rows = {}) {
      this.tableData.push(this.valueAttributes.map(va => {
        return {
          code: va.code,
          text: va.name,
          value: rows[va.code] == undefined ? null : rows[va.code]
        }
      }));
    },
    removeData(index) {
      if (this.tableData.length < 2) {
        this.tableData = [];
        this.addData();
      } else {
        this.tableData.splice(index, 1);
      }
    }
  }
};
</script>
