<template>
  <div class="ds-question-formwrap ds-question-item">
    <div class="example question-slider">
      <el-form label-position="top">
        <el-form-item prop="value" :label="exampleLabel">
          <el-slider
            v-model="value"
            :min="form.min"
            :max="form.max"
            :disabled="form.readOnly"
            :step="form.step"
          ></el-slider>
        </el-form-item>
      </el-form>
    </div>
    <el-form
      ref="form"
      :model="form"
      :rules="rules"
      class="form"
      label-width="auto"
      label-position="left"
    >
      <el-row :gutter="15">
        <el-col :span="24">
          <el-form-item prop="questionBody" label-width="0">
            <el-input v-model="form.questionBody" placeholder="请输入题干"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="step" label="最小滑动单位:">
            <el-input-number v-model="form.step" :min="form.min" :max="form.max"></el-input-number>
          </el-form-item>            
        </el-col>
        <el-col :span="12">
          <el-form-item prop="min" label="最小值:">
            <el-input-number v-model="form.min" :min="0" :max="form.max"></el-input-number>
          </el-form-item>            
        </el-col>  
        <el-col :span="12">
          <el-form-item prop="max" label="最大值:">
            <el-input-number v-model="form.max" :min="form.min"></el-input-number>
          </el-form-item>            
        </el-col>
        <el-col :span="12">
          <el-form-item prop="minText" label="最小值显示文本:">
            <el-input v-model="form.minText" placeholder="请输入"></el-input>
          </el-form-item>
        </el-col> 
        <el-col :span="12">
          <el-form-item prop="maxText" label="最大值显示文本:">
            <el-input v-model="form.maxText" placeholder="请输入"></el-input>
          </el-form-item>
        </el-col>                               
        <el-col :span="12">
          <el-form-item prop="readOnly" label="本题不允许填写:">
            <el-switch v-model="form.readOnly"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="visible" label="本题是否显示:">
            <el-switch v-model="form.visible"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="24">
          <div class="ljsz">
            <span class="label">逻辑设置:</span>
            <span class="btn" @click="showRela">题目关联</span>
          </div>
        </el-col>
      </el-row>                                
    </el-form>
  </div>
</template>
<script>
import questionConfig from "_platform/dsf/mixins/questionConfig";
export default {
    mixins: [questionConfig],
    // eslint-disable-next-line vue/require-prop-types
    props: ["owner"],
    data() {
        return {
            value: 0,
            form: {
                questionBody: '请输入题干',
                step: 1,
                readOnly: false,
                visible: true,
                min: 0,
                max: 100,
                minText: '',
                maxText: '',
            },
            rules: {
                questionBody: [{
                    required: true,
                    message: '请输入题干',
                }]
            }
        }
    },
    computed: {
      exampleLabel() {
        return `${this.owner.questionIndex}.${this.form.questionBody}(${this.value})`;
      },
      exampleRules() {
        return this.form.required ? [{ required: true, trigger: 'change' }] : null;
      }
    },
    methods: {
    },
}
</script>