const getObjById = (array, id, field) => {
  return array.find((obj) => (field ? obj[field] : obj.id) === id);
}

const getObjByTarget = (array, target) => {
  let obj;
  if (target.dataset && target.dataset.id) {
    array.forEach((item) => {
      if (item.id === target.dataset.id) {
        obj = item;
      }
    });
  }
  return obj;
}

const getObjByParentTarget = (parents, type, target) => {
  let obj;
  let parent;
  if (target.dataset && target.dataset.parentId) {
    parents.forEach(item => {
      if (item.id === target.dataset.parentId) {
        parent = item;
        item[type].forEach((sub) => {
          if (sub.id === target.dataset.id) {
            obj = sub;
          }
        });
      }
    });
  }
  return parent && obj ? { parent, sub: obj } : void 0;
}

// 后面会删除 现在主要是线用
const getObjectByTarget = (target) => {
  return $(target).data("object");
}

// 后面修改
let scale = 10;
const getGridPoint = (p) => {
  var result = {
    x: p.x == undefined ? 0 : p.x,
    y: p.y == undefined ? 0 : p.y,
  };
  if (result.x != 0) {
    var mo_x = result.x % scale;
    if (mo_x != 0) {
      if (mo_x > scale / 2) {
        result.x = result.x - mo_x + scale;
      } else {
        result.x = result.x - mo_x;
      }
    }
  }
  if (result.y != 0) {
    var mo_y = result.y % scale;
    if (mo_y != 0) {
      if (mo_y > scale / 2) {
        result.y = result.y - mo_y + scale;
      } else {
        result.y = result.y - mo_y;
      }
    }
  }
  return result;
}

const deepClone = (obj) => {
  const proto = Object.getPrototypeOf(obj);
  if (Array.isArray(obj)) {
    return Object.assign([], Object.create(proto), obj);
  }
  return Object.assign({}, Object.create(proto), obj);
}

const toPx = (number) => {
  return `${number}px`;
}

const getAttributes = function (objectPorperty) {
  var attr = {};
  for (let i = 0; i < objectPorperty.rows.length; i++) {
    var row = objectPorperty.rows[i];
    if (row.nodeLevel == 1) {
      attr[row.nodeName] = row.value;
    } else {
      var temp = attr;
      for (let n = 0; n < row.nodeLevel; n++) {
        var index = n + 1;
        var key = row["nodeName" + index];
        if (n < row.nodeLevel - 1) {
          if (temp[key] === undefined) {
            temp[key] = {};
          }
          temp = temp[key];
        } else {
          if (temp[key] === undefined) {
            temp[key] = row.value;
          }
        }
      }
    }
  }
  return attr;
};

export {
  getObjById,
  getObjByTarget,
  getObjByParentTarget,
  getObjectByTarget,
  getGridPoint,
  deepClone,
  toPx,
  getAttributes
}