<template>
  <div class="control-filter-tree">
    <dsf-tree-select
      :tree-data="treeData"
      node-key="dsfa_rm_id"
      node-name="name"
      :loading="treeLoading"
      :default-checked-key="defaultCheckedKey"
      search-control
      default-expand-all
      @choose-node="radioData = $event"
    />
  </div>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import DsfTreeSelect from "./item/treeSelect";

export default {
  name: "DesBusinessComponent",
  components: {
    DsfTreeSelect,
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      treeData: [],
      treeLoading: false,
      radioData: null,
      defaultCheckedKey: "",
    };
  },
  created() {
    this.getDesignCtrl();
  },
  methods: {
    getDesignCtrl() {
      this.loading = true;
      let url = "/rm/list/bc";
      this.$http
        .get(url)
        .done((result) => {
          if (result.success) {
            this.treeData = result.data;
          } else {
            dsf.layer.message("业务组件获取" + result.message, false);
          }
        })
        .error((res) => {
          dsf.layer.message(res.message, false);
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },
};
</script>
