<template>
  <el-container style="height:100%">
    <el-aside width="30%" style="overflow:auto;height:100%;border:1px solid #d4d4d4;">
      <el-table ref="table" :data="buttonsTable" highlight-current-row @current-change="currentChangeHandler">
        <el-table-column type="index" width="50" label="序号">
        </el-table-column>
        <el-table-column :label="'按钮名称'">
          <template v-slot:default="scope">
            <span>{{ scope.row.text }}</span>
          </template>
        </el-table-column>
      </el-table>
    </el-aside>
    <el-main style="padding:0px;border:1px solid #d4d4d4;margin-left:10px">
      <el-tree
        ref="tree"
        node-key="_id"
        show-checkbox
        default-expand-all
        :expand-on-click-node="false"
        :check-on-click-node="true"
        :data="treeData"
        :props="treeProps"
        @check-change="checkChangeHandler"
      >
      </el-tree>
    </el-main>
  </el-container>
</template>
<script>
export default {
  name: "DesFunctionSetting",
  props: {
    buttons: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
      treeData: [],
      treeProps: {
        'label': "_name"
      },
      buttonsTable: [],
      currentSelectRow: null
    }
  },
  async mounted() {
    let roles = await this.getOuaRulesTree();
    this.buttonsTable = _.map(this.buttons, (button) => {
      return button.setting ? dsf.mix(true, {}, button) : undefined;
    });
    console.log(this.buttons)
    this.buttonsTable = _.filter(this.buttonsTable, (button) => button);
    this.treeData = roles;
    this.$refs.table.setCurrentRow(this.buttonsTable[0])
  },
  methods: {
    async getOuaRulesTree() {
      return this.$webAPI.getOuaRulesTree()
        .then((res) => {
          return res.data;
        })
        .catch((err) => {
          dsf.layer.pc.message("加载角色树失败", false);
          throw err;
        })
    },
    currentChangeHandler(row) {
      this.currentSelectRow = row;
      let roles = this.currentSelectRow.roles;
      roles = _.map(roles, 'id');
      this.$refs.tree.setCheckedKeys(roles);
    },
    checkChangeHandler(data, isSelfCheck, isChildCheck) {
      if (data && this.currentSelectRow) {
        if (data.type_value == 2) {
          if (isSelfCheck) {
            let index = _.findIndex(this.currentSelectRow.roles, (el) => el.id === data._id);
            if (index >= 0) {
              this.currentSelectRow.roles.splice(index, 1);
            }
            this.currentSelectRow.roles.push({ "id": data._id, "name": data._name, "serial_number": data.serial_number });
          }
          else {
            dsf.array.remove(this.currentSelectRow.roles, (el, i) => {
              return el.id == data._id;
            });
          }
        }
      }
    },
    // nodeClickHandler(data, node) {
    //   this.$refs.tree.setChecked(node, !node.checked, true);
    // },
    yes() {
      return this.buttonsTable;
    }
  }
}
</script>