<template>
  <DsfButton type="auxiliary" icon="shezhi" @click="clickHandler">设置</DsfButton>
</template>
<script>
export default {
  name: "DsfDesignerDialog",
  model: {
    prop: "args",
  },
  // eslint-disable-next-line vue/require-prop-types
  props: ["attrdata", "args", "owner"],
  data() {
    return {
      cloneArgs: {},
      dialogVisible: false,
      width: "50%",
    };
  },
  created() {
    // 问卷设置
    const attrdata = this.attrdata;
    if (
      this.owner &&
      this.owner.isQuestionControl &&
      attrdata &&
      attrdata.config &&
      attrdata.config.content === "DesQuestionConfig"
    ) {
      this.$bus.$on("questionSetting", this.clickHandler);
    }
  },
  destroyed() {
    this.$bus.$off("questionSetting", this.clickHandler);
  },
  methods: {
    sure() { },
    cancel() { },
    clickHandler(e) {
      this.dialogVisible = true;
      let _this = this;
      let owner = this.owner;
      let config = this.attrdata.config;
      let title = config.title;
      let content = config.content;
      let width = config.width;
      let height = config.height;
      let overflow = config.overflow;
      let skin = config.class;
      if (dsf.type(this.args) == "array") {
        this.cloneArgs = dsf.mix(true, [], this.args);
      } else if (dsf.type(this.args) == "object") {
        this.cloneArgs = dsf.mix(true, {}, this.args);
      } else {
        this.cloneArgs = this.args;
      }
      var Dialog = Vue.extend({
        data: function () {
          return {
            dialogVisible: true,
            //标题
            title: title || "设置",
            //组件名称
            content: content,
            config: config,
            //将当前值得副本传递给弹出框内部的组件
            currentData: _this.cloneArgs,
            width: width || "50%",
            height: height || "auto",
            destroyOnClose: true,
            owner: owner,
            overflow: overflow || "auto",
            params: config.params || {},
            skin: skin || "",
          };
        },
        methods: {
          async sure() {
            let value = null;
            if (this.$refs.content) {
              if (this.$refs.content.validate() !== false) {
                if (this.$refs.content.yes) {
                  value = await this.$refs.content.yes();
                }
                this.dialogVisible = false;
                this.$nextTick(() => {
                  _this.$emit("change", value);
                });
              }
            }
          },
          close() {
            this.dialogVisible = false;
          },
          cancel() {
            if (component) {
              component = null;
            }
            window.setTimeout(() => {
              this.$destroy();
            }, 0);
          },
        },
        template: `<DsfDialog :class='skin' :title="title" :visible.sync="dialogVisible" :width="width" top="10vh" :close-on-click-modal="false" :append-to-body="true"  @close="close" @closed="cancel" >
                    <div :style="{'height':height,'overflow':(overflow||'auto')}">
                     <component :is="content"  v-bind="params" :current-data="currentData" :owner="owner" :config="config" ref="content"></component>
                    </div>
                    <template v-slot:footer>
                      <DsfButton text="确定" @click="sure" />
                      <DsfButton text="取消" type="auxiliary" @click="close"/>
                    </template>
                  </DsfDialog>`,
      });
      let component = new Dialog().$mount();
      // document.body.appendChild(component.$el);
    },
  },
};
</script>