<template>
  <div class="ds-question-formwrap">
    <div class="example">
      <el-form label-position="top">
        <el-form-item prop="value" :label="exampleLabel" :rules="exampleRules">
          <el-input :maxlength="form.maxLength" readonly placeholder="输入框效果展示" :rows="5" :type="isTextArea ? 'textarea' : 'text'" show-word-limit></el-input>
        </el-form-item>
      </el-form>
    </div>
    <el-form
      ref="form"
      :model="form"
      :rules="rules"
      class="form"
      label-width="auto"
      label-position="left"
    >
      <el-row :gutter="15">
        <el-col :span="24">
          <el-form-item prop="questionBody" label-width="0">
            <el-input v-model="form.questionBody" placeholder="请输入题干"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="isTextArea" label="使用多行文本框:">
            <el-switch v-model="form.isTextArea"></el-switch>
          </el-form-item>            
        </el-col>
        <el-col :span="12">
          <el-form-item prop="readOnly" label="本题不允许填写:">
            <el-switch v-model="form.readOnly"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="visible" label="本题是否显示:">
            <el-switch v-model="form.visible"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="required" label="必填标识:">
            <el-switch v-model="form.required"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="maxLength" label="可输入字数最大限制:">
            <el-input-number v-model="form.maxLength" :min="1" :max="200"></el-input-number>
          </el-form-item>  
        </el-col>
        <el-col :span="24">
          <div class="ljsz">
            <span class="label">逻辑设置:</span>
            <span class="btn" @click="showRela">题目关联</span>
          </div>
        </el-col>
      </el-row>                                
    </el-form>
  </div>
</template>
<script>
import questionConfig from "_platform/dsf/mixins/questionConfig";
export default {
    mixins: [questionConfig],
    // eslint-disable-next-line vue/require-prop-types
    props: ["owner"],
    data() {
        return {
            form: {
                questionBody: '请输入题干',
                isTextArea: false,
                readOnly: false,
                visible: true,
                maxLength: 200,
                required: true,
            },
            rules: {
                questionBody: [{
                    required: true,
                    message: '请输入题干',
                }]
            }
        }
    },
    computed: {
      isTextArea() {
        return this.form.isTextArea;
      },
      exampleLabel() {
        return `${this.owner.questionIndex}.${this.form.questionBody}`;
      },
      exampleRules() {
        return this.form.required ? [{ required: true, trigger: 'change' }] : null;
      }
    },
    methods: {
    },
}
</script>