<template>
  <div :title="title" :name="data.name" class="ds-tool-item ellipsis">
    {{ data.name||data.id }}
  </div>
</template>
<script>
export default {
  name: 'DsfMetaDataItem',
  props: {
    data: {
      type: Object,
      default() {
        return null
      }
    }
  },
  computed: {
    title() {
      let {data, id, name} = this.data;
      return [
        'ID：', id, '\n',
        '标识：', data.code, '\n',
        '名称：', name
      ].join('');
    }
  }
}
</script>