<template>
  <el-tabs v-model="activeName" style="height:100%" class="design-development-edit" @tab-click="tabsCliekHandler">
    <el-tab-pane name="attrs">
      <div slot="label" style="width:100px;text-align:center">属性/事件</div>
      <MonacoEditor ref="attrsEditor" :language="'json'" @mounted="editorLoaded('attrs')"></MonacoEditor>
    </el-tab-pane>
    <el-tab-pane label="其他（暂时无用）" name="events">
      <div slot="label" style="width:100px;text-align:center">其他（暂时无用）</div>
      <MonacoEditor ref="eventsEditor" :language="'json'" @mounted="editorLoaded('events')"></MonacoEditor>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";

export default {
  name: 'DesDevelopment',
  mixins: [propertyDialogSetting],
  data() {
    return {
      activeName: "attrs",
      data: {}
    }
  },
  created() {
    this.data = dsf.mix(true, {}, this.currentData);
  },
  mounted() {

  },
  methods: {
    yes() {
      let obj = {
        attrs: {},
        events: {}
      }
      for (let k in obj) {
        let editor = this.$refs[k + "Editor"];
        if (editor) {
          obj[k] = JSON.parse(editor.getContent() || "{}");
        }
      }
      return obj;
    },
    tabsCliekHandler(tab) {
      this.$nextTick(() => {
        let name = tab.name;
        let key = name + "Editor";
        if (this.$refs[key]) {
          this.$refs[key].layout();
        }

      })
    },
    editorLoaded(key) {
      let name = key + "Editor";
      if (this.$refs[name]) {
        this.$refs[name].setContent(JSON.stringify(this.data[key]));
      }
    }
  }
}
</script>