<template>
  <div class="event-setting">
    <div class="left">
      <MonacoEditor ref="editor" :language="language" @mounted="editorLoaded"></MonacoEditor>
    </div>
    <div class="right">
      <el-collapse v-model="activeName">
        <el-collapse-item v-for="(item, index) in list" :key="index" :title="item.title" :name="index">
          <SelectTree :tree-data="item.tree" :hide-checkbox="true" @change="change"></SelectTree>
        </el-collapse-item>
      </el-collapse>
    </div>
  </div>
</template>
<script>
import SelectTree from "./selectTree";
export default {
  name: "EventSetting",
  components: {
    SelectTree,
  },
  props: {
    code: {
      type: String,
      default: "",
    },
    language: {
      type: String,
      default: "javascript",
    },
    isEvent: {
      type: Boolean,
      default() {
        return false;
      },
    },
  },
  data() {
    return {
      activeName: [0],
      list: [
        {
          title: "上下文",
          tree: [],
        },
        {
          title: "公用函数",
          tree: [],
        },
      ],
    };
  },
  created() {
    this.init();
  },
  mounted() {},
  methods: {
    getContent() {
      let code;
      if (this.language == "html") {
        code = dsf.escapeFilter(this.$refs.editor.getContent());
      } else {
        code = this.$refs.editor.getContent();
      }
      return this.isEvent ? { Expression: code } : code;
    },
    editorLoaded() {
      let v = this.code;
      if (this.language == "html") {
        v = dsf.unescapeHTML(v);
      }
      this.$refs.editor.setContent(v);
    },
    init() {
      this.$http
        .all([
          this.$http.get("./static/pd_other/context.json"),
          this.$http.get("./static/pd_other/utilityFunction.json"),
        ])
        .then(([{ Context }, { Function }]) => {
          this.list[0].tree.push(...Context);
          this.list[1].tree.push(...Function);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    change(val) {
      if (val && val.Key) {
        this.$refs.editor.insertContent(val.Key);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.event-setting {
  position: relative;
  width: 100%;
  height: 100%;
  .left {
    position: relative;
    float: left;
    width: 70%;
    height: 100%;
  }
  .right {
    position: relative;
    float: right;
    width: 29%;
    height: 100%;
    border: 1px solid #ccc;
    box-sizing: border-box;
    overflow: auto;
    /deep/ .dsf-select-tree {
      overflow: hidden;
    }
    /deep/ .el-collapse-item__header {
      height: 42px;
      line-height: 42px;
      padding-left: 10px;
      background-color: #f2f2f2;

      &.is-active {
        border-bottom: 1px solid #e6e6e6;
      }

      .ellipsis {
        padding-right: 30px;
      }
    }

    .el-tabs__header.is-top {
      margin-bottom: 0px;
    }

    .el-collapse-item__content {
      padding-bottom: 0px;
    }
  }
}
</style>
