<template>
  <div class="ds-question-formwrap ds-question-item">
    <div class="example">
      <el-form label-position="top">
        <el-form-item prop="value" :label="exampleLabel" :rules="exampleRules">
          <el-rate :max="form.starLength" :show-text="form.showText"></el-rate>
        </el-form-item>
      </el-form>
    </div>
    <el-form
      ref="form"
      :model="form"
      :rules="rules"
      class="form"
      label-width="auto"
      label-position="left"
    >
      <el-row :gutter="15">
        <el-col :span="24">
          <el-form-item prop="questionBody" label-width="0">
            <el-input v-model="form.questionBody" placeholder="请输入题干"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="starLength" label="星星数量:">
            <el-input-number v-model="form.starLength" :min="1"></el-input-number>
          </el-form-item>            
        </el-col>
        <el-col :span="12">
          <el-form-item prop="readOnly" label="本题不允许填写:">
            <el-switch v-model="form.readOnly"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="visible" label="本题是否显示:">
            <el-switch v-model="form.visible"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="required" label="必填标识:">
            <el-switch v-model="form.required"></el-switch>
          </el-form-item>             
        </el-col>
        <el-col :span="12">
          <el-form-item prop="maxLength" label="显示评分对应星级:">
            <el-switch v-model="form.showText"></el-switch>
          </el-form-item>  
        </el-col>
        <el-col :span="24">
          <div class="ljsz">
            <span class="label">逻辑设置:</span>
            <span class="btn" @click="showRela">题目关联</span>
          </div>
        </el-col>
      </el-row>                                
    </el-form>
  </div>
</template>
<script>
import questionConfig from "_platform/dsf/mixins/questionConfig";
export default {
    mixins: [questionConfig],
    // eslint-disable-next-line vue/require-prop-types
    props: ["owner"],
    data() {
        return {
            form: {
                questionBody: '请输入题干',
                starLength: 5,
                readOnly: false,
                visible: true,
                required: true,
                showText: false,
            },
            rules: {
                questionBody: [{
                    required: true,
                    message: '请输入题干',
                }]
            }
        }
    },
    computed: {
      exampleLabel() {
        return `${this.owner.questionIndex}.${this.form.questionBody}`;
      },
      exampleRules() {
        return this.form.required ? [{ required: true, trigger: 'change' }] : null;
      }
    },
    methods: {
    },
}
</script>