<template>
  <div class="ds-question-formwrap">
    <div ref="UEditor" class="editor"></div>
  </div>
</template>
<script>
import questionConfig from "_platform/dsf/mixins/questionConfig";
export default {
    mixins: [questionConfig],
    // eslint-disable-next-line vue/require-prop-types
    props: ["owner"],
    data() {
        return {
            ueId: '',
            toolBar: [
              'fullscreen',
              'undo',
              'redo',
              'bold',
              'italic',
              'underline',
              'removeformat',
              'formatmatch',
              'pasteplain',
              'forecolor',
              'backcolor',
              'insertorderedlist',
              'insertunorderedlist',
              'selectall',
              'cleardoc',
              'rowspacingtop',
              'rowspacingbottom',
              'lineheight',
              'customstyle',
              'paragraph',
              'fontfamily',
              'fontsize',
              'indent',
              'justifyleft',
              'justifycenter',
              'justifyright',
              'justifyjustify',
              'link',
              'unlink',
              'anchor',
              'imagenone',
              'imageleft',
              'imageright',
              'imagecenter',
              'simpleupload',
              'insertimage',
              'insertvideo',
              'inserttable',
              'autotypeset'
            ]       
        }
    },
    mounted() {
      const id = dsf.uuid();
      this.$refs.UEditor.id = id;   //为div赋值id, 用于UEditor渲染
      this.ueId = id;

      const fileUrl = dsf.url.getWebPath("$/js/libs/UEditor/ueditor.config.js");
      const configUrl = dsf.url.getWebPath("$/js/libs/UEditor/ueditor.all.js");
      this.$http.importFiles([fileUrl, configUrl])
        .then(() => {
          this.renderEditor();
        })
        .catch(err => {
          dsf.layer.alert("加载UEditor.js文件出错");
        })          
    },
    methods: {
      renderEditor() {
        this.$ue && this.$ue.destroy();   //如果已经存在UEditor实例，则先销毁
        this.$ue = UE.getEditor(this.ueId, {
          autoHeightEnabled: true,
          toolbars: [this.toolBar],
          zIndex: 20000
        });
        this.$ue.ready(() => {
          this.$ue.setContent(this.owner.questionBody);   //给UEditor赋初始值
        })  
      },
     
    }
}
</script>