<template>
  <el-container class="tree-selector-edit">
    <el-header style="padding: 10px 0px; text-align: right;">
      <DsfButton @click="add">新增</DsfButton>
    </el-header>
    <el-main style="padding: 0px;">
      <el-table
        ref="table"
        class="ds-table"
        :border="true"
        :data="tableData"
        :row-class-name="tableRowClassName"
      >
        <el-table-column
          label="显示名称"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-input v-model="scope.row.title"></el-input>
          </template>
        </el-table-column>

        <el-table-column
          label="复选框模式"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-select
              v-model="scope.row.showCheckBox"
              placeholder="启用/不启用"
            >
              <el-option label="启用" value="1"></el-option>
              <el-option label="不启用" value="0"></el-option>
            </el-select>
          </template>
        </el-table-column>

        <el-table-column
          label="选择条件"
          :header-align="'center'"
          :align="'center'"
        >
          <template slot="header">
            <el-popover trigger="hover" placement="top">
              <p>支持表达式，严格区分值类型；比如:@[isExist(_type,1,2,'3')]</p>
              <div slot="reference" class="name-wrapper">
                <div>选择条件</div>
              </div>
            </el-popover>
          </template>
          <template v-slot:default="scope">
            <el-input v-model="scope.row.types" placeholder="无" />
          </template>
        </el-table-column>

        <el-table-column
          label="父子关联状态"
          :header-align="'center'"
          :align="'center'"
        >
          <template slot="header">
            <el-popover trigger="hover" placement="top">
              <p>
                选中一个节点后，父子节点的选中状态是否受当前节点选中状态影响
              </p>
              <div slot="reference" class="name-wrapper">
                <div>父子关联状态</div>
              </div>
            </el-popover>
          </template>
          <template v-slot:default="scope">
            <el-select
              v-model="scope.row.chooseOneNode"
              placeholder="关联/不关联"
            >
              <el-option label="关联" value="1"></el-option>
              <el-option label="不关联" value="0"></el-option>
            </el-select>
          </template>
        </el-table-column>

        <el-table-column
          label="初始化展开层级"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-input v-model="scope.row.defaultOpenSeries" placeholder="无" />
          </template>
        </el-table-column>

        <el-table-column
          v-if="isOuaSelect"
          label="数据类型"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-select v-model="scope.row.metaDataType" placeholder="请选择">
              <el-option label="本部门" value="2"></el-option>
              <el-option label="本单位" value="3"></el-option>
              <el-option label="组织架构" value="4"></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          v-else
          label="数据类型"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-select v-model="scope.row.metaDataType" placeholder="请选择">
              <el-option label="接口" value="0"></el-option>
              <el-option label="元数据组" value="1"></el-option>
            </el-select>
          </template>
        </el-table-column>

        <el-table-column
          label="数据详情"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <div v-if="scope.row.metaDataType * 1 > 1">
              <el-select
                v-model="scope.row.showType"
                placeholder="显示类型"
                title="选择显示类型：人员、部门，两者的请求接口不一样。"
              >
                <el-option label="人员类型" value="user"></el-option>
                <el-option label="部门类型" value="dept"></el-option>
              </el-select>

              <el-select
                v-if="
                  scope.row.metaDataType == '4' && scope.row.showType == 'user'
                "
                v-model="scope.row.showSubDept"
                placeholder="仅显示子机构"
                title="是否仅显示子机构"
              >
                <el-option label="仅显示子机构" value="1"></el-option>
                <el-option label="不仅显示子机构" value="0"></el-option>
              </el-select>

              <el-input
                v-if="scope.row.metaDataType == '4'"
                v-model="scope.row.showLevel"
                title="组织架构显示层级（0代表子机构）"
              >
                <template slot="prepend">显示第</template>
                <template slot="append">级</template>
              </el-input>
            </div>

            <el-input
              v-else-if="scope.row.metaDataType != '1'"
              v-model="scope.row.sqlUrl"
              :disabled="scope.row.metaDataType != '0'"
              placeholder="支持表达式"
              title="数据请求地址"
            />

            <el-button v-else type="text" @click="setChooseMetaData(scope)">
              选择元数据组
            </el-button>
          </template>
        </el-table-column>

        <!-- <el-table-column
          label="异步加载"
          :header-align="'center'"
          :align="'center'"
        >
          <template v-slot:default="scope">
            <el-select v-model="scope.row.async" placeholder="启用/不启用">
              <el-option label="启用" value="1"></el-option>
              <el-option label="不启用" value="0"></el-option>
            </el-select>
          </template>
        </el-table-column>-->

        <el-table-column
          label="操作"
          :header-align="'center'"
          :align="'center'"
          :width="120"
        >
          <template v-slot:default="scope">
            <DsfButton
              :size="'small'"
              title="插入"
              icon="hao"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="insert(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title="删除"
              icon="guanbi2"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="deleted(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title="上移"
              icon="shang"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="up(scope)"
            ></DsfButton>
            <DsfButton
              :size="'small'"
              title="下移"
              icon="xia"
              btn-style="icon-text"
              style="min-width: auto; margin-left: 5px;"
              @click="down(scope)"
            ></DsfButton>
          </template>
        </el-table-column>
      </el-table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "TreeSelectorEdit",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: [],
      names: [],
      isOuaSelect: false,
    };
  },
  created() {
    if (this.config.params && this.config.params.type == "checkNode") {
      this.isOuaSelect = true;
    }
    this.tableData = this.currentData || [];
  },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      let res = "";
      if (row.title) {
        this.names[rowIndex] = "";
        let i = this.names.indexOf(row.title);
        if (i != -1 && i < rowIndex) {
          res = "warning-row";
        }
        this.names[rowIndex] = row.title;
      }
      return res;
    },
    newTab() {
      return {
        title: "Tab" + (this.tableData.length + 1),
        showCheckBox: "0",
        types: "",
        chooseOneNode: "1",
        defaultOpenSeries: "1",
        // metaDataType: "1",
        metaDataType: this.isOuaSelect ? "2" : "0",
        showType: "user",
        showSubDept: "1",
        showLevel: "0",
        chooseMetaData: {},
        sqlUrl: "",
        async: "0",
        controls: [],
        name: dsf.uuid(),
        error: false,
      };
    },
    add() {
      // 新增
      this.tableData.push(this.newTab());
    },
    insert(scope) {
      // 插入
      this.tableData.splice(scope.$index + 1, 0, this.newTab());
    },
    deleted(scope) {
      dsf.array.remove(this.tableData, scope.row);
    },
    up(scope) {
      if (scope.$index > 0) {
        let curr = this.tableData[scope.$index];
        let currIndex = scope.$index;
        dsf.array.remove(this.tableData, curr);
        this.tableData.splice(currIndex - 1, 0, curr);
      }
    },
    down(scope) {
      if (this.tableData.length - 1 > scope.$index) {
        let curr = this.tableData[scope.$index];
        let currIndex = scope.$index;
        dsf.array.remove(this.tableData, curr);
        this.tableData.splice(currIndex + 1, 0, curr);
      }
    },
    validate() {
      for (let i in this.tableData) {
        this.tableData[i]["title"] = this.tableData[i]["title"] || dsf.uuid();
        let index = this.names.indexOf(this.tableData[i]["title"]);
        if (index != -1 && index < i) {
          dsf.layer.message("请解决重复名称", false);
          return false;
        }
      }
      return true;
    },
    yes() {
      return this.tableData;
    },
    setChooseMetaData(scope) {
      const self = this;
      dsf.layer.openDialog({
        title: "请选择",
        fullscreen: false,
        width: "1000px",
        overflow: "hidden",
        content: "TreeDataSource",
        params: {
          chooseMetaData: self['tableData'][scope.$index].chooseMetaData,
        },
        btns: [
          {
            text: "确定",
            handler: (component) => {
              let status = component.validate();
              if (status) {
                self['tableData'][scope.$index].chooseMetaData = component.yes();
              } else {
                return false;
              }
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
  },
};
</script>
