<template>
  <div class="send-condition">
    <div class="editor">
      <EventSetting ref="event" :language="language" :code="config.Condition.Expression"></EventSetting>
    </div>

    <div class="table">
      <SelectTable :data="config.Condition.Switch.ItemValue" :columns="columns"></SelectTable>
    </div>
  </div>
</template>
<script>
import SelectTable from "./selectTable";
export default {
  name: "SendCondition",
  components: {
    SelectTable,
  },
  props: {
    lines: {
      type: Array,
      default() {
        return [];
      },
    },
    checked: {
      type: Object,
      default() {
        return {};
      },
    },
    value: {
      type: Object,
      default() {
        return {};
      },
    },
    code: {
      type: String,
      default: "",
    },
    language: {
      type: String,
      default: "javascript",
    },
  },
  data() {
    return {
      config: {
        IsEnabled: "1",
        Condition: {
          Expression: "",
          Switch: { ItemValue: [] },
        },
      },
      selectNodes: [],
      columns: [
        {
          prop: "LineName",
          label: "连线名称",
        },
        {
          prop: "Type",
          label: "条件",
          width: "140",
          options: [
            {
              label: "等于",
              value: 0,
            },
            {
              label: "包含",
              value: 1,
            },
            {
              label: "被包含",
              value: 2,
            },
            {
              label: "大于",
              value: 3,
            },
            {
              label: "大于等于",
              value: 4,
            },
            {
              label: "小于",
              value: 5,
            },
            {
              label: "小于等于",
              value: 6,
            },
            {
              label: "范围",
              value: 7,
            },
            {
              label: "有效",
              value: 8,
            },
          ],
        },
        {
          prop: "Value",
          label: "值",
          fieldType: "input",
        },
      ],
    };
  },
  created() {
    this.initTabel();
  },
  mounted() {},
  methods: {
    getContent() {
      this.config.Condition.Expression = this.$refs.event.getContent();
      return this.config;
    },
    initTabel() {
      const { Condition } = this.value;
      let ItemValue;
      if (Condition && Condition.Switch && Condition.Switch.ItemValue) {
        ItemValue = Condition.Switch.ItemValue;
      }
      if (Condition && Condition.Expression) {
        this.config.Condition.Expression = Condition.Expression;
      }
      // 初始化已保存数据
      let mapValues = {};
      if (ItemValue && ItemValue.length > 0) {
        ItemValue.forEach((item) => {
          mapValues[item.LineID] = item;
        });
      }
      let data = this.config.Condition.Switch.ItemValue;
      const { out } = this.checked;
      if (out && out.length > 0) {
        out.forEach((id) => {
          this.lines.forEach((line) => {
            if (line.id === id) {
              const item = mapValues[line.id];
              const attr = {
                Value: (item && item.Value) || "",
                Type: (item && item.Type) || 8,
                LineID: line.id,
                LineName: line.name,
              };
              data.push(attr);
            }
          });
        });
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.send-condition {
  position: relative;
  .editor {
    width: 100%;
    height: 354px;
  }
  /deep/.el-input-number {
    width: 166px;
    margin-right: 10px;
  }
  .table {
    padding: 10px 0;
  }
}
</style>
