
<template>
  <el-table
    ref="multipleTable"
    :data="data"
    :header-align="'center'"
    :align="'center'"
    height="200"
    border
    tooltip-effect="dark"
    style="width: 100%;"
    @selection-change="selectTable"
  >
    <template
      v-for="({
                type,
                isSelect,
                prop,
                label,
                align,
                width,
                options,
                operate,
                fieldType,
              },
              index) in columns"
    >
      <el-table-column
        v-if="!options && !operate && !fieldType && !isSelect"
        :key="prop"
        :type="type"
        :prop="prop"
        :label="label"
        :width="width"
        :align="align ? align : 'center'"
      ></el-table-column>
      <el-table-column
        v-if="options"
        :key="prop"
        :type="type"
        :prop="prop"
        :label="label"
        :width="width"
        align="center"
      >
        <template slot-scope="scope">
          <el-select
            v-model="scope.row[prop]"
            placeholder="请选择"
            class="level"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </template>
      </el-table-column>
      <el-table-column
        v-if="operate"
        :key="prop"
        :type="type"
        :prop="prop"
        :label="label"
        :width="width"
        align="center"
      >
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="deleteRow(scope.row)">
            删除
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        v-if="fieldType && fieldType === 'input'"
        :key="prop"
        :type="type"
        :prop="prop"
        :label="label"
        :width="width"
        align="center"
      >
        <template slot-scope="scope">
          <el-input
            v-model="scope.row[prop]"
            placeholder="请输入内容"
          ></el-input>
        </template>
      </el-table-column>
      <el-table-column
        v-if="fieldType && fieldType === 'editInput'"
        :key="prop"
        :type="type"
        :prop="prop"
        :label="label"
        :width="width"
        align="center"
      >
        <template slot-scope="scope">
          <span
            v-if="!scope.row[prop + 'Edit']"
            class="edit-filed"
            @click="scope.row[prop + 'Edit'] = true"
          >
            {{ scope.row[prop] }}
          </span>
          <el-input
            v-if="scope.row[prop + 'Edit']"
            v-model="scope.row[prop]"
            v-focus
            placeholder="请输入内容"
            @blur="scope.row[prop + 'Edit'] = false"
            @keyup.enter="scope.row[prop + 'Edit'] = false"
          ></el-input>
        </template>
      </el-table-column>
      <el-table-column
        v-if="isSelect"
        :key="prop"
        :type="type"
        :prop="prop"
        :label="label"
        :width="width"
        :align="align ? align : 'center'"
      >
        <template slot="header" slot-scope="scope">
          <span>{{ label }}&nbsp;&nbsp;</span>
          <el-checkbox
            v-model="columns[index][prop + 'All']"
            :indeterminate="columns[index].isIndeterminate"
            @change="
              checkAllChange(
                columns[index][prop + 'All'],
                prop,
                columns[index],
                scope
              )
            "
          ></el-checkbox>
        </template>
        <template slot-scope="scope">
          <el-checkbox
            v-model="scope.row[prop]"
            @change="selectRow(scope.row[prop], prop, columns[index])"
          ></el-checkbox>
        </template>
      </el-table-column>
    </template>
  </el-table>
</template>
<script>
/* eslint-disable vue/html-indent */
export default {
  name: "SelectTree",
  props: {
    data: {
      type: Array,
      default() {
        return [];
      },
    },
    columns: {
      type: Array,
      default() {
        return [];
      },
    },
    select: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {};
  },
  created() {},
  mounted() {
    if (this.select && this.select.length > 0) {
      this.select.forEach((row) => {
        this.$refs.multipleTable.toggleRowSelection(row);
      });
    }
    // this.$refs.multipleTable.toggleRowSelection(this.data, true);
  },
  methods: {
    selectTable(selection) {
      this.$emit("update:select", selection);
    },
    deleteRow(item) {
      var index = this.data.indexOf(item);
      if (index >= 0) {
        this.data.splice(index, 1);
      }
      this.$parent.deleteRow(item);
    },
    checkAllChange(val, prop, column) {
      this.data.forEach((item) => {
        item[prop] = val;
      });
      column.isIndeterminate = false;
    },
    selectRow(val, prop, column) {
      const filter = this.data.filter((item) => item[prop]);
      const length = filter.length;
      column[prop + "All"] = length === this.data.length;
      column.isIndeterminate = length > 0 && length < this.data.length;
    },
  },
};
</script>
<style lang="scss" scoped>
body .el-table th.gutter {
  display: table-cell !important;
}
body .el-table colgroup.gutter {
  display: table-cell !important;
}
.el-table {
  /deep/ th,
  /deep/ td {
    padding: 0;
  }

  /deep/ .el-input {
    height: 30px;
    line-height: 30px;
    .el-input__inner,
    .el-input__icon {
      height: 24px;
      line-height: 24px;
      font-size: 12px;
      background-color: transparent;
    }
  }
  .el-select {
    /deep/ .el-input__inner,
    .el-input__icon {
      height: 30px;
      line-height: 30px;
      border: none;
    }
  }
  .edit-filed {
    display: block;
    width: 100%;
    height: 30px;
    line-height: 30px;
  }
}
</style>
