<template>
  <el-container style="height:100%">
    <el-main style="padding:0px">
      <el-form ref="form" :inline="true" :model="formData" label-width="100px">
        <!-- <el-form-item label="匹配方式">
          <el-select v-model="formData.operator">
            <el-option label="等于" value="eq"></el-option>
            <el-option label="包含" value="in"></el-option>
            <el-option label="匹配" value="like"></el-option>
            <el-option label="匹配开始" value="slike"></el-option>
            <el-option label="匹配结束" value="elike"></el-option>
            <el-option label="区间" value="range"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="依赖">
          <el-select v-model="formData.rel">
            <el-option :label="'无'" :value="''"></el-option>
            <el-option v-for="(op,$index) in sitems" :key="$index" :label="op.fieldCaption" :value="op.id"></el-option>
          </el-select>
        </el-form-item> -->
      </el-form>
      <el-tabs v-model="activeName">
        <el-tab-pane label="静态数据" name="static">
          <div style="height:400px;overflow:auto">
            <div style="margin-bottom:5px;float:right">
              <dsf-button size="small" icon="hao" @click="addRow">增加</dsf-button>
              <dsf-button size="small" icon="guanbi2" type="auxiliary">清空</dsf-button>
            </div>
            <el-table :data="values.static" border>
              <el-table-column label="名称">
                <template v-slot:default="scope">
                  <el-input v-model="scope.row.text" placeholder="选项文本"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="值">
                <template v-slot:default="scope">
                  <el-input v-model="scope.row.value" placeholder="多个值用;分割"></el-input>
                </template>
              </el-table-column>
              <el-table-column label="操作">
                <template v-slot:default="scope">
                  <dsf-button title="删除" size="small" btn-style="icon-text" icon="guanbi2" @click.stop="remove(scope.row)"></dsf-button>
                  <dsf-button title="上移" size="small" btn-style="icon-text" icon="shang" @click.stop="up(scope.row)"></dsf-button>
                  <dsf-button title="下移" size="small" btn-style="icon-text" icon="xia" @click.stop="down(scope.row)"></dsf-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-tab-pane>
        <el-tab-pane label="数据字典" name="dict">
          <div style="height:400px;overflow:auto">
            <DesDictDataSelect v-model="values.dict"></DesDictDataSelect>
          </div>
        </el-tab-pane>
        <el-tab-pane label="数据源" name="dbsource">
          <div style="height:400px;overflow:auto">
            <DesDbDataSelect v-model="values.dbsource"></DesDbDataSelect>
          </div>
        </el-tab-pane>
        <el-tab-pane label="接口" name="interface">
          <el-input v-model="values.interface" type="textarea" :rows="5" placeholder="请输入接口地址"></el-input>
        </el-tab-pane>
      </el-tabs>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from '_platform/dsf/mixins/propertyDialogSetting';


export default {
  name: 'DesSuperSearchEnumItems',
  mixins: [propertyDialogSetting],
  props: {
    'item': {
      type: Object,
      default() {
        return {}
      }
    },
    'currentData': {
      type: Object,
      default() {
        return null;
      }
    },
    'searchItems': {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      formData: {
        rel: "",
        operator: "eq"
      },
      activeName: "static",
      values: {
        static: [],
        dict: { key: null, node: null },
        dbsource: { key: null, node: null },
        interface: ""
      }
    }
  },
  computed: {
    sitems() {
      return _.filter(this.searchItems, (v) => {
        return v.id !== this.item.id
      });
    }
  },
  created() {
    let data = dsf.mix(true, {}, this.currentData);
    if (!dsf.isEmptyObject(data)) {
      this.formData.rel = data.rel;
      this.formData.operator = data.operator;
      this.values[data.type] = data.value;
      this.activeName = data.type || "static";
    }

  },
  mounted() {
  },
  methods: {
    yes() {
      return {
        operator: this.formData.operator,
        rel: this.formData.rel,
        type: this.activeName,
        value: this.values[this.activeName]
      }
    },
    addRow() {
      this.values.static.push({})
    },
    remove(row) {
      dsf.array.remove(this.values.static, row);
    },
    up(row) {
      let index = _.findIndex(this.values.static, (r) => r == row);
      if (index > 0) {
        let targetRow = this.values.static[index - 1];
        this.$set(this.values.static, index - 1, row);
        this.$set(this.values.static, index, targetRow);
      }
    },
    down(row) {
      let index = _.findIndex(this.values.static, (r) => r == row);
      if (index < this.values.static.length - 1) {
        let targetRow = this.values.static[index + 1];
        this.$set(this.values.static, index + 1, row);
        this.$set(this.values.static, index, targetRow);
      }
    },
  }
}
</script>