<template>
  <el-container style="height: 100%; border: 1px solid #eee;">
    <el-aside width="40%" style="border-right: 1px solid #eee;">
      <div class="design-column-edit">
        <el-tree
          v-if="!isReload"
          ref="tree"
          :data="treeData"
          :highlight-current="true"
          :expand-on-click-node="false"
          :render-after-expand="true"
          :default-expand-all="true"
          node-key="id"
          :props="defaultProps"
          @node-click="nodeClick"
        >
          <span slot-scope="args" class="custom-tree-node">
            <span>
              {{ args.node.label }}
              <span style="margin-left: 5px;">{{ args.data.hide ? "[隐藏]" : "" }}</span>
            </span>
            <span>
              <dsf-button title="添加子项" size="small" btn-style="icon-text" icon="hao" @click.stop="append(args.data)"></dsf-button>
              <template v-if="!args.data.isRoot">
                <dsf-button title="删除" size="small" btn-style="icon-text" icon="guanbi2" @click.stop="remove(args.node)"></dsf-button>
                <dsf-button title="上移" size="small" btn-style="icon-text" icon="shang" @click.stop="up(args.node)"></dsf-button>
                <dsf-button title="下移" size="small" btn-style="icon-text" icon="xia" @click.stop="down(args.node)"></dsf-button>
              </template>
              <template v-else>
                <dsf-button title="删除" size="small" btn-style="icon-text" icon="guanbi2" style="color: #c0c0c0;"></dsf-button>
                <dsf-button title="上移" size="small" btn-style="icon-text" icon="shang" style="color: #c0c0c0;"></dsf-button>
                <dsf-button title="下移" size="small" btn-style="icon-text" icon="xia" style="color: #c0c0c0;"></dsf-button>
              </template>
            </span>
          </span>
        </el-tree>
      </div>
    </el-aside>
    <el-main>
      <el-form ref="form" :model="form" label-width="120px">
        <el-form-item label="列名称">
          <el-input v-model="form.label" :disabled="form.readOnly"></el-input>
        </el-form-item>
        <el-form-item label="表头对齐方式">
          <el-select v-model="form.headerAlign" class="select-full" :disabled="form.readOnly">
            <el-option label="居左" value="left"></el-option>
            <el-option label="居右" value="right"></el-option>
            <el-option label="居中" value="center"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="数据对齐方式">
          <el-select v-model="form.align" class="select-full" :disabled="form.readOnly">
            <el-option label="居左" value="left"></el-option>
            <el-option label="居右" value="right"></el-option>
            <el-option label="居中" value="center"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="宽度">
          <el-input v-model="form.width" :disabled="form.readOnly"></el-input>
        </el-form-item>
        <el-form-item label="是否隐藏">
          <el-switch v-model="form.hide" :disabled="form.readOnly"></el-switch>
        </el-form-item>
      </el-form>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";

export default {
  name: "DesColumnsEdit",
  mixins: [propertyDialogSetting],
  data() {
    return {
      isReload: false,
      tableData: [],
      columns: [],
      data: [],
      treeData: [
        {
          id: dsf.uuid(),
          label: "表头",
          isRoot: true,
          level: -1,
          children: []
        }
      ],
      defaultProps: {
        children: "children",
        label: "label"
      },
      form: null,
      formNode: null,
      formDisabled: {
        label: "",
        align: "center",
        headerAlign: "center",
        hide: false,
        width: "",
        readOnly: true
      }
    };
  },
  watch: {
    "form.hide": {
      handler(v) {
        let data = this.form;
        if (data && !data.readOnly) {
          if (this.formNode && data.level > 0) {
            let hide = this.formNode.parent.data.hide;
            if (hide && hide != v) {
              dsf.layer.pc.message("父列为隐藏状态时子列不可设置显示状态", false);
              data.hide = hide;
              return;
            }
          }
          this.deep(data, c => {
            c.hide = v;
          });
        }
      }
    }
  },
  created() {
    this.form = this.formDisabled;
  },
  mounted() {
    if (this.owner) {
      let c = this.owner.$$getComponent();
      let props = dsf.mix(true, {}, c.columns);
      this.treeData[0].children.push(...this.getTreeData(props, 0));
    }
  },
  methods: {
    getTreeData(columns, level) {
      let tree = [];
      _.each(columns, (c, i) => {
        let obj = dsf.mix({}, getColumnObjectBase(), {
          id: level + "_" + i,
          level: level,
          label: c.label,
          columnId: c.columnId,
          headerAlign: c.headerAlign,
          align: c.align,
          hide: c.hide,
          width: c.width
        });
        tree.push(obj);
        let children = this.getTreeData(c.children, level + 1);
        obj.children = children;
      });
      return tree;
    },
    deep(node, callback, level) {
      callback && callback(node, level);
      if (node.children) {
        _.each(node.children, cnode => {
          this.deep(cnode, callback, level + 1);
        });
      }
    },
    append(parent) {
      let index = null,
        parentNode = null;
      if (parent) {
        parentNode = parent;
      } else {
        parentNode = this.treeData[0];
      }
      index = parentNode.children.length + 1;
      let newColumn = dsf.mix({}, getColumnObjectBase(), {
        id: parent.level + "_" + index,
        label: "新建列",
        level: parentNode.level + 1,
        hide: parentNode.hide
      });
      parentNode.children.push(newColumn);
    },
    remove(node) {
      let parentNode = node.parent;
      dsf.array.remove(parentNode.data.children, node.data);
    },
    up(node) {
      sort.call(this, node, "up");
    },
    down(node) {
      sort.call(this, node, "down");
    },
    nodeClick(data, Node) {
      if (!data.isRoot) {
        this.form = data;
        this.formNode = Node;
      } else {
        this.form = this.formDisabled;
        this.formNode = null;
      }
    },
    yes() {
      let s = this.treeData[0].children;
      return s;
    }
  }
};
function sort(node, type) {
  let parentNode = node.parent;
  let index = parentNode.childNodes.indexOf(node);
  let mark = type == "up" ? index > 0 : index <= parentNode.childNodes.length - 1;
  if (mark) {
    let targetIndex = type == "up" ? index - 1 : index + 1;
    let target = parentNode.childNodes[targetIndex];
    this.$set(parentNode.data.children, targetIndex, node.data);
    this.$set(parentNode.data.children, index, target.data);
    this.$set(parentNode.childNodes, targetIndex, node);
    this.$set(parentNode.childNodes, index, target);
  }
}
function getColumnObjectBase() {
  let columnObject = {
    id: "",
    level: 0,
    label: "",
    headerAlign: "center",
    align: "center",
    columnId: "",
    children: [],
    hide: false,
    resizable: false
  };
  return dsf.mix({}, columnObject);
}
</script>
