      <div v-if="examInfoData.remark" class="exam-remark">{{ examInfoData.remark }}</div>
      <template v-for="(data, index) in examInfoData.questions">
        <h3 :key="data.type+index" class="ds-font3">{{ dsf.math.toChinesNum(++index)+'、'+ examTitle[data.type] + (data.score && data.score.total ? ' (共' + data.score.total + '分)' : '') }}</h3>
        <component :is="componentIs[data.type]" :key="data.type+index" :res-data="data.subjects" :exam-mark-active="examMarkActive" />
      </template>
    </el-main>
    <el-aside class="ds-exam-aside" :style="`width: ${asideWidth}px`">
      <div class="aside-content" :style="`width: ${asideWidth}px`">
