          <i class="iconfont icon-dui"></i>
          <p>提交试卷</p>
        </div>
        <div class="ds-exam-time-info">
          <div class="ds-exam-score">
            <!-- exam-result -->
            <span class="ds-font4">{{ !examInfoData.answer && examInfoData.state === 3 ? '剩余时间' : '成绩' }}</span>
            <span
              v-if="!examInfoData.answer && examInfoData.state === 3"
              class="ds-font2"
              :class="{ 'exam-result': examInfoData.answer}"
            >{{ getTime }}</span>
            <span
              v-else
              class="ds-font2 exam-result"
            >{{ examResult ? (examResult.examScore || '正在阅卷中') : examInfoData.state == 1 ? '待' : examInfoData.state == 2 ? '已阅卷' : '待阅卷' }}</span>
          </div>
          <div class="ds-exam-score">
            <span class="ds-font4">当前进度</span>
            <span class="ds-font2">2/{{ speedLength }}</span>
          </div>
        </div>
        <div class="ds-exam-card">
          <p class="title">答题卡</p>
          <div class="content">
            <ul v-for="(data, index) in examInfoData.questions" :key="data.type+index" class="lists">
              <li>
