    },
    // 处理试题 随机试题和题型随机
    loadExamSubject() {
      // 当前题型的 随机判断。我也不知道为什么有这个参数来 1.0 拷贝过来的
      let resRandom = this.examInfoData.random
      let questions = _.map(this.examInfoData.questions, (data) => {
        // 右侧总进度
        this.speedLength += data.subjects.length
        // 试题选择
        let optionsList = []
        // 试题列表 +  当前试题的长度大于二 就要把题目随机排序
        let questionList =  data.subjects.length > 1 &&  resRandom.question == '1' ? {...data, subjects: _.shuffle(data.subjects) } : data
        // 以下三种题型 需要把选项随机排序
        if( data.type == 'checkbox' || data.type  == 'radio' || data.type == 'judge'){
          optionsList = _.map(questionList.subjects, (item) => {
            return  {
              ...item,
              items: _.shuffle(item.items)
            }
          })
          return {
            ...questionList,
            subjects: optionsList
          }
        }
        return questionList
      });

      return {
        ...this.examInfoData,
        questions: questions
      }
       
    },
