      var modulo = totalSeconds % (60 * 60 * 24)
      //小时数
      var hours = Math.floor(modulo / (60 * 60))
      modulo = modulo % (60 * 60)
      //分钟
      var minutes = Math.floor(modulo / 60)
      //秒
      var seconds = modulo % 60
      //输出到页面
      hours = ((hours || 0) + 100).toString().substr(1)
      minutes = ((minutes || 0) + 100).toString().substr(1)
      seconds = ((seconds || 0) + 100).toString().substr(1)
      return hours + ':' + minutes + ':' + seconds
    },
    createExamTimer() {
      let zero = '00:00:00',
        seconds = this.examInfoData.duration || 0
      this.examTimer = window.setInterval(() => {
        if (!this.isTiming) {
          return
        }
        seconds--
        // 控制提交按钮的显示
        if (this.examInfoData.allowTime === 0 && this.examInfoData.state === 3) {
             this.getAllowSubmit = false
        } else {
          this.examInfoData.allowTime--
        }
        // 300秒后自动暂存
        if (++this.countDown === 10) {
          this.saveExamHold()
        }

        if (seconds < 30 && seconds > 1) {
          dsf.layer.message('请注意考试时间还剩余' + seconds + '秒', false);
        }else if(seconds <= 0){
          seconds = 0
          window.clearInterval(this.examTimer)
          this.getAllowSubmit = true
          console.log('时间到，强制交卷');
        }

        this.getTime = this.timeDown(seconds)
        // 判断如果注意事项弹出 暂停时间
        if(this.popClose){ this.isTiming = false }
      }, 1000)


      
