    // 绑定浏览器切换窗口事件 
    bindWindowEvent() {
      // 当前没有提交过试卷无回答数据
      if( !this.examInfoData.answer ){
        let isChange = 0
        $(document).on('visibilitychange', () => {
          if (document.hidden && document.visibilityState == 'hidden') {
            isChange++
            if (isChange > 5) {
              this.formSave()
            } else {
              dsf.layer.alert({
                title: "温馨提示",
                message: "考生跳出答卷页面次数不能超过5次，否则会保存并自动交卷，当前跳出次数为"+isChange+"次。",
                showClose: false
              })
              if(isChange === 1){
                this.isTiming = false
              }
            }
          }
        })
      }
    },
    //分钟转秒
    minutesToSeconds(m) {
      let currentDate = new Date()
      var endDate = new Date(currentDate.getTime() + m * 60 * 1000)
      var totalSeconds = parseInt((endDate - currentDate) / 1000)
      return totalSeconds
