<template>
  <el-container>
    <el-header style="padding:10px 0;text-align:right">
      <DsfButton @click="addRow">新增行</DsfButton>
      <DsfButton @click="addCol">新增列</DsfButton>
    </el-header>
    <el-main style="padding:0">
      <table class="table">
        <tr>
          <td style="width:100px;text-align:center">
            <div class="size-title">
              宽度\高度
            </div>
          </td>
          <td v-for="$index of colNum" :key="$index">
            <dsf-textbox v-model="widths[$index-1]" :show-label="false"></dsf-textbox>
          </td>
        </tr>
        <tr v-for="(row,$index) in tableData" :key="$index">
          <td>
            <dsf-textbox v-model="heights[$index]" :show-label="false"></dsf-textbox>
          </td>
          <td v-for="(cell,$cellIndex) in row" :key="$cellIndex" style="text-align:center">
            <!-- <div style="padding:8px">
              <el-select v-model="row[$cellIndex]" style="width:100%" @change="areaChange($index,$cellIndex,$event)">
                <el-option v-for="op in areas" :key="op.value" :label="op.text" :value="op.value"></el-option>
              </el-select>
            </div> -->
          </td>
        </tr>
      </table>
    </el-main>
  </el-container>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesGridLayout",
  mixins: [propertyDialogSetting],
  data() {
    return {
      widths: [],
      heights: [],
      tableData: []
    }
  },
  computed: {
    rowNum() {
      return this.tableData.length;
    },
    colNum() {
      if (this.tableData.length) {
        return this.tableData[0].length;
      }
      else {
        return 0;
      }
    },
    areas() {
      let areas = [];
      let temp1 = _.flattenDeep(this.tableData);
      let temp2 = _.flattenDeep(this.currentData.matrix);
      areas = temp1.concat(temp2);
      areas = _.uniq(areas);
      areas = areas.sort((v1, v2) => {
        if (v1 < v2) {
          return -1;
        }
        return 1;
      })
      areas = _.map(areas, (index) => {
        return {
          text: "区域" + index,
          value: index
        }
      });
      return areas;
    }
  },
  created() {
    // let layout = this.currentData;
    // layout = dsf.mix(true, [], layout);
    this.tableData = this.owner.layout.matrix;
    this.widths = dsf.mix(true, [], this.owner.layout.widths);
    this.heights = dsf.mix(true, [], this.owner.layout.heights);
  },
  beforeMount() {
  },
  mounted() {
  },
  methods: {
    rowClickHandler() {
      // let c = row;
      // _.each(this.sizeMap, (el) => {
      //   if (!row[el.size]) {
      //     row[el.size] = {
      //       height: '',
      //       span: ''
      //     }
      //   }
      // });
      // this.form = c;
    },
    addCol() {
      let table = [];
      for (let i = 0; i < this.rowNum; i++) {
        let tr = [];
        for (let j = 0; j <= this.colNum; j++) {
          if (j == this.colNum) {
            tr.push(i * (this.colNum + 1) + j + 1);
          } else {
            let oldIndex = this.tableData[i][j];
            let c = (oldIndex - 1) % this.colNum;
            let r = (oldIndex - c - 1) / this.colNum;
            tr.push(r * (this.colNum + 1) + c + 1);
          }
        }
        table.push(tr);
      }
      this.widths = new Array(this.colNum + 1).fill(
        dsf.pxToRem(+(100 / (this.colNum + 1)).toFixed(3) + "%")
      );
      this.tableData = table;
    },
    addRow() {
      let arr = [];
      for (let i = 1; i <= this.colNum; i++) {
        arr.push(this.colNum * this.rowNum + i);
      }
      this.tableData.push(arr);
      this.heights.push('46px');
    },
    vaildate() {
      if (data.length == 0) {
        dsf.layer.pc.message("至少存在一格", false);
        return false
      }
      return true;
    },
    deleted(scope) {
      if (scope.row.slots[0].controls.length > 0) {
        let controls = scope.row.slots[0].controls
        dsf.layer.pc.confirm("栅格中存在" + controls.length + "个子组件,删除该单元格子组件将一起被删除。<br/>是否确定要继续?").then(() => {
          dsf.array.remove(this.tableData, scope.row)
        }).catch(() => { })
      } else {
        dsf.array.remove(this.tableData, scope.row)
      }
    },
    areaChange(rowIndex, colIndex, area) {
      console.log(rowIndex, colIndex, area)
    },
    yes() {
      // let data = this.tableData;
      this.owner.layout.widths = this.widths;
      this.owner.layout.heights = this.heights
    },
    checkMaxSpan(row, key) {
      row[key] = dsf.isNumber(row[key]) ? Number(row[key]) : null;
      if (row[key] && row[key] > 24) {
        row[key] = 24;
      }
      if (row[key] && row[key] < 0) {
        row[key] = null;
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.table {
  width: 100%;
  height: 100%;
  border-left: 1px solid #e6e6e6;
  border-top: 1px solid #e6e6e6;
  border-collapse: collapse;
  td {
    border-right: 1px solid #e6e6e6;
    border-bottom: 1px solid #e6e6e6;
    height: 60px;
  }
  .size-title {
    position: relative;
    font-weight: 700;
  }
}
</style>