<template>
  <el-tabs v-model="tabIndex" class="ds-enum-editor" type="border-card">
    <el-tab-pane name="1" label="数据字典">
      <enum-editor-item v-model="value1" type="dict" :tree-loading="loading[0]" :tree-data="treeData1" :default-checked-key="defaultCheckedKey1" :table-post="tablePost1" empty-text="请选择数据字典" />
    </el-tab-pane>
    <el-tab-pane name="2" label="数据源">
      <enum-editor-item v-model="value2" type="db" :tree-loading="loading[1]" :tree-data="treeData2" :default-checked-key="defaultCheckedKey2" :table-post="tablePost2" empty-text="请选择数据源" />
    </el-tab-pane>
    <el-tab-pane class="ds-enum-editor-item3" name="3" label="静态数据">
      <el-input v-model="value3" type="textarea" :rows="2" resize="none" placeholder="支持格式：'文本'、'文本|值'两种格式，多个选项用回车符换行" />
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import EnumEditorItem from './item/enumEditorItem';

export default {
  name: 'DesEnumEditor',
  components: {
    EnumEditorItem
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      tabIndex: '0',
      loaded: [false, false, false],
      loading: [false, false],
      value1: {
        type: 'dict',
        code: null,
        attach: []
      },
      value2: {
        type: 'db',
        code: null,
        attach: []
      },
      value3: '',
      treeData1: [],
      treeData2: [],
      defaultCheckedKey1: '',
      defaultCheckedKey2: '',
      tablePost1: (id, callback) => {
        this.$http.get(
          'dict/getList/' + id
        ).done((data) => {
          if (data.success) {
            callback && callback(data.data);
          } else {
            dsf.layer.message('请求异常！', false);
          }
        })
      },
      tablePost2: (rmId, callback) => {
        this.$http.get(
          'meta/tree/dbSource', {
          rmId
        }
        ).done((data) => {
          if (data.success) {
            callback && callback(data.data);
          } else {
            dsf.layer.message('请求异常！', false);
          }
        })
      }
    }
  },
  watch: {
    'tabIndex'(to) {
      if (!this.loaded[to - 1] && this['postData' + to]) {
        this['postData' + to]();
      }
      this.$set(this.loaded, parseInt(to) - 1, true);
    }
  },
  created() {
    this.currentData && this.init(this.currentData);
  },
  methods: {
    init({ type, attach, code }) {
      if (type === 'static') {
        if (!code || !code.length) this.value3 = '';
        else {
          this.value3 = code.map(v => {
            if (v.text === v.value) {
              return v.text;
            }
            return v.text + '|' + v.value;
          }).join('\n');
        }
        this.tabIndex = '3';
        this.$set(this.loaded, 2, true);
      } else {
        // const data = dsf.mix(true, []);
        if (type === 'dict') {
          this.value1 = { type, attach: [], code };
          this.defaultCheckedKey1 = code;
          this.tabIndex = '1';
        } else if (type === 'db') {
          this.value2 = { type, attach: [], code };
          this.defaultCheckedKey2 = code;
          this.tabIndex = '2';
        }
      }
    },
    yes() {
      if (this.tabIndex === '3') {
        const nv = this.value3.trim().split('\n');
        return {
          type: 'static',
          code: nv.map(v => {
            v = v.split('|');
            return {
              text: v[0],
              value: v[1] || v[0]
            }
          })
        }
      } else {
        let v = this['value' + this.tabIndex];
        delete v.attach;
        return v;
      }
    },
    postData1() {
      this.$set(this.loading, 0, true);
      this.$http.get(
        '/rm/list/dataDic'
      ).done((data) => {
        if (data.success) {
          this.treeData1 = data.data.map(v => this.dataFormat1(v));
        } else {
          dsf.layer.message('请求异常！', false);
        }
      }).always(() => {
        this.$set(this.loading, 0, false);
      });
    },
    postData2() {
      this.$set(this.loading, 1, true);
      this.$http.get('/rm/list/ds', {
        namespace: ''
      }).done((data) => {
        if (data.success) {
          this.treeData2 = data.data.map(v => this.dataFormat2(v));
        } else {
          dsf.layer.message('请求异常！', false);
        }
      }).always(() => {
        this.$set(this.loading, 1, false);
      });
    },
    dataFormat1({ name, dsfa_rm_id, type_value, children }) {
      const obj = {
        label: name,
        ID: dsfa_rm_id,
        nocheck: type_value !== '5',
        iconSkin: ''
      };
      if (children) {
        obj.children = children.map(v => this.dataFormat1(v));
      } else if (obj.nocheck) {
        obj.iconSkin = '_special';
      }
      return obj;
    },
    dataFormat2({ name, ID, type_value, children }) {
      const obj = {
        label: name,
        ID,
        nocheck: type_value !== "3" && type_value !== "8",
        iconSkin: ''
      };
      if (children) {
        obj.children = children.map(v => this.dataFormat2(v));
      } else if (obj.nocheck) {
        obj.iconSkin = '_special';
      }
      return obj;
    }
  }
}
</script>