<template>
  <el-tabs v-model="tabIndex" class="ds-enum-editor" type="border-card" @tab-click="handleClick">
    <el-tab-pane class="ds-enum-editor-item3" label="用户管理" name="static">
      <el-input
        v-model="dataTwo"
        type="textarea"
        :rows="2"
        resize="none"
        placeholder="支持格式：'文本'、'文本|值'两种格式，多个选项用回车符换行"
      />
    </el-tab-pane>
    <el-tab-pane name="dict" label="数据字典" :lazy="true">
      <enum-editor-item
        v-model="dataOne"
        type="dict"
        :tree-loading="treeLoading"
        :tree-data="treeData"
        :default-checked-key="defaultCheckedKey"
        :table-post="tablePost"
        empty-text="请选择数据字典"
      />
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import EnumEditorItem from './item/enumEditorItem';

export default {
  name: 'DesExamEnumEditor',
  components: {
    EnumEditorItem
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      tabIndex: 'static',
      treeLoading: false,
      isTreeCheck: false,
      treeData: [],
      defaultCheckedKey: '',
      dataTwo: '',
      dataOne: {
        type: 'dict',
        code: null,
        attach: []
      },
      tablePost: (id, callback) => {
        this.$http.get(
          'dict/getList/' + id
        ).done((data) => {
          if (data.success) {
            callback && callback(data.data);
          } else {
            dsf.layer.message('请求异常！', false);
          }
        })
      },
    }
  },
  created() {
    this.currentData && this.init(this.currentData);
  },
  methods: {
     handleClick(tab, event) {
       if(tab.name === "dict" && !this.isTreeCheck && this.treeData) {
         this.getTreeData()
       }
    },
    init({ type, attach, code }) {
      if (type === 'static') {
        this.tabIndex = 'static';
        if(!attach){
          return false
        }
        this.dataTwo =  attach.map(v => {
          if (v.text === v.value) {
            return v.text;
          }
          return v.text + '|' + v.value;
        }).join('\n');
      }  else {
        this.dataOne = {
          type,
          code,
          attach
        }
        this.defaultCheckedKey = code;
        this.tabIndex = 'dict';
        this.getTreeData()
      }
    },
    yes() {
      if (this.tabIndex === 'static') {
        const temp = this.dataTwo.trim().split('\n');
        return {
          type: 'static',
          attach: temp == false ? [] : temp.map(v => {
            if(!_.isEmpty(v)){
              v = v.split('|')
              return {
                text: v[0],
                value: v[1] || v[0]
              }
            }
          })
        }
      } else {
        return this.dataOne
      }
    },
    getTreeData() {
      this.treeLoading = true
      this.$http.get(
        '/rm/list/dataDic'
      ).done((res) => {
        if (res.success) {
          this.isTreeCheck = true
          this.treeData = res.data.map(v => this.dataFormat(v));
        } else {
          dsf.error(err);
        }
      }).always(() => {
        this.treeLoading = false
      });
    },
    dataFormat({ name, dsfa_rm_id, type_value, children }) {
      const obj = {
        label: name,
        ID: dsfa_rm_id,
        nocheck: type_value !== '5',
        iconSkin: ''
      };
      if (children) {
        obj.children = children.map(v => this.dataFormat(v));
      } else if (obj.nocheck) {
        obj.iconSkin = '_special';
      }
      return obj;
    }
  }
}
</script>