<template>
  <enum-editor-item
    ref="meta"
    v-model="value1"
    type="meta"
    :tree-data="treeData1"
    :table-post="tablePost"
    :tree-loading="loading"
    empty-text="请选择元数据"
    name-key="name"
    id-key="treeinfo_globalid"
  />
</template>
<script>
import propertyDialogSetting from '_platform/dsf/mixins/propertyDialogSetting';
import EnumEditorItem from './item/enumEditorItem';

export default {
  name: 'DesAutoCompleteMD',
  components: {
    EnumEditorItem
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      loading: false,
      value1: {
        type: 'meta',
        code: '',
        attach: []
      },
      treeData1: [],
      tablePost: () => { }
    };
  },
  created() {
    if (this.currentData) {
      this.init(this.currentData);
    } else {
      this.postData1();
    }
  },
  methods: {
    init({ type, attach, code, chooseKey }) {
      const data = dsf.mix(true, [], attach);
      this.value1 = { type, attach: data, code, chooseKey };
      this.postData1();
    },
    validate() {
      this.$refs.meta.getResult();
      if (this.value1['code']) {
        return true;
      } else {
        dsf.layer.message('请选择元数据以及右侧具体节点名称字段', false);
        return false;
      }
    },
    yes() {
      const { code } = this.value1;
      code.id = code.value;
      return code;
    },
    postData1() {
      this.loading = true;
      this.$http
        .get('rm/list/meta', { namespace: '' })
        .done((data) => {
          if (!data.success) {
            dsf.layer.message('请求异常！', false);
            return
          }
          this.treeData1 = data.data.map(v => this.dataFormat(v));
          this.$nextTick(() => {
            this.$refs.meta.initMetaTable();
          });
        })
        .always(() => {
          this.loading = false;
        });
    },
    dataFormat(node) {
      if (node.type_value != '3' && node.type_value != '8') {
        node.nocheck = true;
      }
      if (node.type_value != '3' && node.type_value != '8' && !node.children) {
        node.iconSkin = '_special';
      }
      if (node.children) {
        node.children = node.children.map(v => this.dataFormat(v));
      }
      return node;
    }
  }
};
</script>
