<template>
  <el-table :data="tableData" :border="true" style="width: 100%;">
    <el-table-column prop="text" label="名称" width="150"></el-table-column>
    <el-table-column prop="value" label="宽度">
      <template v-slot:default="scope">
        <el-input v-model="scope.row.width" placeholder="auto" />
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesTableColumnsInfo",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: []
    };
  },
  mounted() {
    for (let i = 0; i < this.currentData.length; i++) {
      this.tableData.push({ text: "第" + (i + 1) + "列", width: this.currentData[i].width });
    }
  },
  methods: {
    yes() {
      let v = _.map(this.tableData, row => {
        if (dsf.isNumber(row.width)) {
          row.width = row.width + "px";
        }
        return { width: row.width };
      });
      return v;
    }
  }
};
</script>
