<template>
  <div class="control-filter-tree">
    <dsf-tree-select
      :tree-data="nodeList"
      :loading="loading"
      theme="theme1"
      node-key="dsfa_rm_id"
      node-name="name"
      search-control
      default-expand-all
      multiple
      @choose-node="checkNodeList = $event"
    />
  </div>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
import DsfTreeSelect from './item/treeSelect';

export default {
  name: "DesControlSelect",
  components: {
    DsfTreeSelect
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      loading: false,
      checkNodeList: [],
      nodeList: []
    };
  },
  created() {
    this.getDesignCtrl();
  },
  methods: {
    filterNode(value, nodeList) {
      if (!value) return true;
      return nodeList.name.indexOf(value) !== -1;
    },
    getDesignCtrl() {
      this.loading = true;
      let url = "/rm/list/control";
      this.$http.get(url).done((result) => {
        if (result.success) {
          this.nodeList = result.data;
        } else {
          dsf.layer.message("控件组获取" + result.message, false);
        }
      }).error((res) => {
        dsf.layer.message(res.message, false);
      }).finally(() => {
        this.loading = false;
      });
    }
  }
};
</script>
