<template>
  <el-tabs v-model="tabIndex" class="ds-enum-editor" type="border-card">
    <el-tab-pane name="1" label="元数据">
      <enum-editor-item
        ref="meta"
        v-model="value1"
        type="meta"
        :tree-data="treeData1"
        :default-checked-key="defaultCheckedKey1"
        :table-post="tablePost"
        :tree-loading="loading[0]"
        empty-text="请选择元数据"
        name-key="name"
        id-key="treeinfo_globalid"
      />
    </el-tab-pane>
    <el-tab-pane
      v-if="!(config && config.other && config.other.exclude == 'ds')"
      name="2"
      label="数据源"
    >
      <enum-editor-item
        v-model="value2"
        type="ds"
        :tree-data="treeData2"
        :default-checked-key="defaultCheckedKey2"
        :table-post="tablePost"
        :hide-right="true"
        :tree-loading="loading[1]"
        empty-text="请选择数据源"
        name-key="name"
        id-key="treeinfo_globalid"
      />
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import propertyDialogSetting from '_platform/dsf/mixins/propertyDialogSetting'
import EnumEditorItem from './item/enumEditorItem'

export default {
  name: 'TreeDataSource',
  components: {
    EnumEditorItem,
  },
  mixins: [propertyDialogSetting],
  data() {
    return {
      tabIndex: 0,
      loaded: [false, false],
      loading: [false, false],
      value1: {
        type: 'meta',
        code: '',
        attach: [],
      },
      value2: {
        type: 'ds',
        code: null,
        attach: [],
      },
      treeData1: [],
      treeData2: [],
      defaultCheckedKey1: '',
      defaultCheckedKey2: '',
      tablePost: () => {},
    }
  },
  watch: {
    tabIndex(to) {
      if (!this.loaded[to - 1] && this['postData' + to]) {
        this['postData' + to]()
      }
      this.$set(this.loaded, parseInt(to) - 1, true)
    },
  },
  created() {
    if (this.currentData && this.currentData.type !== undefined) {
      this.init(this.currentData)
    } else if (this.$attrs && this.$attrs.chooseMetaData && this.$attrs.chooseMetaData.code) {
      this.init(this.$attrs.chooseMetaData)
    } else {
      this.tabIndex = '1'
    }
  },
  methods: {
    init({ type, attach, code, chooseKey }) {
      const data = dsf.mix(true, [], attach)
      if (type === 'meta') {
        this.value1 = { type, attach: data, code, chooseKey }
        this.defaultCheckedKey1 = chooseKey
        this.tabIndex = '1'
      } else if (type === 'ds') {
        this.value2 = { type, attach: data, code, chooseKey }
        this.defaultCheckedKey2 = chooseKey
        this.tabIndex = '2'
      }
    },
    validate() {
      this.$refs.meta.getResult()
      if (this['value' + this.tabIndex]['code']) {
        return true
      } else {
        dsf.layer.message(
          '请选择' +
            (this.tabIndex === '1'
              ? '元数据以及右侧具体节点名称字段'
              : '数据源'),
          false
        )
        return false
      }
    },
    yes() {
      return this['value' + this.tabIndex]
    },
    postData1() {
      this.$set(this.loading, 0, true)
      this.$http
        .get('rm/list/meta', { namespace: '' })
        .then(({ data }) => {
          if (data.success) {
            this.treeData1 = data.data.map((v) => this.dataFormat(v, '1'))
            this.$nextTick(() => {
              this.$refs.meta.initMetaTable()
            })
          } else {
            dsf.layer.message('请求异常！', false)
          }
        })
        .finally(() => {
          this.$set(this.loading, 0, false)
        })
    },
    postData2() {
      this.$set(this.loading, 1, true)
      this.$http
        .get('/rm/list/ds', { namespace: '' })
        .then(({ data }) => {
          if (data.success) {
            this.treeData2 = data.data.map((v) => this.dataFormat(v, '2'))
          } else {
            dsf.layer.message('请求异常！', false)
          }
        })
        .finally(() => {
          this.$set(this.loading, 1, false)
        })
    },
    dataFormat(node, index) {
      if (node.type_value != '3' && node.type_value != '8') {
        node.nocheck = true
      }
      if (node.type_value != '3' && node.type_value != '8' && !node.children) {
        node.iconSkin = '_special'
      }
      if (node.children) {
        node.children = node.children.map((v) => this.dataFormat(v, index))
      }
      return node
    },
  },
}
</script>
