<template>
  <div v-show="popShow" class="seat-pop-box" :style="sty">
    <ul>
      <li @mousedown.left="popDialog">方阵组</li>
    </ul>

    <el-dialog :visible.sync="dialogVisible" title="信息" width="300px" :modal="false" :append-to-body="true">
      <el-form :model="form" size="small">
        <el-form-item label="列数">
          <el-input-number v-model="form.colNum" :precision="0" :controls="false" :min="1"></el-input-number>
        </el-form-item>
        <el-form-item label="个数">
          <el-input-number v-model="form.total" :precision="0" :controls="false" :min="1"></el-input-number>
        </el-form-item>
        <el-form-item style="text-align: right">
          <el-button @click="setGroup">确定</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import eventBus from "_platform/dsf/designer/seat/eventBus"
  import {eventBusEvent} from "_platform/dsf/designer/seat/constant";

  export default {
    name: "SeatPop",
    data(){
      return{
        dialogVisible: false,
        form: {
          colNum: 3,
          total: 7
        },
        seat: {},
        position: {},
        popShow: false
      }
    },
    computed:{
      sty(){
        return{
          left: this.position.x + "px",
          top: this.position.y + "px"
        }
      }
    },
    mounted(){
      eventBus.on(eventBusEvent.SEAT_POP, data => {
        Object.assign(this, data);
        console.log(data);
        this.popShow = true;
      })

      eventBus.on(eventBusEvent.BODY_EMPTY_CLICK, ()=>{
        this.popShow = false;
      })
    },
    methods:{
      popDialog(evt){
        evt.stopPropagation();
        evt.preventDefault();
        this.popShow = true;
        this.dialogVisible = true
      },
      setGroup(){
        let {total, colNum} = this.form;
        if (total === 1){
          this.dialogVisible = false;
          this.popShow = false;
          return false
        }

        const jud = total % colNum;
        let hitNum = parseInt(total/colNum);
        if (jud > 0 )  hitNum += 1;

        const memberList = [];
        for (let i=0; i< total; i++){
          let rowIndex = parseInt(i / colNum);
          let colIndex = (i % colNum);
          colIndex = colIndex<0? colNum-1: colIndex;
          let _left = colIndex * this.seat.width;
          let _top = rowIndex * this.seat.height;

          let member = {
            ...this.seat,
            id: dsf.uuid(),
            left: _left,
            top: _top,
            colIndex: colIndex,
            rowIndex: rowIndex,
            angle: 0
          }

          memberList.push(member)
        }
        let _width = this.seat.width * colNum;
        let _height = this.seat.height * hitNum;

        const group = {
          id: dsf.uuid(),
          type: 'group',
          left: this.seat.left,
          top: this.seat.top,
          cols: colNum,
          rows: hitNum,
          width: _width,
          height: _height,
          angle: this.seat.angle,
          memberType: this.seat.type,
          member: memberList
        }
        this.$store.commit("seatDesign/addGroup2Panel", group);
        eventBus.emit(eventBusEvent.KEY_DELETE)
        this.dialogVisible = false;
        this.popShow = false;
      }
    }
  }
</script>