<template>
  <div class="tree-img-list">
    <div
      v-for="item in imgList"
      :key="item.name"
      class="img-box"
      :class="{ active: item.icon === chooseIcon }"
      @click="addClass(item)"
    >
      <el-image
        :alt="item.item"
        :src="
          require(`../../../assets/styles/themes/tree_img/${item.showIcon}.png`)
        "
      ></el-image>
      <p class="demonstration">
        {{ item.icon }}
      </p>
    </div>
  </div>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesTreeImgList",
  mixins: [propertyDialogSetting],
  props: {
    treeList: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      imgList: [],
      chooseIcon: "",
      urlPath: "../../../assets/styles/themes/tree_img/",
    };
  },
  created() {
    this.getTreeImgList();
  },
  mounted() {},
  methods: {
    addClass(item) {
      this.chooseIcon = item.icon;
      this.treeList.icon = item.icon ? this.urlPath + item.icon + ".png" : "";
    },
    getTreeImgList() {
      this.$http
        .get("./static/pd_other/treeImgList.json")
        .done((res) => {
          this.imgList = res[this.treeList.imgType];
          let data = this.treeList.icon;
          this.chooseIcon = data ? data.slice(this.urlPath.length, -4) : "";
        })
        .error((err) => {
          dsf.error(err);
        });
    },
  },
};
</script>
