<template>
  <div class="designer_tools">
    <DsfButton v-for="(button, $index) in layout.buttons" :key="$index" :icon="button.icon" :text="button.text" btn-style="icon-text" @click="button.handler($event, button)" />
    <input ref="uploadInput" class="input-file" type="file" accept="image/*" name="file">
    <width-input :canvas-set="canvasSet" :has-bg="hasBg"></width-input>
  </div>
</template>

<script>
  import {uploadImg} from '../func'
  import {mapState} from 'vuex';
  import widthInput from "_platform/dsf/designer/seat/seatDesign/widthInput";

  export default {
    name: "TopTools",
    components: {
      widthInput,
    },
    data() {
      return {
        layout: {
          buttons: [
            {text: "保存", icon: "baocun", handler: this.saveLayout},
            {text: "序号设置", icon: "shuzishurukuang", handler: this.saveLayout},
            {text: "底图", icon: "tupiantianjia", handler: this.updateBG},
            {text: "复位", icon: "shuaxin", handler: this.saveLayout},
            {text: "回退", icon: "shangyibu1", handler: this.saveLayout},
          ],
        },
        backgroundImage: {
          url: "",
          width: 0,
          height: 0,
        },
        hasBg: false,   //是否有底图
        canvasSet: {
          width: undefined,
          height: undefined,
        }
      }
    },

    computed: {
      ...mapState('seatDesign', [
        "compelPanelSize"
      ])
    },
    watch: {
      "canvasSet.width"(val) {
        if (!this.hasBg) return;

        if (!val) {
          this.$nextTick(() => {
            let boxWid = $(".seat-designer-page").width();
            let scale = boxWid / this.backgroundImage.width;
            this.canvasSet.height = this.backgroundImage.height * scale;
          });
          return;
        }
        let scale = val / this.backgroundImage.width;
        this.canvasSet.height = this.backgroundImage.height * scale;
      },
    },
    methods: {
      saveLayout() {
        
      },
      updateBG() {
        const self = this;
        const upDOM = self.$refs.uploadInput;
        $(upDOM).click();
        $(upDOM).off('change').on('change', function () {
          if (upDOM.files.length === 0) return;
          const formData = new FormData();
          formData.append("file", upDOM.files[0]);
          const callback = result => {
            if (result === "error") {
              self.hasBg = false;
              return false;
            }
            setBgImage2Panel.call(self, result[0]);
          };
          uploadImg.call(self, formData, callback)
        })
      }
    }
  }

  function setBgImage2Panel(data) {
    const self = this;
    let image = new Image();
    image.src = dsf.url.getWebPath(data.relativePath);
    image.onload = function () {
      self.hasBg = true;
      self.backgroundImage.width = image.naturalWidth;
      self.backgroundImage.height = image.naturalHeight;
      self.backgroundImage.relativePath = data.relativePath;
      self.backgroundImage.absolutePath = data.absolutePath;
      changePanelSizeForBgImage.call(self);
    };
    image.onerror = function () {
      dsf.layer.message("图片加载失败", false);
      self.hasBg = false;
    }
  }

  function changePanelSizeForBgImage() {
    const self = this;
    let bg_w = self.backgroundImage.width;
    let bg_h = self.backgroundImage.height;
    let box_w = self.compelPanelSize.width || $(".seat-designer-page").width();   //若画布宽没有设置值
    let scale = box_w / bg_w;

    self.canvasSet.width = box_w;
    self.canvasSet.height = bg_h * scale;
    $("#panel_bg").css("background-image", "url(" + self.backgroundImage.absolutePath + ")");
  }

</script>