<template>
  <el-table :data="tableData" :border="true" style="width: 100%;">
    <el-table-column prop="text" label="名称" width="150"></el-table-column>
    <el-table-column prop="value" label="高度">
      <template v-slot:default="scope">
        <el-input v-model="scope.row.height" placeholder="auto" />
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";
export default {
  name: "DesTableRowsInfo",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: []
    };
  },
  mounted() {
    for (let i = 0; i < this.currentData.length; i++) {
      this.tableData.push({ text: "第" + (i + 1) + "行", height: this.currentData[i].height });
    }
  },
  methods: {
    yes() {
      for (let i = 0; i < this.tableData.length; i++) {
        let h = this.tableData[i].height;
        if (h.trim() == "") {
          h = 57;
        }
        if (dsf.isNumber(h)) {
          h = h + "px";
        }
        this.owner.rows[i].height = h;
      }
    }
  }
};
</script>
