<template>
  <div class="range-rule">
    <el-tabs v-model="config.selectTab" @tab-click="handleClick">
      <el-tab-pane label="部门+角色" name="0" class="dept">
        <div class="dept_left">
          <div class="top">
            <el-radio v-model="config.model" label="assign" @change="tabsChange">
              指定部门
            </el-radio>
            <el-radio v-model="config.model" label="extend" @change="tabsChange">
              继承节点部门
            </el-radio>
            <el-radio v-model="config.model" label="super" @change="tabsChange">
              高级模式
            </el-radio>
            <el-checkbox v-model="config.IsUser" class="send-to-people">
              发送到人
            </el-checkbox>
            <el-checkbox v-model="config.IsDeep" class="is-deep">
              深度遍历
            </el-checkbox>
          </div>
          <div class="content-box" :class="{ assign: config.model === 'assign' }">
            <SelectTree v-show="config.model == 'assign'" :tree-data="deptData" :select.sync="selectDept"></SelectTree>
            <SelectTable
              v-if="config.model === 'extend'"
              :data="nodesData"
              :select.sync="selectExtends"
              :columns="deptColumns"
            ></SelectTable>
            <div v-if="config.model == 'super'" class="super">
              <EventSetting ref="editor" :language="language" :code="code"></EventSetting>
            </div>
          </div>
        </div>
        <div class="dept_right">
          <SelectTree :tree-data="roleData" :select.sync="selectRole"></SelectTree>
        </div>
      </el-tab-pane>
      <el-tab-pane label="直接选人" name="1">
        <div class="dept_left">
          <div class="top">
            <el-radio v-model="config.model" label="assign" @change="tabsChange">
              指定人员
            </el-radio>
            <el-radio v-model="config.model" label="extend" @change="tabsChange">
              继承节点经办人
            </el-radio>
            <el-radio v-model="config.model" label="super" @change="tabsChange">
              高级模式
            </el-radio>
          </div>
          <div class="content-box" :class="{ assign: config.model === 'assign' }">
            <SelectTree
              v-show="config.model == 'assign'"
              :tree-data="peopleData"
              :multi="true"
              :select.sync="selectPeople"
            ></SelectTree>
            <SelectTable
              v-if="config.model === 'extend'"
              :data="nodesData"
              :select.sync="selectExtendPerson"
              :columns="noLevelColumns"
            ></SelectTable>
            <div v-if="config.model == 'super'" class="super">
              <EventSetting ref="personEditor" :language="language" :code="code"></EventSetting>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="行政岗位" name="2">
        <div class="dept_left">
          <div class="top">
            <el-radio v-model="config.model" label="assign" @change="tabsChange">
              指定岗位
            </el-radio>
            <el-radio v-model="config.model" label="super" @change="tabsChange">
              高级模式
            </el-radio>
            <el-checkbox v-model="config.IsUser" class="send-to-people">
              发送到人
            </el-checkbox>
            <el-checkbox v-model="config.IsDeep" class="is-deep">
              深度遍历
            </el-checkbox>
          </div>
          <div class="content-box" :class="{ assign: config.model === 'assign' }">
            <SelectTable
              v-if="config.model === 'assign'"
              :data="jobData"
              :select.sync="selectJobs"
              :columns="jobColumns"
            ></SelectTable>
            <div v-if="config.model == 'super'" class="super">
              <EventSetting ref="jobEditor" :language="language" :code="code"></EventSetting>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="人+关系" name="3" :lazy="true" class="dept">
        <div class="dept_left">
          <div class="top">
            <el-radio v-model="config.model" label="assign" @change="tabsChange">
              继承节点经办人
            </el-radio>
            <el-radio v-model="config.model" label="super" @change="tabsChange">
              高级模式
            </el-radio>
            <el-checkbox v-model="config.IsUser" class="send-to-people">
              发送到人
            </el-checkbox>
          </div>
          <div class="content-box">
            <SelectTable
              v-if="config.model === 'assign'"
              :data="nodesData"
              :select.sync="selectAgents"
              :columns="noLevelColumns"
            ></SelectTable>
            <div v-if="config.model == 'super'" class="super">
              <EventSetting ref="relationEditor" :language="language" :code="code"></EventSetting>
            </div>
          </div>
        </div>
        <div class="dept_right">
          <SelectTree :tree-data="roleData" :select.sync="selectRelation"></SelectTree>
        </div>
      </el-tab-pane>
    </el-tabs>
    <div class="bottom">
      <div class="btnBox">
        <el-button class="ds-button auxiliary" icon="iconfont icon-xia" size="small" @click="chooseFun">
          选择
        </el-button>
        <el-button class="ds-button auxiliary" icon="iconfont icon-delete" size="small" @click="clearData">
          清空
        </el-button>
      </div>
      <div class="table-box">
        <SelectTable :data="config.tableResult.Rule || []" :columns="resultColumns"></SelectTable>
      </div>
    </div>
  </div>
</template>
<script>
import { GetIsSelected } from "_platform/dsf/designer/flow/propertySource";
import SelectTree from "./selectTree";
import SelectTable from "./selectTable";
export default {
  name: "RangeRule",
  components: {
    SelectTree,
    SelectTable,
  },
  props: {
    nodes: {
      type: Array,
      default() {
        return [];
      },
    },
    checked: {
      type: Object,
      default() {
        return {};
      },
    },
    code: {
      type: String,
      default: "",
    },
    language: {
      type: String,
      default: "javascript",
    },
    value: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  data() {
    return {
      config: {
        selectTab: "0",
        model: "assign",
        IsDeep: false,
        IsUser: true,
        tableResult: {
          Type: "",
          IsUser: 1,
          Rule: [],
        },
        chooseDataObj: {},
      },
      rangeType: null,
      deptData: [],
      nodesData: [], // 节点数据共用
      roleData: [],
      peopleData: [],
      extendPersonData: [],
      jobData: [
        // 假数据
        {
          label: 1,
          desc: 2,
        },
      ],
      selectDept: null,
      selectRole: null,
      selectExtends: [],
      selectPeople: null,
      selectExtendPerson: [],
      selectJobs: [],
      selectAgents: [],
      selectRelation: null,
      deptOptions: [
        {
          label: "当前部门",
          value: -1,
        },
        {
          label: "子机构",
          value: 0,
        },
        {
          label: "一级部门",
          value: 1,
        },
        {
          label: "二级部门",
          value: 2,
        },
        {
          label: "三级部门",
          value: 3,
        },
      ],
      deptColumns: [],
      resultColumns: [
        {
          prop: "name",
          label: "范围",
        },
        {
          prop: "IsSelected",
          label: "状态",
          width: "140",
          options: GetIsSelected(),
        },
        {
          label: "操作",
          width: "100",
          operate: true,
        },
      ],
      noLevelColumns: [],
      jobColumns: [
        {
          selection: true,
          type: "selection",
          width: 50,
        },
        {
          prop: "label",
          label: "岗位名称",
        },
        {
          prop: "desc",
          label: "备注",
        },
      ],
    };
  },
  created() {
    const commonColumns = [
      {
        type: "index",
        width: 44,
      },
      {
        selection: true,
        type: "selection",
        width: 50,
      },
      {
        prop: "label",
        label: "范围",
      },
    ];
    this.deptColumns = [
      ...commonColumns,
      {
        prop: "level",
        label: "级别",
        options: this.deptOptions,
      },
    ];
    this.noLevelColumns = [...commonColumns];
    this.initExtendDataForOrg();
    this.initAssignDataForOrg();
    this.getFlowPosition();
    this.initAssignDataForPeople();
    this.initData();
  },
  mounted() {},
  methods: {
    getContent() {
      return this.config.tableResult;
    },
    tabsChange(e) {
      // console.log(e);
      this.clearData();
    },
    // 初始化显示数据
    initData() {
      const Rule = (this.value && dsf.mix(true, [], this.value.Rule)) || [];
      if (Rule && Rule.length > 0) {
        this.config.tableResult = dsf.mix(true, {}, this.value);
        Rule.forEach((item) => {
          let key;
          const value = this.value;
          let config = this.config;
          if (value.Type === 12) {
            config.model = "super";
            config.selectTab = "2";
            key = "jobAdvanced";
          } else if (value.Type === 21) {
            config.model = "assign";
            config.selectTab = "1";
            key = item.id;
          } else if (value.Type === 22) {
            config.model = "extend";
            config.selectTab = "1";
            key = item.id;
          } else if (value.Type === 23) {
            config.model = "super";
            config.selectTab = "1";
            key = "personAdvanced";
          } else if (value.Type === 31) {
            config.model = "assign";
            config.selectTab = "0";
            key = item.DeptID + "-" + item.RoleID;
          } else if (value.Type === 32) {
            config.model = "extend";
            config.selectTab = "0";
            key = item.NodeID + "-" + item.RoleID;
          } else if (value.Type === 33) {
            config.model = "super";
            config.selectTab = "0";
            key = "role-" + item.RoleID;
          } else if (value.Type === 41) {
            config.model = "assign";
            config.selectTab = "3";
            key = "relation" + item.id;
          } else if (value.Type === 42) {
            config.model = "super";
            config.selectTab = "3";
            key = "relationAdvanced";
          } else {
            key = item.id;
          }
          this.config.chooseDataObj[key] = item;
        });
      }
    },
    // 初始化指定部门接口
    initAssignDataForOrg() {
      this.$http.get("oua/getDeptByCurUnit", {}).then((response) => {
        if (response.data.success) {
          const data = this.formatData(response.data.data);
          this.deptData = [data];
        }
      });
    },
    // 初始化继承部门节点
    initExtendDataForOrg() {
      let nodes = this.nodes;
      let rows = [];
      rows.push({
        id: "DS_CUR_SEND_NODE",
        label: "当前发送节点",
        level: 1,
      });
      for (let i = 0; i < nodes.length; i++) {
        rows.push({
          id: nodes[i].id,
          label: nodes[i].name,
          level: 1,
        });
      }
      this.nodesData = rows;
    },
    // 初始化指定人员接口
    initAssignDataForPeople() {
      this.$http.get("oua/getUserByCurUnit", {}).then((response) => {
        if (response.data.success) {
          const data = this.formatData(response.data.data, "1");
          this.peopleData = [data];
        }
      });
    },
    // 获取角色树
    getFlowPosition() {
      this.$http.get("oua/getRole", {}).then((response) => {
        if (response.data.success) {
          const data = [];
          response.data.data.forEach((item) => {
            data.push(this.formatData(item, "2"));
          });
          this.roleData = data;
        }
      });
    },
    formatData(data, disabledVaule) {
      data.label = data.NAME || data._name;
      data.id = data.id || data._id;
      data.type = data.TYPE || data._type;
      if (disabledVaule) {
        data.disabled = data.type == disabledVaule ? false : true;
      }
      data.attributes = {
        NAME: data.label,
        id: data.id,
        FID: data.pid,
        type: data.type,
      };
      delete data.NAME;
      delete data.Atts;
      delete data.TYPE;
      delete data.FID;
      if (data.children) {
        for (var i = 0; i < data.children.length; i++) {
          this.formatData(data.children[i], disabledVaule);
        }
      }
      return data;
    },
    handleClick({ index }) {
      this.config.model = "assign";
      this.clearData();
    },
    editorLoaded(dom) {
      let v = this.code;
      if (this.language == "html") {
        v = dsf.unescapeHTML(v);
      }
      this.$refs[dom].setContent(v);
    },
    deleteRow(item) {
      if (item.Type === 12) {
        delete this.config.chooseDataObj["jobAdvanced"];
      } else if (item.Type === 23) {
        delete this.config.chooseDataObj["personAdvanced"];
      } else if (item.Type === 31) {
        delete this.config.chooseDataObj[item.DeptID + "-" + item.RoleID];
      } else if (item.Type === 32) {
        delete this.config.chooseDataObj[item.NodeID + "-" + item.RoleID];
      } else if (item.Type === 33) {
        delete this.config.chooseDataObj["role-" + item.RoleID];
      } else if (item.Type === 41) {
        delete this.config.chooseDataObj["relation" + item.id];
      } else if (item.Type === 42) {
        delete this.config.chooseDataObj["relationAdvanced"];
      } else {
        delete this.config.chooseDataObj[item.id];
      }
    },
    chooseFun() {
      if (!this.verify()) {
        return;
      }
      const config = this.config;
      if (config.selectTab === "0") {
        if (config.model === "assign") {
          this.addRule(this.selectDept, this.selectRole);
        } else if (config.model === "extend") {
          this.selectExtends.forEach((extend) => {
            this.addRule(extend, this.selectRole);
          });
        } else {
          const rule = {
            label: "表达式选角色",
            exp: this.$refs.editor.getContent(),
            id: dsf.uuid(32),
          };
          this.addRule(rule, this.selectRole);
        }
      } else if (config.selectTab === "1") {
        if (config.model === "assign") {
          this.selectPeople.forEach((people) => {
            this.addPerson(people);
          });
        } else if (config.model === "extend") {
          this.selectExtendPerson.forEach((person) => {
            this.addPerson(person);
          });
        } else {
          const person = {
            label: "表达式选人",
            exp: this.$refs.personEditor.getContent(),
            id: dsf.uuid(32),
          };
          this.addPerson(person);
        }
      } else if (config.selectTab === "2") {
        if (config.model === "super") {
          const job = {
            label: "表达式选岗位" + (config.IsUser ? "(发送到人)" : ""),
            exp: this.$refs.jobEditor.getContent(),
            id: dsf.uuid(32),
          };
          this.addJob(job);
        }
      } else {
        if (config.model === "assign") {
          this.selectAgents.forEach((agent) => {
            this.addRelation(agent, this.selectRelation);
          });
        } else if (config.model === "super") {
          const relation = {
            label: "表达式选关系",
            exp: this.$refs.relationEditor.getContent(),
            id: dsf.uuid(32),
          };
          this.addRelation(relation, this.selectRelation);
        }
      }
    },
    verify() {
      const config = this.config;
      if (config.selectTab === "0") {
        if (config.model === "assign" && !this.selectDept) {
          dsf.layer.alert("请选择左侧指定部门节点");
          return false;
        }
        if (config.model === "extend" && this.selectExtends.length === 0) {
          dsf.layer.alert("请选择继承的节点部门");
          return false;
        }
        if (!this.selectRole) {
          dsf.layer.alert("请选择右侧角色节点");
          return false;
        }
      } else if (config.selectTab === "1") {
        if (config.model === "assign" && !this.selectPeople) {
          dsf.layer.alert("请选择指定人员");
          return false;
        }
        if (config.model === "extend" && this.selectExtendPerson.length === 0) {
          dsf.layer.alert("请选择继承节点经办人");
          return false;
        }
      } else if (config.selectTab === "2") {
        if (config.model === "assign") {
          dsf.layer.alert("暂无真实数据");
          return false;
        }
      } else {
        if (config.model === "assign" && this.selectAgents.length === 0) {
          dsf.layer.alert("请选择左侧继承节点经办人");
          return false;
        }
        if (!this.selectRelation) {
          dsf.layer.alert("请选择右侧关系节点");
          return false;
        }
      }
      return true;
    },
    addRule(dept, role) {
      const config = this.config;
      let text = "";
      text += config.IsDeep ? "深度遍历" : "";
      text += config.IsDeep && config.IsUser ? "," : "";
      text += config.IsUser ? "发送到人" : "";
      const name = `${dept && dept.label}-${this.getDeptLevel(dept && dept.level)}${role.label}${
        text ? "(" + text + ")" : ""
      }`;
      let obj = {
        // 共用属性
        id: dsf.uuid(32),
        name: name,
        IsDeep: config.IsDeep ? 1 : 0,
        IsUser: config.IsUser ? 1 : 0,
        IsSelected: 0,
        RoleID: role.id,
        RoleName: role.label,
      };
      let key;
      if (config.model === "assign") {
        Object.assign(obj, {
          Type: 31,
          DeptID: dept.id,
          DeptName: dept.label,
        });
        key = obj.DeptID + "-" + obj.RoleID;
      } else if (config.model === "extend") {
        Object.assign(obj, {
          NodeID: dept.id,
          NodeName: dept.label,
          Type: 32,
          Level: dept.level,
          DeptName: dept.label,
        });
        key = obj.NodeID + "-" + obj.RoleID;
      } else {
        Object.assign(obj, {
          Type: 33,
          exp: dept.exp,
        });
        key = "role-" + role.id;
      }
      this.addToTable(config, key, obj);
    },
    addPerson(node) {
      const config = this.config;
      const obj = {
        id: node.id,
        name: node.label,
        IsSelected: 0,
        IsUser: 1,
      };
      let key;
      if (config.model === "assign") {
        Object.assign(obj, {
          UserID: node.id,
          UserName: node.label,
          DeptID: node.UnitID,
          DeptName: node.UnitName,
          Type: 21,
        });
        key = obj.id;
      } else if (config.model === "extend") {
        obj.Type = 22;
        key = obj.id;
      } else {
        Object.assign(obj, {
          exp: node.exp,
          Type: 23,
        });
        key = "personAdvanced";
      }
      this.addToTable(config, key, obj);
    },
    addJob(job) {
      const config = this.config;
      const obj = {
        id: job.id,
        name: job.label,
        IsDeep: config.IsDeep ? 1 : 0,
        IsUser: config.IsUser ? 1 : 0,
        IsSelected: 0,
      };
      let key;
      if (config.model === "super") {
        Object.assign(obj, {
          JobID: job.id,
          JobName: job.label,
          exp: job.exp,
          Type: 12,
        });
        key = "jobAdvanced";
      }
      this.addToTable(config, key, obj);
    },
    addRelation(relation, role) {
      const config = this.config;
      const name = `${relation && relation.label}-${role.label}${config.IsUser ? "(发送到人)" : ""}`;
      const obj = {
        id: relation.id,
        name: name,
        IsUser: config.IsUser ? 1 : 0,
        RelationID: role.id,
        RelationName: role.label,
        IsSelected: 0,
      };
      let key;
      if (config.model === "assign") {
        Object.assign(obj, {
          NodeID: relation.id,
          NodeName: relation.label,
          Type: 41,
        });
        key = "relation" + relation.id;
      } else if (config.model === "super") {
        obj.Type = 42;
        key = "relationAdvanced";
      }
      this.addToTable(config, key, obj);
    },
    addToTable(config, key, obj) {
      if (!config.chooseDataObj[key]) {
        config.tableResult.IsUser = obj.IsUser;
        config.tableResult.Type = obj.Type;
        config.chooseDataObj[key] = obj;
        config.tableResult.Rule.push(obj);
      }
    },
    getDeptLevel(level) {
      if (!level || this.config.selectTab !== "0") {
        return "";
      }
      const deptLevel = this.deptOptions.find((item) => item.value === level);
      return deptLevel.label + "-";
    },
    clearData() {
      this.config.tableResult.Type = 31;
      this.config.tableResult.IsUser = 0;
      this.config.tableResult.Rule = [];
      this.config.chooseDataObj = {};
    },
  },
};
</script>
<style lang="scss" scoped>
.range-rule {
  .el-tabs {
    height: 298px;
  }
  .dept {
    .dept_left {
      width: 70%;
      float: left;
    }
    .dept_right {
      position: absolute;
      left: 71%;
      right: 0;
      bottom: 0;
      top: 0px;
      border: 1px solid #e6e6e6;
      overflow: auto;
    }
  }
  .top {
    height: 34px;
  }
  .content-box {
    height: 202px;
    // overflow: auto;
    margin-top: 5px;
    width: 100%;
    position: relative;
    &.assign {
      border: 1px solid #e6e6e6;
    }
    .super {
      width: 100%;
      height: 100%;
    }
  }
  .el-radio {
    float: left;
    margin-right: 14px;
  }
  .is-deep {
    float: right;
    margin-right: 14px;
  }
  .send-to-people {
    float: right;
    margin-right: 0;
  }

  .btnBox {
    margin: 10px 0px;
    text-align: center;
    .ds-button {
      padding: 5px 8px;
      min-width: 74px;
    }
  }
}
</style>
