<template>
  <el-table :data="tableData" :border="true" style="width: 100%;">
    <el-table-column prop="code" label="标识" width="100"></el-table-column>
    <el-table-column prop="name" label="描述"></el-table-column>
    <el-table-column prop="typeValue" label="数据类型">
      <template v-slot:default="scope">
        <el-select v-model="scope.row.typeValue" @change="typeChange(scope.row, $event)">
          <el-option v-for="item in getType()" :key="item.value" :label="item.text" :value="item.value"></el-option>
        </el-select>
      </template>
    </el-table-column>
    <el-table-column prop="length" label="数据长度">
      <template v-slot:default="scope">
        <el-input v-model="scope.row.length" placeholder="无" />
      </template>
    </el-table-column>
    <el-table-column prop="defaultValue" label="默认值">
      <template v-slot:default="scope">
        <el-input v-model="scope.row.defaultValue" placeholder="无" />
      </template>
    </el-table-column>
  </el-table>
</template>
<script>
import propertyDialogSetting from "_platform/dsf/mixins/propertyDialogSetting";

export default {
  name: "DesValueAttributes",
  mixins: [propertyDialogSetting],
  data() {
    return {
      tableData: []
    };
  },
  mounted() {
    if (!this.owner) {
      return;
    }

    let arr = dsf.mix(true, [], this.owner.metadata.valueAttributes);
    _.each(arr, v => {
      v.typeValue = v.type.value;
      this.tableData.push(v);
    });

    // let ctrl = this.owner.$$getComponent();
    // let options = ctrl.$options;
    // let design = options.design || {};
    // let metadata = design.metadata || {};
    // let createFn=metadata.create;
    // if (createFn) {
    //   let schema =createFn.call(ctrl);// this.owner.createMetaData();
    //   if (schema.metadata) {
    //     let arr = dsf.mix(true, [], schema.metadata.valueAttributes);
    //     _.each(arr, v => {
    //       v.typeValue = v.type.value;
    //       this.tableData.push(v);
    //     });
    //   }
    // }
  },
  methods: {
    getType() {
      return dsf.metadata.dataType;
    },
    typeChange(row, evt) {
      row.type = _.find(dsf.metadata.dataType, v => v.value == evt);
    },
    yes() {
      let tableData = dsf.mix(true, [], this.tableData);
      let result = [];
      _.each(tableData, v => {
        delete v.typeValue;
        result.push(v);
      });
      return result;
    }
  }
};
</script>
