<template>
  <div class="ds-ueditor">
    <DsfUEditor
      :value="content"
      :show-label="false"
      :$rich-txt-height="320"
      @editor-change="valueChange"
    ></DsfUEditor>
  </div>
</template>
<script>
export default {
  name: "RichText",
  props: {
    value: {
      type: String,
      default() {
        return "";
      },
    },
  },
  data() {
    return {
      content: "",
    };
  },
  created() {
    this.content = this.value;
  },
  mounted() {},
  methods: {
    getContent() {
      return this.content;
    },
    valueChange(res, val) {
      this.content = res.data;
    },
  },
};
</script>
