import { tools } from "./tools";
const Line_proxy = function (config, paper, opts) {
  let element = null,
    arrow = null,
    options = {
      path: [],
      from: null,
      to: null,
      strokeWidth: 2,
      stroke: config.Path.stroke,
      "stroke-dasharray": opts["stroke-dasharray"],
    };
  options = Object.assign(options, opts);
  this.id = null;
  this.path = options.path;
  this.from = options.from;
  this.to = options.to;
  this.linkInfo = options.linkInfo || {};
  this.render = function () {
    var path = [];
    for (var i = 0; i < this.path.length; i++) {
      var p = this.path[i];
      if (i == 0) {
        path.push(["M", p.x, p.y]);
      } else {
        path.push(["L", p.x, p.y]);
      }
    }
    if (path.length > 1) {
      var listSegmen = {
        sp: {
          x: path[path.length - 2][1],
          y: path[path.length - 2][2],
        },
        ep: {
          x: path[path.length - 1][1],
          y: path[path.length - 1][2],
        },
      };
      if (listSegmen.sp.y == listSegmen.ep.y) {
        listSegmen.ep.x =
          listSegmen.ep.x - (listSegmen.sp.x < listSegmen.ep.x ? 2 : -2);
      } else {
        listSegmen.ep.y =
          listSegmen.ep.y - (listSegmen.sp.y < listSegmen.ep.y ? 2 : -2);
      }
      try {
        if (!element) {
          element = paper.path(path).attr({
            "stroke-width": options.strokeWidth,
            stroke: options.stroke,
            "stroke-dasharray": options["stroke-dasharray"],
          });
          arrow = paper.path(tools.drawArrow(listSegmen)).attr({
            "stroke-width": options.strokeWidth,
            stroke: options.stroke,
            fill: options.stroke,
          });
          this.isRender = true;
        } else {
          element.attr({
            path: path,
            "stroke-width": options.strokeWidth,
            stroke: options.stroke,
          });
          if (arrow) {
            arrow.attr({
              path: tools.drawArrow(listSegmen),
              "stroke-width": options.strokeWidth,
              stroke: options.stroke,
              fill: options.stroke,
            });
          }
        }
      } catch (ex) {
        console.log(ex);
      }
    }
  };

  this.remove = function () {
    if (element) {
      element.remove();
    }
    if (arrow) {
      arrow.remove();
    }
  };
}

export default Line_proxy;