<template>
  <div ref="designerPanel" v-loading.fullscreen.lock="loading" class="ds-designer" :class="{ mobile: mobile }">
    <div class="designer-head">
      <div class="designer_tools">
        <!-- btn-style="icon-text" -->
        <template v-if="tpl">
          <DsfButton v-for="(button, $index) in templateButtons" :key="$index" :title="button.text" :icon="button.icon" btn-style="icon-text" @click="button.handler($event, button)">
            {{ button.text }}
          </DsfButton>
        </template>
        <template v-else>
          <template v-if="buttonLoad">
            <DsfButton v-for="(button, $index) in buttons" v-show="buttonShow(button)" :key="$index" :title="button.text" :icon="button.icon" btn-style="icon-text" @click="button.handler($event, button)">
              {{ button.text }}
            </DsfButton>
          </template>
        </template>
      </div>
    </div>
    <div class="designer-content">
      <div class="designer-center">
        <div class="designer-page">
          <DsfLayoutTree
            v-if="page"
            ref="viewProxy"
            style="position: absolute; height: 100%; width: 100%;"
            :is-design="true"
            :component-props.sync="page"
            @dragstart.native.prevent
            @mousedown.native.stop="mouseDownHanlder($event)"
            @mousemove.native="mouseMoveHanlder($event)"
            @contextmenu.native="contextMenuOpen($event)"
            @click.native="selectedComponent($event)"
          ></DsfLayoutTree>
        </div>
        <div class="designer-left">
          <div class="designer-left-tools">
            <el-tabs v-model="layout.toolsBox.activeName">
              <el-tab-pane v-for="tab in layout.toolsBox.tabs" :key="tab.name" :label="tab.label" :name="tab.name">
                <template v-slot:label>
                  <div style="padding-left: 4px;">{{ tab.label }}</div>
                </template>
                <div style="padding: 5px;">
                  <form action="" @submit.stop.prevent="leftSearch">
                    <el-input v-model="leftSearchInput" class="design-search-input" style="height: 30px; line-height: 30px;">
                      <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                  </form>
                </div>
                <!--控件组-->
                <template v-if="tab.name == 'controls'">
                  <el-collapse v-for="(group, $index) in useControlsGroup.groups" :key="$index" v-model="tools.activeName" accordion>
                    <el-collapse-item :name="group.title">
                      <template slot="title">
                        <div :title="group.title" class="ellipsis" style="font-size: 14px;">
                          {{ group.title }}
                        </div>
                        <DsfIcon :name="'guanbi2'" style="position: absolute; right: 30px;" @click.native.stop="removeControlGroup(group)"></DsfIcon>
                      </template>
                      <div class="ds-tools-group-content" style="padding: 10px; max-height: 60vh; overflow: auto;">
                        <drag-item v-for="(item, itemindex) in group.items" v-show="isMatchLeftSearch(item.name)" :key="itemindex" :data="item"></drag-item>
                      </div>
                    </el-collapse-item>
                  </el-collapse>
                </template>
                <template v-else-if="tab.name == 'metadata'">
                  <!--元数据组-->
                  <el-collapse v-for="(group, $index) in useMetaGroup.groups" :key="$index" v-model="useMetaGroup.activeName" accordion>
                    <el-collapse-item :name="group.id">
                      <template slot="title">
                        <div :title="group.title || group.name" class="ellipsis" style="font-size: 14px;">
                          {{ group.title || group.name }}
                        </div>
                        <DsfIcon :name="'guanbi2'" style="position: absolute; right: 30px;" @click.native.stop="removeMetaDataGroup(group)"></DsfIcon>
                      </template>
                      <div class="ds-tools-group-content" style="padding: 10px; max-height: 60vh; overflow: auto;">
                        <drag-item v-for="(item, itemindex) in group.items" v-show="isMatchLeftSearch(item.name)" :key="itemindex" :data="item"></drag-item>
                      </div>
                    </el-collapse-item>
                  </el-collapse>
                </template>
                <template v-else-if="tab.name == 'business'">
                  <el-collapse v-for="(group, $index) in useBusinessControlGroup.groups" :key="$index" v-model="tools.activeName" accordion>
                    <el-collapse-item :name="group.title">
                      <template slot="title">
                        <div :title="group.title" class="ellipsis" style="font-size: 14px;">
                          {{ group.title }}
                        </div>
                        <DsfIcon :name="'guanbi2'" style="position: absolute; right: 30px;" @click.native.stop="removeBusinessGroup(group)"></DsfIcon>
                      </template>
                      <div class="ds-tools-group-content" style="padding: 10px; max-height: 60vh; overflow: auto;">
                        <drag-item v-for="(item, itemindex) in group.items" v-show="isMatchLeftSearch(item.name)" :key="itemindex" :data="item"></drag-item>
                      </div>
                    </el-collapse-item>
                  </el-collapse>
                </template>
              </el-tab-pane>
            </el-tabs>
          </div>
        </div>
        <div class="designer-right">
          <DsfDesignerProperties :data="attributesGroup" :owner="attributesOwner"></DsfDesignerProperties>
        </div>
      </div>
    </div>
    <div v-show="dragProxy.show" ref="dragProxy" class="drag-proxy"></div>
    <div v-if="contextMenu.show" ref="contextmenu" class="ds-contextmenu" :style="{ top: contextMenu.pos.y + 'px', left: contextMenu.pos.x + 'px' }">
      <dl class="ds-contextmenu-child ds-contextmenu-anim">
        <template v-for="(item, $index) in contextMenu.items">
          <dd v-if="dsf.type(item) == 'object'" :key="$index" :class="{ disabled: item.disabled }" @click="contextMenuItemClick(item, $event)">
            <a>{{ item.text }}</a>
          </dd>
          <dd v-else-if="item == '-'" :key="$index" class="split"></dd>
        </template>
      </dl>
    </div>
  </div>
</template>
<script>
import extDefaultCode from "../extCode.txt";
import dragItem from "./dragItem";
import control from "../../../mixins/control";

let businessCode = 12;
let controlCode = 2;
export default {
  name: "DsfDesigner",
  components: { dragItem },
  provide() {
    return {
      $designer: this
    }
  },
  props: {
    material: {
      type: Array,
      default() {
        return [];
      },
    },
    business: {
      type: String,
      default: "",
    },
    module: {
      type: String,
      default: "",
    },
    pageName: {
      type: String,
      default: "",
    },
    pageTitle: {
      type: String,
      default: "",
    },
    mobile: {
      type: Boolean,
      default: false,
    },
    tpl: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      loading: true,
      firstOpen: true,
      saved: false,
      mousedown: {
        target: null,
      },
      design: true,
      buttons: [
        {
          text: "保存",
          id: "save",
          icon: "baocun",
          handler: this.saveLayout,
          setting: true,
          roles: [],
        },
        {
          text: "暂存",
          id: "stagesave",
          icon: "baocun",
          handler: this.stageSaveLayout,
          setting: false,
          roles: [],
        },
        {
          text: "预览",
          id: "preview",
          icon: "icon_yulan",
          handler: this.preView,
          setting: false,
          roles: [],
        },
        {
          text: "模板选择",
          id: "tpl",
          icon: "shebeikaifa1",
          handler: this.useTpl,
          setting: true,
          roles: [],
        },
        {
          text: "控件组选择",
          id: "ctrl",
          icon: "icon_shezhi",
          handler: this.selectComponents,
          setting: true,
          roles: [],
        },
        {
          text: "元数据选择",
          id: "meta",
          icon: "icon_shezhi",
          handler: this.selectMateGroup,
          setting: true,
          roles: [],
        },
        {
          text: "保存业务组件",
          id: "bctrl",
          icon: "biaodanzujian-xialakuang",
          handler: this.saveBusinessComponent,
          setting: true,
          saved: true,
          roles: [],
        },
        {
          text: "Vue模板",
          id: "vue",
          icon: "shebeikaifa1",
          handler: this.openVueTemplate,
          setting: false,
          roles: [],
        },
        {
          text: "二次开发JS",
          id: "js",
          icon: "shebeikaifa1",
          handler: this.openExtJs,
          setting: false,
          roles: [],
          saved: true,
        },
        {
          text: "自定义CSS",
          id: "css",
          icon: "shebeikaifa1",
          handler: this.openExtCss,
          setting: false,
          saved: true,
          roles: [],
        },
        {
          text: "自定义审核",
          id: "ev",
          icon: "guize",
          handler: dsf.noop,
          setting: false,
          saved: true,
          roles: [],
        },
      ],
      //模板设计时显示的按钮
      templateButtons: [
        {
          text: "保存模板",
          icon: "baocun",
          handler: this.saveLayoutToTemplate,
          setting: false,
          roles: [],
          show: () => true,
        },
        {
          text: "预览",
          icon: "icon_yulan",
          handler: this.preView,
          setting: false,
          roles: [],
          show: () => true,
        },
        {
          text: "模板选择",
          icon: "shebeikaifa1",
          handler: this.useTpl,
          setting: true,
          roles: [],
          show: () => true,
        },
        {
          text: "控件组选择",
          icon: "icon_shezhi",
          handler: this.selectComponents,
          setting: true,
          roles: [],
          show: () => true,
        },
        {
          text: "功能授权",
          icon: "jiaohuan",
          handler: this.settingFunction,
          setting: false,
          roles: [],
          show: () => true,
        },
      ],
      layout: {
        toolsBox: {
          //tab默认选中
          activeName: "controls",
          tabs: [
            { name: "controls", label: "基础组件" },
            { name: "business", label: "业务组件" },
            { name: "metadata", label: "元数据" },
          ],
        },
      },
      page: null,
      tools: {
        activeName: "",
        groups: [], //this.material
      },
      useControlsGroup: {
        activeName: "",
        groups: [],
      },
      useMetaGroup: {
        activeName: "",
        groups: [],
      },
      useBusinessControlGroup: {
        activeName: "",
        groups: [],
      },
      dragProxy: {
        show: false,
      },
      attributesGroup: [],
      attributesOwner: {},
      subTree: [],
      controls: {},
      dragMouseMoveTarget: null,
      contextMenu: {
        show: true,
        items: [],
        pos: { x: -10, y: -10 },
        target: null,
      },
      extJsCode: extDefaultCode,
      extCssCode: "",
      extScssCode: "",
      fromTemplateId: "",
      desButtons: {},
      currentUserRoles: {},
      buttonLoad: false,
      leftSearchStr: "",
      leftSearchInput: "",
    };
  },
  computed: {
    dragProxy_el() {
      return this.$refs.dragProxy;
    },
    toolBoxGroup() {
      let arr = [];
      _.each(this.material, (m) => {
        let tool = {
          title: m.title,
          items: [],
        };
        _.each(m.items, (el) => {
          el.type = "control";
          if (this.mobile) {
            el.isMobile && tool.items.push(el);
          } else {
            tool.items.push(el);
          }
        });
        if (tool.items.length > 0) {
          arr.push(tool);
        }
      });
      return arr;
    },
  },
  created() {
    window.designer = this;
    // this.initToolsBox();
  },
  async mounted() {
    this.loading = true;
    try {
      this.init();
      this.currentUserRoles = await this.getCurrentUserRoles();
      await this.loadLayout();
      await this.getFunctionSetting();
    } catch (err) {
      dsf.error(err);
      dsf.layer.pc.message("设计器加载失败", false);
    } finally {
      this.loading = false;
      this.buttonLoad = true;
    }
  },
  updated() { },
  methods: {
    buttonShow(button) {
      let result = true;
      if (button.saved && !this.saved) {
        return false;
      }
      if (!button.setting) {
        return true;
      }
      if (this.currentUserRoles["SUPER_ADMIN"]) {
        return true;
      } else {
        if (this.fromTemplateId) {
          let desBtnRoles = this.desButtons[button.id];
          if (!desBtnRoles) {
            return false;
          } else {
            let isExist = false;
            _.every(desBtnRoles, (v) => {
              isExist = hasRole.call(this, v);
              return isExist ? false : true;
            });
            return isExist;
          }
        }
        return true;
      }
    },
    dragEnd() {
      dragEnd.call(this);
    },
    async getFunctionSetting() {
      let params = {};
      let ns = "";
      if (this.tpl) {
        ns =
          (this.page.context.B || "") +
          "." +
          (this.page.context.M || "") +
          "." +
          this.page.name;
      } else {
        ns = this.fromTemplateId;
      }
      if (ns) {
        return await this.setDesButtons(ns);
      }
    },
    init() {
      this.initToolDrag();
      this.initDrop();
    },
    //初始化左侧组件工具箱
    initToolsBox() {
      // this.tools.groups = this.toolBoxGroup;
      // if (this.tools.groups[0].title) {
      //   this.tools.activeName = this.tools.groups[0].title;
      // }
    },
    //获取当前用户的所有角色
    async getCurrentUserRoles() {
      let roles = await this.$webAPI.getCurrentUserRoles();
      return _.keyBy(roles.data, "serialNumber");
    },
    //功能全新管理
    settingFunction() {
      let _this = this;
      this.$openDialog({
        title: "功能授权",
        content: "DesFunctionSetting",
        width: "1000px",
        height: "60vh",
        params: {
          buttons: _.filter(this.buttons, (btn) => btn.setting),
        },
        btns: [
          {
            text: "确定",
            handler(view) {
              let result = view.yes();
              if (result) {
                _.each(result, (button) => {
                  let btn = _.filter(_this.buttons, (b) => b.id == button.id);
                  btn = btn.length > 0 ? btn[0] : null;
                  if (btn) {
                    btn.roles = button.roles;
                  }
                });
              }
            },
          },
          {
            text: "取消",
          },
        ],
      });
    },
    //初始化左侧工具箱拖动
    initToolDrag() {
      initToolDrag.call(this);
    },
    //初始化右侧接受拖动
    initDrop() {
      initDrop.call(this);
    },
    //拖放时候，鼠标经过元素时触发
    dragMouseMoveHandler(evt) {
      let target = $(evt.target);
      let box = null;
      if (target.hasClass("drag-proxy")) {
        return;
      }
      if (target.is("[slot-name]")) {
        let allow = dsf.designer.isAllowDrop(target, this);
        if (allow) {
          box = target;
          this.dragProxy.show = true;
          let proxy = $(this.dragProxy_el);
          box.append(proxy);
        }
      } else {
        let ctrl = target.closest(".designer-ctrl-proxy");
        if (ctrl && ctrl.length > 0) {
          if ((!target.hasClass("designer-mask") && !target.hasClass("ds-control")) || target.hasClass("ds-no-drop")) {
            return;
          }
          let vueComponent = dsf.designer.getComponent(
            ctrl.get(0).__vue__,
            this
          );
          if (vueComponent) {
            if (vueComponent.$$getComponent) {
              vueComponent = vueComponent.$$getComponent();
            }
            box = ctrl.closest("[slot-name]");
            let allow = dsf.designer.isAllowDrop(box, this);
            if (allow) {
              let height = target.height();
              let proxy = $(this.dragProxy_el);
              this.dragProxy.show = true;
              if (evt.offsetY < height / 2) {
                ctrl.before(proxy);
              } else {
                ctrl.after(proxy);
              }
            }
          }
        }
      }
    },
    //拖拽完成
    dropHandler(evt, container, slot, data) {
      dropHandler.call(this, evt, container, slot, data);
    },
    //将组件添加到指定位置
    addControl(parent, slot, child, index) {
      if (!child.designId) {
        child.designId = dsf.uuid();
      }
      if (!child.caption) {
        let arr = child.ctrlType.split(".");
        child.caption =
          arr[arr.length - 1].toLowerCase() + this.$refs.viewProxy.getSerial();
      }
      if (parent.$$getComponent) {
        //如果是代理组件
        parent = parent.$children[0];
      } else {
        //此处需要有一个判断，组件的根元素直接是elementui的情况
        while (!parent.slots) {
          parent = parent.$parent;
        }
      }
      // if (child.metadata) {
      //   let metadataLevel = 0;
      //   if (isInSubtable(parent)) {
      //     metadataLevel = 1;
      //   }
      // }
      if (parent.$el) {
        //如果在子表中或者列表中是表单组建不显示label区域
        let isSubtableCell = parent.$el.matches(".ds-subtable-cell-content");
        let isDataGridCell = parent.$el.matches(".ds-datagrid-cell-content");
        if ((isSubtableCell || isDataGridCell) && child.isFormControl) {
          child.showLabel = false;
          child.simple = true;
          child.lrNoPadding = true;
        } else {
          child.simple = false;
          child.lrNoPadding = false;
        }
      }
      if (slot && slot.textProxy && child.isFormControl) {
        child.simple = true;
      }
      let slotIndex = _.findIndex(parent.slots, (s) => s.name == slot);
      if (slotIndex >= 0) {
        //判断是后入还是中间插入
        if (dsf.isUnDef(index) || index < 0) {
          parent.slots[slotIndex].controls.push(child);
        } else {
          parent.slots[slotIndex].controls.splice(index, 0, child);
        }
        child.isQuestionControl &&
          this.setQuestionIndex(child, parent.slots[slotIndex].controls, index);
      }
      // 问卷题自动选择控件
      if (child.isQuestionControl) {
        if (!this.$questionComponentTemp) {
          this.$questionComponentTemp = {};
        }
        setTimeout(() => {
          $(`[des-id=${child.designId}]`).trigger("click");
        }, 0);
      }
      // console.log('模式选择', child);
    },
    //问卷索引设置
    setQuestionIndex(child, childs, postion) {
      // 后入
      if (dsf.isUnDef(postion) || postion < 0) {
        this.setPrevQuestionIndex(child, childs);
      } else {
        let index = 0;
        _.forEach(childs, function (item, i) {
          if (item.isQuestionControl && item.hasQuestionIndex && item.visible) {
            index++;
            item.questionIndex = index;
          }
        });
      }
    },
    //设置childs中child之前所有问卷组件索引
    setPrevQuestionIndex(child, childs) {
      let index = 0;
      _.find(childs, function (item) {
        if (item.isQuestionControl && item.hasQuestionIndex && item.visible) {
          index++;
          item.questionIndex = index;
        }
        if (item.designId == child.designId) {
          return true;
        }
      });
    },
    //设置index之后所有问卷组件索引
    setNextQuestionIndex(index, nexts) {
      _.forEach(nexts, function (item) {
        if (item.hasQuestionIndex && item.visible) {
          item.questionIndex = index;
          index++;
        }
      });
    },
    //选中组件
    selectedComponent($event) {
      window.setTimeout(() => {
        this.contextMenuClose();
        let target = $($event.target);
        let component_el = target.closest(".designer-ctrl-proxy");
        if (component_el.length) {
          let component = component_el.get(0).__vue__;
          let allComponents = $(".designer-ctrl-proxy");
          allComponents.each(function (i, el) {
            let c = el.__vue__;
            c && (c.__desContext__.selected = false);
          });
          if (component) {
            if (!component.isView) {
              component.__desContext__.selected = true;
            }
          }
          this.$http
            .get("./static/pd_attributes/" + component.ctrlType + ".json")
            .done((res) => {
              if (res && res[0] && res[0].attributes) {
                res[0].attributes.forEach((attr) => {
                  this.format(attr, component);
                });
              }
              this.attributesGroup = res || [];
              this.attributesOwner = component;
              if (
                component.isQuestionControl &&
                this.$questionComponentTemp &&
                !this.$questionComponentTemp[component.designId]
              ) {
                this.$questionComponentTemp[component.designId] = true;
                setTimeout(() => {
                  this.$bus.$emit(`questionSetting`);
                }, 200);
              }
            })
            .error((err) => {
              this.attributesGroup = [];
              this.attributesOwner = component;
              dsf.error(err);
            });
        }
      }, 0);
      $event.preventDefault();
      $event.stopPropagation();
    },
    format(item, component) {
      // 流程信息--事项名称 单独处理
      if (item.dictionaryControl) {
        if (item.dictionary && item.dictionary.length === 0) {
          item.dictionary = [];
          dsf.recursionComponentProps(
            component.$$getComponent().$page.$props,
            (data) => {
              if (
                data.isFormControl &&
                data.ctrlName !== "DsfFlowInfo" &&
                data.ctrlName !== "DsfDisposeIdea"
              ) {
                item.dictionary.push({
                  id: data.designId,
                  text: `${data.label}(${data.caption})`,
                  value: data.caption,
                });
              }
            }
          );
        }
        console.log(item.dictionary);
      }
      return item;
    },
    /*--- 设计 事件 -----*/
    //处理设计时需要打开右键菜单的组件
    contextMenuOpen(evt) {
      let component = dsf.designer.getComponentByTarget(evt.target);
      if (!component) {
        return;
      }
      dsf.designer.broadcast(
        this,
        "clearContextMemuHandler",
        evt,
        null,
        component
      );
      do {
        let _options = component ? component.$options : {};
        let _contextMenuHandler = _options.design
          ? _options.design.contextMenuHandler
          : null;
        if (!_contextMenuHandler) {
          component = component.$parent;
        } else {
          break;
        }
      } while (component != this.$root);
      let options = component.$options;
      let contextMenuHandler = component.$options.design
        ? component.$options.design.contextMenuHandler
        : null;
      if (contextMenuHandler) {
        this.contextMenuClose();
        let items = options.design.contextMenuHandler.call(
          component,
          evt,
          this
        );
        if (items && items.length > 0) {
          this.$nextTick(() => {
            this.contextMenu.items = items;
            this.contextMenu.show = true;
            this.contextMenu.target = component;
            this.$nextTick(() => {
              let cm = this.$refs.contextmenu;
              if (cm && cm.childNodes[0]) {
                cm = cm.childNodes[0];
                this.contextMenu.pos.x = evt.pageX;
                this.contextMenu.pos.y = evt.pageY;
                if (
                  this.contextMenu.pos.y + cm.offsetHeight >
                  this.$refs.designerPanel.offsetHeight
                ) {
                  this.contextMenu.pos.y = evt.pageY - cm.offsetHeight;
                }
              }
            });
          });
          $(document).one("click", this.contextMenuClose);
        }
        evt.stopPropagation();
        evt.preventDefault();
      }
    },
    //右键菜单关闭
    contextMenuClose() {
      this.contextMenu.items = [];
      this.contextMenu.show = false;
      this.contextMenu.target = null;
    },
    //右键菜单点击
    contextMenuItemClick(item, evt) {
      if (!item.disabled) {
        item.handler.call(this.contextMenu.target, item);
      } else {
        evt.preventDefault();
        evt.stopPropagation();
      }
    },
    //处理设计时需要操作鼠标点下事件的组件
    mouseDownHanlder(evt) {
      try {
        if (evt.button != 0) {
          return;
        }
        let component = dsf.designer.getComponentByTarget(evt.target);
        if (!component) {
          return;
        }
        //设计器会向下级所有组件触发,可用于清理下级所有组件触发过mousedown事件留下的痕迹
        dsf.designer.broadcast(
          this,
          "clearMouseDownHandler",
          evt,
          null,
          component
        );
        this.mousedown.target = component;
        let options = component ? component.$options : {};
        if (options.design.mouseDownHandler) {
          options.design.mouseDownHandler.call(component, evt, this);
        }
      } catch (ex) {
        dsf.error(ex);
      } finally {
        $(document).one("mouseup", this.mouseUpHandler);
      }
    },
    //处理设计时需要操作鼠标点下事件的组件,组件必须触发过mousedown
    mouseMoveHanlder(evt) {
      try {
        if (this.mousedown.target) {
          let options = this.mousedown.target.$options || {};
          if (options.design.mouseMoveHandler) {
            options.design.mouseMoveHandler.call(
              this.mousedown.target,
              evt,
              this
            );
          }
        }
      } catch (ex) {
        dsf.error(ex);
      }
    },
    mouseUpHandler(evt) {
      try {
        if (this.mousedown.target) {
          let options = this.mousedown.target.$options || {};
          if (options.design.mouseUpHandler) {
            options.design.mouseUpHandler.call(
              this.mousedown.target,
              evt,
              this
            );
          }
        }
      } catch (ex) {
        dsf.error(ex);
      } finally {
        this.mousedown.target = null;
      }
    },
    /*--- 设计 事件 -----*/

    //生成二次开发js
    openExtJs() {
      dsf.layer.pc.openDialog({
        title: "自定义扩展",
