    },
    //加载布局
    async loadLayout(business, module, pname) {
      var _this = this;
      let b = business || this.business;
      let m = module || this.module;
      let pn = pname || this.pageName;
      let title = this.pageTitle;
      let ctrlType = this.mobile ? "dsf.mobile.page" : "dsf.page";
      let defaultPageAttrs = {
        caption: "page",
        name: pn,
        title: title,
        context: { B: b, M: m },
      };
      if (b && m && pn) {
        let layout = null,
          scss = null,
          designer = null;
        try {
          let res = await getLayout.call(this, b, m, pn, title, this.mobile, this.tpl);
          let dom = $("<div>" + res.data + "</div>");
          let layoutStr = dom.children("script[id='layout']").html() || "";
          let scssStr = dom.children("script[id='scss']").html() || "";
          let designerStr = dom.children("script[id='designer']").html() || "";
          let layoutFn = new Function(
            [
              "var layout=" + (layoutStr || "null") + ";",
              "return layout;",
            ].join("\n")
          );
          let scssFn = new Function(
            ["return " + JSON.stringify(scssStr.trim() || "")].join("\n")
          );
          let designerFn = new Function(
            ["return " + (designerStr || "{}").trim()].join("\n")
          );
          layout = dsf.fillComponentDefaultProps(layoutFn());
          scss = scssFn();
          designer = designerFn();
          if (!this.tpl) {
            let res1 = await getExtJs.call(this, b, m, pn, this.mobile, this.tpl);
            let code = res1.data;
            this.extJsCode = code;
          }
        } catch (err) {
          dsf.error(err);
        } finally {
          if (layout) {
            this.page = createComponent(ctrlType, layout, this.mobile);
            this.extScssCode = scss || "";
            //加载控件组
            let controlGroup =
              dsf.isEmptyObject(designer.controlGroup) || !designer.controlGroup
                ? this.material
                : designer.controlGroup;
            loadControlGrooup.call(this, controlGroup);
            //加载元数据组
            let metadataGroup = designer.metadataGroup || [];
            loadMetaDataGroup.call(this, metadataGroup);
            //加载业务组件组
            let businessGroup = designer.businessControlGroup || [];
            loadBusinessComponetGroup.call(this, businessGroup);
            this.fromTemplateId = designer.fromTemplateId || "";
            this.firstOpen = false;
            this.saved = true;
          } else {
            this.loadEmptyDesigner(ctrlType, defaultPageAttrs, this.material);
            this.fromTemplateId = "";
            this.firstOpen = true;
            this.saved = false;
            //强行弹出模板选择
            if (!this.tpl) {
              this.useTpl(false, false, false);
