async function getLayout(business, module, pname, title, isMobile, isTpl) {
  // console.log("dsfsfdsf")
  let b = business || "";
  let m = module || "";
  let pn = pname || "";
  let htmlPath = dsf.designer.getLayoutPath(b, m, pn, isMobile, isTpl);
  return this.$http.get(htmlPath, null, { responseType: "text" });
}

async function getExtJs(b, m, pn, isMobile, isTpl) {
  let extPath = dsf.designer.getExtPath(b, m, pn, isMobile, isTpl);
  return this.$http.get(extPath, null, { responseType: "text" });
}

//保存布局
function saveLayout() {
  let page = this.$refs.viewProxy;
  try {
    let md = dsf.designer.createViewMetaData(page);
    //检查元数据key是否是在不同源，将页面元数据key记录到页面中
    if (md.metadata && !dsf.isEmptyObject(md.metadata)) {
      let list = [],
        metadataSource = [];
      _.each(md.metadata, (m, i) => {
        let arr = i.split(".");
        let prefix = arr[0];
        let name = arr[1] ? arr[1] : "";
        if (m.ref.isFormControl && !m.ref.isOneToMany) {
          //子表在创建元数据时就强制同源，所以此处只验证主表是否同源
          if (m.level == 0) {
            let obj = {
              ctrl: m.ref,
              source: prefix,
            };
            dsf.array.ensure(metadataSource, prefix);
            list.push(obj);
          }
        }
      });
      //如果存在多张主表，则提示用户选择主表的元数据代码
      if (metadataSource.length > 1) {
        let _this = this;
        dsf.layer.openDialog({
          title: "元数据修正",
          content: "DesMetaDataSource",
          params: {
            source: list,
            dict: metadataSource,
          },
          btns: [
            {
              text: "确定",
              handler: (res) => {
                let list = res.yes();
                _.each(list, (el, i) => {
                  if (el.isChange) {
                    let ctrl = el.ctrl;
                    ctrl.metadata.isuser = true;
                    ctrl.metadata.id =
                      el.mdkey.replace("_", ".") + "." + ctrl.metadata.code;
                    ctrl.metadata.at = el.mdkey.replace("_", ".");
                  }
                });
                _this.saveLayout();
              },
            },
            { text: "取消" },
          ],
        });

        return;
      }
    }
    this.$nextTick(() => {
      postLayoutInfo.call(this, page, md);
    });
  } catch (ex) {
    let message = "出现了未知的错误";
    if (dsf.type(ex) == "string") {
      message = ex;
    }
    dsf.layer.pc.message(message, false);
  }
}

//保存模板
function saveLayoutToTemplate() {
  if (this.tpl == 1) {
    //递归清空掉所有无用的元数据
    let page = this.$refs.viewProxy;
    dsf.designer.recursionLayoutTree(page, null, null, (child) => {
      if (child.metadata && child.metadata.isFromMetaData) {
        child.metadata_fullcode = "";
      }
    });
    this.$nextTick(() => {
      postTemplateLayoutInfo.call(this, page);
    });
  }
}

function preView() {
  try {
    let page = this.$refs.viewProxy.$$getComponent();
    dsf.designer.createViewMetaData(page);
    this.$nextTick(() => {
      let str = dsf.designer.getLayoutInfo(page, true);
      str = dsf.designer.controlToJson(str);
      let x = "/";
      let layoutStr = `<script type="text/template" id="layout">\n${str}\n<${x}script>`;
      dsf.storage.session.set("layoutContent", layoutStr)
      // window.localStorage.setItem("layoutContent", layoutStr);
      window.open(
        "./designer.html#" + (this.mobile ? "/mobile" : "/pc") + "/preview"
      );
    });
  } catch (ex) {
    let message = "出现了未知的错误";
    if (dsf.type(ex) == "string") {
      message = ex;
    } else {
      dsf.error(ex);
    }
    dsf.layer.pc.message(message, false);
  }
}

function createComponent(name, attrs, isMobile) {
  let fn = dsf.designer.createComponent;
  if (isMobile) {
    fn = dsf.designer.createMobileComponent;
  }
  let _attrs = dsf.mix(attrs, { designProxy: true });
  return fn(name, _attrs);
}

//加载控件组
async function loadControlGrooup(params) {
  //去除重复
  try {
    params = _.uniqBy(params, (v) => v.id);
    let result = await requestControlGroupContent.call(this, params);
    _.each(result, (r) => {
      controlToUseMetaDataGroup.call(this, r);
    });

    // _.each(result, (r) => {
    //   var controlList = r.controlList;
    //   debugger;
    // })
  } catch (ex) {
    dsf.error(ex);
  }
}

//加载元数据组
async function loadMetaDataGroup(params) {
  try {
    //去除重复
    params = _.uniqBy(params, (v) => v.id);
    let result = await requestMetaDataGroupContent.call(this, params);
    _.each(result, (r) => {
      let metadataList = r.data;
      metadataList = _.filter(metadataList || [], (m) => {
        if (m.type_value == 3) {
          return _.map(params, "id").indexOf(m.ID) >= 0;
        } else {
          return _.map(params, "id").indexOf(m.dsfa_rm_id) >= 0;
        }
      });
      _.each(metadataList, (group) => {
        metadataToUseMetaDataGroup.call(this, group);
      });
    });
  } catch (ex) {
    dsf.error(ex);
  }
}

//加载业务组件租
async function loadBusinessComponetGroup(params) {
  try {
    //去除重复
    params = _.uniqBy(params, (v) => v.id);
    let result = [];
    if (params.length > 0) {
      result = await requestBusinessComponent.call(this, params);
    } else {
      result = [];
    }
    _.each(result.data, (r) => {
      businessToUseBusinessGroup.call(this, r);
    });
  } catch (ex) {
    dsf.error(ex);
  }
}

//获取控件组
function requestControlGroupContent(params) {
  let ids = _.map(params, (el) => {
    return el.id;
  });
  return this.$webAPI.getComponents(ids).then(({ data }) => {
    return data;
  });
}
//获取元数据组
function requestMetaDataGroupContent(params) {
  let httpList = [];
  for (let i = 0; i < params.length; i++) {
    if (params[i].type == "3") {
      httpList.push(
        this.$http.get("/rm/list/meta", { namespace: params[i].id })
      );
    } else if (params[i].type == "8") {
      httpList.push(this.$http.get("/dbsource/rmId", { rmId: params[i].id }));
    }
  }
  return this.$http.all(httpList);
}

//获取业务组件中的组件信息
function requestBusinessComponent(params) {
  let ids = _.map(params, (el) => {
    return el.id;
  });
  return this.$webAPI.getBusinessComponets(ids);
}

//将元数据添加到设计器的元数据组中
function metadataToUseMetaDataGroup(group) {
  let id = group.type_value == 8 ? group.dsfa_rm_id : group.ID;
  let result = {
    id: id,
    title: group.name,
    type: group.type_value,
    items: [],
  };
  _.each(group.metadata, (m) => {
    let item = {
      type: "metadata",
      name: m.name,
      data: m,
      group: group,
      id: m.id,
    };
    if (group.type_value == 8) {
      item.rmId = group.dsfa_rm_id;
    }
    result.items.push(item);
  });
  let index = _.findIndex(this.useMetaGroup.groups, (g) => {
    return g.id == result.id;
  });
  if (index < 0) {
    this.useMetaGroup.groups.push(result);
  } else {
    this.useMetaGroup.groups[index].items = result.items;
  }
  this.$nextTick(() => {
    //附加拖动
    this.initToolDrag();
  });
}

//将业务组件添加到设计器可选业务组件组中
function businessToUseBusinessGroup(group) {
  let result = {
    id: group.rmId,
    title: group.name,
    type: businessCode,
    items: [],
  };
  _.each(group.bcList, (m) => {
    let item = {
      type: "business",
      name: m.name,
      data: m,
      group: group,
      id: m.id,
    };
    result.items.push(item);
  });
  let index = _.findIndex(this.useBusinessControlGroup.groups, (g) => {
    return g.id == result.id;
  });
  if (index < 0) {
    this.useBusinessControlGroup.groups.push(result);
  } else {
    this.useBusinessControlGroup.groups[index].items = result.items;
  }
  this.$nextTick(() => {
    //附加拖动
    this.initToolDrag();
  });
}

function controlToUseMetaDataGroup(group) {
  let result = {
    id: group.rmId,
    title: group.name,
    type: controlCode,
    items: [],
  };
  _.each(group.controlList, (m) => {
    let item = {
      type: "control",
      name: m.name,
      data: m,
      group: group,
      id: m.id,
    };
    result.items.push(item);
  });
  let index = _.findIndex(this.useControlsGroup.groups, (g) => {
    return g.id == result.id;
  });
  if (index < 0) {
    this.useControlsGroup.groups.push(result);
  } else {
    this.useControlsGroup.groups[index].items = result.items;
  }
  this.$nextTick(() => {
    //附加拖动
    this.initToolDrag();
  });
}

//保存布局时生成简易的元数据信息
function setMetaDataDict(metadastas) {
  let dict = {};
  if (!metadastas) {
    return dict;
  }
  for (let key in metadastas) {
    let arr = key.split(".");
    let prefix = arr[0];
    let name = arr[1] ? arr[1] : "";
    if (metadastas[key].level == 1 && !name) {
      dict[key] = {
        type: "subtable",
        fieldsLength: 0,
        valueAttributes: [],
        level: 1,
      };
    } else {
      dict[key] = {
        type: "field",
        level: 0,
        fieldsLength:
          metadastas[key].valueAttributes &&
          metadastas[key].valueAttributes.length,
        valueAttributes:
          metadastas[key].valueAttributes &&
          _.map(metadastas[key].valueAttributes, "code"),
      };
      if (dict[prefix]) {
        dict[key].level = 1;
        dict[key].inSubTable = prefix;
      }
    }
  }
  return dict;
}

//提交布局信息
function postLayoutInfo(page, md) {
  //创建页面使用的元数据字典
  let mdDict = null;
  if (md) {
    let dict = setMetaDataDict.call(this, md.metadata);
    if (!dsf.isEmptyObject(dict)) {
      mdDict = dict;
    }
  }

  this.loading = true;
  let layout = dsf.designer.getLayoutInfo(page);
  layout = dsf.designer.controlToJson(layout);
  let css = "";
  let scss = this.extScssCode || "";
  let ns = (this.page.context.B || "") + "." + (this.page.context.M || "");
  let moduleName = ns + "." + (this.page.name || "");
  let designer = {
    metadataGroup: {},
    controlGroup: {},
    businessControlGroup: {},
    fromTemplateId: this.fromTemplateId,
  };
  designer["metadataGroup"] = _.map(this.useMetaGroup.groups, (m) => {
    return { name: m.title || m.name, id: m.id, type: m.type };
  });
  designer["businessControlGroup"] = _.map(
    this.useBusinessControlGroup.groups,
    (b) => {
      return { name: b.title, id: b.id, type: b.type };
    }
  );
  designer["controlGroup"] = _.map(this.useControlsGroup.groups, (b) => {
    return { name: b.title, id: b.id, type: b.type };
  });
  if (md && (page.pageType == "form" || page.pageType == "question" || page.pageType == "exam")) {
    let metadataGroup = [];
    _.each(md.metadata, (m, key) => {
      let arr = key.split(".");
      let metaDataKey = arr[0];
      let mID = metaDataKey.replace(/_/g, ".");
      let isExist = _.find(designer["metadataGroup"], (mg) => {
        return mg.id == mID;
      });
      if (!isExist) {
        designer["metadataGroup"].push({
          name: mID + "元数据",
          id: mID,
          type: 3,
        });
      }
    });
  }
  // let ns = this.page.context.B + "." + this.page.context.M + "." + this.page.name;
  dsf.designer.compileCss(this.extScssCode, moduleName)
    .then((cssStr) => {
      this.extCssCode = cssStr;
      css = this.extCssCode;
    })
    .then(() => {
      let extJs = this.extJsCode;
      extJs = dsf.beautify.js(extJs, {
        indent_size: "2",
        indent_char: " ",
        //保持数组压缩
        keep_array_indentation: false,
        //链式方法换行
        break_chained_methods: true,
        //换行长度
        wrap_line_length: 0,
        space_after_anon_function: true,
      });
      let x = "/";
      let post = {
        isMobile: this.mobile,
        md: JSON.stringify(md, (k, v) => (k == "ref" ? undefined : v)),
        layout: [
          `<script type="text/template" id="layout">\n${layout}\n<${x}script>`,
          `<script type="text/template" id="css">\n${css}\n<${x}script>`,
          `<script type="text/template" id="scss">\n${scss}\n<${x}script>`,
          `<script type="text/template" id="designer">\n${JSON.stringify(
            designer
          )}\n<${x}script>`,
          `<script type="text/template" id="metadatedict">\n${mdDict ? JSON.stringify(mdDict) : ""
          }\n<${x}script>`,
        ].join("\n"),
        extJsCode: extJs,
        evJsCode: "",
        templateid: this.fromTemplateId,
        pageName: this.page.name,
        namespace: ns,
        pageTitle: this.page.title,
      };

      this.$webAPI
        .pageBuild(post)
        .done((result) => {
          if (result.state == "20000") {
            dsf.layer.message("保存成功", true);
            this.saved = true;
          } else {
            dsf.layer.message(result.message, false);
          }
        })
        .error((error) => {
          dsf.layer.message("保存失败", false);
        })
        .always(() => {
          loadMetaDataGroup.call(this, designer["metadataGroup"]);
          this.loading = false;
        });
    })
    .catch((errorMsg) => {
      this.loading = false;
      dsf.layer.pc.message(errorMsg, false);
    });
}
//提交模板布局信息
function postTemplateLayoutInfo(page) {
  this.loading = true;
  let layout = dsf.designer.getLayoutInfo(page);
  layout = dsf.designer.controlToJson(layout);
  let css = "";
  let scss = this.extScssCode || "";
  let ns = (this.page.context.B || "") + "." + (this.page.context.M || "");
  let moduleName = ns + "." + (this.page.name || "");
  let designer = {
    metadataGroup: {},
    controlGroup: {},
    businessControlGroup: {},
  };
  designer["metadataGroup"] = _.map(this.useMetaGroup.groups, (m) => {
    return { name: m.name, id: m.id, type: m.type };
  });
  designer["businessControlGroup"] = _.map(
    this.useBusinessControlGroup.groups,
    (b) => {
      return { name: b.title, id: b.id, type: b.type };
    }
  );
  designer["controlGroup"] = _.map(this.useControlsGroup.groups, (b) => {
    return { name: b.title, id: b.id, type: b.type };
  });
  let x = "/";
  let post = {
    isMobile: this.mobile,
    layout: [
      `<script type="text/template" id="layout">\n${layout}\n<${x}script>`,
      `<script type="text/template" id="css"><${x}script>`,
      `<script type="text/template" id="scss">\n${scss}\n<${x}script>`,
      `<script type="text/template" id="designer">\n${JSON.stringify(
        designer
      )}\n<${x}script>`,
      `<script type="text/template" id="metadatedict"><${x}script>`,
      // `<script type="text/template" id="buttonsRules">${JSON.stringify(this.buttons)}<${x}script>`,
    ].join("\n"),
    desButtonRules: JSON.stringify(this.buttons),
    pageName: this.page.name,
    namespace: ns,
    // pageTitle: this.page.title,
  };

  this.$webAPI
    .pageSaveTemplate(post)
    .done((result) => {
      result.state == 20000
        ? dsf.layer.message("保存成功", true)
        : dsf.layer.message(result.message, false);
    })
    .error((error) => {
      dsf.layer.message("保存失败", false);
    })
    .always(() => {
      // loadMetaDataGroup.call(this, designer['metadataGroup']);
      // dsf.layer.pc.closeLoading(dialog);
      this.loading = false;
    });
}

function hasRole(roleName) {
  let role = this.currentUserRoles[roleName];
  if (role) {
    return true;
  }
  return false;
}
